import zhuanxian from '../../../api/zhuanxian/zhuanxian'
import initdata from '../../../api/initdata'

const state = {
  lineStartCity: [],
  lineEndCity: [],
  budgetMoney: '',
  coupon: [],
  findOrder: null,
  AllOrder: {
    state: true,
    total: true,
    val: []
  },
  payInfo: null,
  queryReview: null
}

const mutations = {
  ZxLineStartCity (state, data) {
    state.lineStartCity = data
  },
  ZxLineEndCity (state, data) {
    let arry = []
    if (!data) {
      state.lineEndCity = arry
      return
    }
    data.forEach((data) => {
      if (arry.length === 0) {
        arry.push(data)
      } else {
        arry.forEach((opt) => {
          if (data.city !== opt.city) {
            arry.push(data)
          }
        })
      }
    })
    state.lineEndCity = arry
  },
  ZxBudgetMoney (state, data) {
    state.budgetMoney = data
  },
  ZxCoupon (state, data) {
    state.coupon = data
  },
  ZxFindOrder (state, data) {
    state.findOrder = data
  },
  ZxPayInfo (state, data) {
    state.payInfo = data
  },
  ZxQueryAllOrder (state, obj) {
    let type = obj.type
    let data = obj.data
    if (data) {
      state.AllOrder.total = data.total
      state.AllOrder.state = false
      if (type === 'upData') {
        state.AllOrder.val = []
      }
      for (let i = 0; i < data.rows.length; i++) {
        state.AllOrder.val.push(data.rows[i])
      }
    }
  },
  ZxQueryReview (state, data) {
    if (data && data && data.content !== '') {
      let tabs = data.content.substring(0, data.content.lastIndexOf('&*&'))
      tabs = tabs !== '' ? tabs.split('&|&') : []
      state.queryReview = {
        reviewContent: data.content.substring(data.content.lastIndexOf('&*&')).replace('&*&', ''),
        tabs: tabs.length > 0 ? tabs : null,
        score: data.score
      }
    }
  }
}

const getters = {
  ZxLineStartCity: state => {
    return state.lineStartCity
  },
  ZxLineEndCity: state => {
    return state.lineEndCity
  },
  ZxBudgetMoney: state => {
    return state.budgetMoney
  },
  ZxCoupon: state => {
    return state.coupon
  },
  ZxFindOrder: state => {
    return state.findOrder
  },
  ZxPayInfo: state => {
    return state.payInfo
  },
  ZxQueryAllOrder: state => {
    return state.AllOrder
  },
  ZxQueryReview: state => {
    return state.queryReview
  }
}

const actions = {
  ZxLineStartCity ({commit}, obj) {
    zhuanxian.lineStartCity(obj, (data) => {
      if (data && obj && obj.fn) {
        obj.fn(data.data)
      }
      commit('ZxLineStartCity', data.data)
    })
  },
  ZxLineEndCity ({commit}, obj) {
    zhuanxian.lineEndCity(obj, (data) => {
      if (data && obj && obj.fn) {
        obj.fn(data.data)
      }
      commit('ZxLineEndCity', data.data)
    })
  },
  ZxBudgetMoney ({commit}, obj) {
    zhuanxian.budgetMoney(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZxBudgetMoney', data.data)
    })
  },
  ZxCoupon ({commit}, obj) {
    zhuanxian.coupon(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZxCoupon', data)
    })
  },
  ZxMakeAnAppointment ({commit}, obj) {
    zhuanxian.makeAnAppointment(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZxFindOrder ({commit}, obj) {
    zhuanxian.findOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZxFindOrder', data.data)
    })
  },
  ZxPayInfo ({commit}, obj) {
    zhuanxian.payInfo(obj, (data) => {
      commit('ZxPayInfo', data.data)
    })
  },
  ZxToPay ({commit}, obj) {
    zhuanxian.toPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZxReview ({commit}, obj) {
    zhuanxian.review(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZxCancelOrder ({commit}, obj) {
    zhuanxian.cancelOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZxQueryAllOrder ({commit}, obj) {
    initdata.allOrder(obj, (data) => {
      if (data.code === 0 && obj.fn) {
        obj.fn(true)
      }
      commit('ZxQueryAllOrder', {data: data.data, type: obj.type})
    })
  },
  ZxQueryReview ({commit}, obj) {
    zhuanxian.queryReview(obj, (data) => {
      commit('ZxQueryReview', data.data)
    })
  },
  ZxWXPay ({commit}, obj) {
    zhuanxian.weixinPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



