/**支付费用**/
<template>
  <div class="Settlement">
    <div class="Settlementcont">
      <div class="feiyong">
        <div class="title">
          <h2>支付费用</h2>
          <div class="line"></div>
        </div>
        <div class="content">
        <span class="sum">
          <span class="daxie">{{v5FindZcOrderById&&v5FindZcOrderById.real_pay ? (v5FindZcOrderById.real_pay).toFixed(1):0}}</span>
          <!--<span class="xiaoye">.{{ZcPayInfo ? ZcPayInfo.payMoney.toFixed(1).split(',')[1] : '0'}}</span>-->
          <span class="danwei">元</span>
        </span>
        </div>
      </div>
      <div class="detailed">
        <span @click="detailsBoxFn">费用详情<i class="iconfont  icon-iconfontyiwen"></i></span>
      </div>
      <div class="payType">
        <div class="title">
          <h2>支付方式</h2>
          <div class="line"></div>
        </div>
        <div class="payTypeContent">
          <ul>
            <li
              v-if="true"
              @click="payTypeFn(1)">
              <span>
                <i class="iconfont icon-yue Icon"></i>
                余额支付
                <span class="select " :class="data.payType==1?'selectAction':''">
                  <i class="iconfont icon-chenggong "></i>
                </span>
              </span>
            </li>
            <li v-if="wxState" @click="payTypeFn(2)">
            <span>
            <i class="iconfont icon-weixin Icon"></i>
            微信支付
            <span class="select" :class="data.payType==2?'selectAction':''">
            <i class="iconfont icon-chenggong "></i>
            </span>
            </span>
            </li>
            <!--<li v-if="loginData && loginData.passenger.allowOverdraw" @click="payTypeFn(3)">-->
            <!--<span>-->
            <!--<i class="iconfont icon-qiandan-copy Icon"></i>-->
            <!--签单支付-->
            <!--<span class="select" :class="data.payType==3?'selectAction':''">-->
            <!--<i class="iconfont icon-chenggong "></i>-->
            <!--</span>-->
            <!--</span>-->
            <!--</li>-->
          </ul>
        </div>
      </div>
      <div class="submitBut" @click="payFn">
        <span>立即支付</span>
      </div>
    </div>
    <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <ul>
          <li v-for="itme in payDataFn" v-if="itme.val !== 0" class="clear">
            <span class="name">{{itme.name}}</span>
            <span class="val"><span class="sum">{{itme.val}}</span><span class="unit">元</span></span>
            <span class="time" v-if="itme.time">{{itme.time}}<span class="unit">分钟</span></span>
          </li>
        </ul>
      </div>
    </Xk-detailsBox>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkdetailsBox from '../../module/detailsBox/src/detailsBox.vue'
  import {mapGetters} from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'

  export default {
    components: {
      XkUploadMod,
      XkHintBox
    },
    component: {
      XkdetailsBox
    },
    name: 'Settlement',
    computed: {
      ...mapGetters({
        ZcPayInfo: 'ZcPayInfo',
        loginData: 'loginData',
        ZcQueryOrderInfo: 'ZcQueryOrderInfo',
        wxState: 'wxState',
        infoData: 'infoData',
        v5FindZcOrderById: 'v5FindZcOrderById'
      }),
      payDataFn: function () {
        let obj = []
        let order = this.orderFee
        if (order) {
          if (order.pre_pay) {
            obj.push({
              name: '预付金额',
              val: order.pre_pay
            })
          }
          if (order.start_price) {
            obj.push({
              name: '起步价',
              val: order.start_price
            })
          }
          if (order.distance_fee) {
            obj.push({
              name: '里程费',
              val: order.distance_fee
            })
          }
          if (order.cross_fee) {
            obj.push({
              name: '垫付费用',
              val: order.cross_fee
            })
          }
          if (order.time_fee) {
            obj.push({
              name: '时间费',
              val: order.time_fee
            })
          }
          if (order.wait_fee) {
            obj.push({
              name: '等待费',
              val: order.wait_fee
            })
          }
          if (order.other_fee) {
            obj.push({
              name: '附加服务费',
              val: order.other_fee
            })
          }
          if (order.peak_cost) {
            obj.push({
              name: '运营高峰费',
              val: order.peak_cost
            })
          }
          if (order.night_price) {
            obj.push({
              name: '夜间里程费',
              val: order.night_price
            })
          }
          if (order.night_time_price) {
            obj.push({
              name: '夜间时间费',
              val: order.night_time_price
            })
          }
          if (order.low_speed_cost) {
            obj.push({
              name: '低速费',
              val: order.low_speed_cost
            })
          }
          if (order.add_fee) {
            obj.push({
              name: '调价费用',
              val: order.add_fee
            })
          }
          if (order.coupon_fee) {
            obj.push({
              name: '优惠券抵扣',
              val: order.coupon_fee
            })
          }
          if (order.total_fee) {
            obj.push({
              name: '订单总金额',
              val: order.total_fee
            })
          }
        }
        return obj
      }
    },
    data () {
      return {
        data: {
          costDetail: [], // 费用详情
          payType: 1 // 支付方式
        },
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        loadState: false,
        orderFee: ''
      }
    },
    watch: {
      ZcPayInfo (val) {
        if (val) {
          this.data.costDetail = [
            {
              name: '服务费',
              val: this.$store.state.zhuanche.payInfo.payMoney
            },
            {
              name: '过路费',
              val: this.$store.state.zhuanche.payInfo.guoluMoney
            },
            {
              name: '远程费',
              val: this.$store.state.zhuanche.payInfo.yuanchengMoney
            },
            {
              name: '行驶时间费',
              val: this.$store.state.zhuanche.payInfo.runTimePrice,
              time: this.$store.state.zhuanche.payInfo.runTime
            },
            {
              name: '等待时长费',
              val: this.$store.state.zhuanche.payInfo.waitPrice,
              time: this.$store.state.zhuanche.payInfo.midwayWait
            },
            {
              name: '其他（商品费，感谢费）',
              val: this.$store.state.zhuanche.payInfo.otherMoney
            },
            {
              name: '优惠券',
              val: this.$store.state.zhuanche.payInfo.couponMoney
            }
          ]
        }
      },
      ZcQueryOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/zhuanche')
          return
        }
        if (val.status === 5) {
          if (that.$route.fullPath.split('?')[0] === '/zhuanche/settlement') {
            this.$router.push({path: '/zhuanche/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
          }
        } else {
          if (that.$route.fullPath.split('?')[0] === '/zhuanche/settlement') {
            setTimeout(function () {
              that.$store.dispatch('ZcQueryOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
            }, 3000)
          }
        }
      }
    },
    methods: {
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      },
      payTypeFn: function (val) { // 支付方式事件
        let that = this
        switch (val) {
          case 1:
            that.data.payType = 1
            break
          case 2:
            that.data.payType = 2
            break
          case 3:
            that.data.payType = 3
            break
        }
      },
      payFn: function () {
        let that = this
        this.loadState = true
        if (this.data.payType === 1 || this.data.payType === 3) {
          this.$store.dispatch('v5ZcBalancePay', {
            id: Toucth.AcquireUrlParameter().orderId,
            pay_type: this.data.payType === 1 ? 'balance' : 'sign', // balance 余额 sign 签单
            fn: (data) => {
              this.loadState = false
              switch (data.code) {
                case 1:
                  this.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '支付成功！'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                  }, 1500)
                  this.$router.push({path: '/zhuanche/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
                  break
                case 20088:
                  this.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '您的余额不足'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                  }, 1500)
                  break
                case 20000:
                  this.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '请求参数无效'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche'})
                  }, 1500)
                  break
                case 20032:
                  this.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '订单状态错误'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche'})
                  }, 1500)
                  break
                case 30044:
                  this.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '该订单不存在'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche'})
                  }, 1500)
                  break
                default:
                  this.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '支付失败'
                  }
                  setTimeout(() => {
                    this.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche'})
                  }, 1500)
                  break
              }
              // if (data.code === -47) {
              //   this.hintBox = {
              //     stateShow: true,
              //     state: 'no',
              //     content: '余额不足！'
              //   }
              //   setTimeout(() => {
              //     this.hintBox.stateShow = false
              //   }, 1500)
              // }
            }
          })
        }
        if (this.data.payType === 2) {
          this.$store.dispatch('ZcWXPay', {
            'orderId': Toucth.AcquireUrlParameter().orderId,
            'openid': that.loginData.openId,
            'WXappId': window.configure.WXappId,
            fn: function (data) {
              that.loadState = false
              if (data === '支付成功') {
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '支付成功！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                  that.$store.dispatch('unfilledOrderState', {type: 'zhuanche', state: false})
                  that.$router.push({path: '/zhuanche/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
                }, 1500)
              } else {
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '支付失败！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
              }
            }
          })
        }
      },
      getOrderInforByid () { // v5获取单个订单信息
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('findZcOrderById', {
            order_id: that.$route.query.orderId,
            fn (data) {
              if (data.code === 1) {
                resolve(data)
              } else {
                reject(data)
              }
            }
          })
        })
      }
    },
    mounted: function () {
      let that = this
      this.$store.dispatch('checkLogin', (data) => {
        if (data) {
          let zcOrder = that.getOrderInforByid()
          zcOrder.then(function (res) {
            console.log('订单详情', res)
            that.orderFee = res.orderFee
          })
          // that.$store.dispatch('ZcQueryOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
          // that.$store.dispatch('ZcPayInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
          // that.$store.dispatch('updateLoginData', {passengerId: that.$store.state.mc.loginData.passenger.id})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Settlement {
    .Settlementcont {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      padding: 1rem;
    }
    .title {
      height: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      position: relative;
      h2 {
        display: inline-block;
        color: #333;
        font-size: 1.6rem;
        padding: 0 0.8rem;
        background-color: #fff;
        z-index: 10;
        position: relative;
      }
      .line {
        width: 100%;
        height: 1px;
        background-color: #eee;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .feiyong {
      background-color: #fff;
      padding: 1rem;
      padding-top: 1.5rem;
      padding-bottom: 1rem;
      .content {
        text-align: center;
        padding-top: 1.8rem;
        .sum {
          color: #f06553;
          .xiaoye, .danwei {
            font-size: 1.5rem;
          }
          .daxie {
            font-size: 3rem;
          }
        }
      }
    }
    .detailed {
      background-color: #fff;
      text-align: center;
      line-height: 1.5rem;
      height: 1.5rem;
      color: #999999;
      padding: 0.5rem 0 1.8rem 0;
      font-size: 1rem;
      span {
        display: inline-block;
        i {
          font-size: 1.3rem;
          padding-left: 0.3rem;
          color: #999999;
        }
      }

    }
    .payType {
      background-color: #fff;
      margin-top: 1rem;
      padding: 1rem;
      padding-top: 1.5rem;
      .payTypeContent {
        padding-top: 1rem;
        ul {
          width: 100%;
          li {
            box-sizing: border-box;
            line-height: 3.6666rem;
            height: 3.6666rem;
            font-size: 1.3rem;
            color: #434343;
            width: 100%;
            position: relative;
            padding: 0 1rem;
            > span {
              width: 100%;
            }
            i {
              font-size: 1.5rem;
            }
            .Icon {
              margin-right: 0.5rem;
            }
            .select {
              display: inline-block;
              width: 1.8rem;
              height: 1.8rem;
              box-sizing: border-box;
              overflow: hidden;
              border-radius: 50%;
              border: 1px solid #cccccc;
              position: absolute;
              right: 1.1666rem;
              top: 50%;
              transform: translate(0, -50%);
              transition: 0.3s all;
              i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                font-size: 0.3rem;
                opacity: 0;
                color: #fff;
                transition: 0.3s all;
              }
            }
            .selectAction {
              background-color: #f06553;
              border-color: #f06553;
              i {
                opacity: 1;
              }
            }
          }
          .icon-yue {
            color: #fe5459;
          }
          .icon-weixin {
            color: #00c801;
          }
          .icon-qiandan-copy {
            color: #ffb400;
          }
        }
      }
    }
    .submitBut {
      box-sizing: border-box;
      width: 100%;
      margin-top: 1rem;
      margin-bottom: 1rem;
      span {
        width: 100%;
        line-height: 3.666rem;
        height: 3.666rem;
        border-radius: 0.333rem;
        background-color: #f06553;
        text-align: center;
        color: #fff;
        font-size: 1.4rem;
      }
    }
    .costBox {
      ul {
        li {
          height: 2rem;
          line-height: 2rem;
          color: #999999;
          .name {
            float: left;
          }
          .time, .val {
            float: right;
          }
          .time {
            margin-right: 0.8rem;
            color: #333333;
          }
          .sum {
            color: #f06553;
          }
          .unit {
            margin-left: 0.3rem;
          }
        }
      }
    }
  }
</style>



