import zhuanche from '../../../api/zhuanche/zhuanche'
import initdata from '../../../api/initdata'

const state = {
  orderNum: null,
  waiterNum: 0,
  budgetCost: {
    countMoney: 0,
    countMileage: 0,
    carTypes: {}
  },
  carInfo: {
    allNum: 1,
    maxNum: 10,
    data: []
  },
  couponList: [],
  orderInfo: null,
  payInfo: null,
  AllOrder: {
    state: true,
    total: true,
    val: []
  },
  v5ZcBusTypeAndCarType: [],
  queryReview: null,
  v5FindZcOrderById: '' // v5专车单个订单信息
}

const mutations = {
  ZcBudgetCost (state, data) {
    if (data) {
      state.budgetCost.countMoney = data.countMoney
      state.budgetCost.countMileage = data.countMileage
      state.budgetCost.carTypes = {}
      for (let key in data) {
        if (key !== 'countMileage') {
          if (key !== 'countMoney') {
            state.budgetCost.carTypes[key] = data[key]
          }
        }
      }
    }
  },
  // V5ZcBudgetCost (state, data) {
  //   if (data) {
  //     state.budgetCost.countMoney = data.countMoney
  //     state.budgetCost.countMileage = data.countMileage
  //     state.budgetCost.carTypes = {}
  //     for (let key in data) {
  //       if (key !== 'countMileage') {
  //         if (key !== 'countMoney') {
  //           state.budgetCost.carTypes[key] = data[key]
  //         }
  //       }
  //     }
  //   }
  // },
  v5FindZcOrderById (state, data) {
    if (data.code === 1) {
      state.v5FindZcOrderById = data.order
    }
  },
  ZcQueryCar (state, data) {
    state.carInfo = data
  },
  ZcCarTypeSelect (state, data) {
    switch (data.type) {
      case 'plus':
        for (let i = 0; i < state.carInfo.data.length; i++) {
          if (state.carInfo.data[i].id === data.id && state.carInfo.data[i].name === data.name) {
            state.carInfo.data[i].num++
            state.carInfo.allNum++
          }
        }
        break
      case 'radio':
        for (let i = 0; i < state.carInfo.data.length; i++) {
          state.carInfo.data[i].num = 0
        }
        for (let i = 0; i < state.carInfo.data.length; i++) {
          if (state.carInfo.data[i].id === data.id && state.carInfo.data[i].name === data.name) {
            state.carInfo.data[i].num++
          }
        }
        break
      case 'minus':
        for (let i = 0; i < state.carInfo.data.length; i++) {
          if (state.carInfo.data[i].id === data.id && state.carInfo.data[i].name === data.name) {
            state.carInfo.data[i].num--
            state.carInfo.allNum--
          }
        }
        break
      case 'reset':
        for (let i = 0; i < state.carInfo.data.length; i++) {
          if (i === 0) {
            state.carInfo.data[i].num = 1
          } else {
            state.carInfo.data[i].num = 0
          }
        }
        state.carInfo.allNum = 1
        break
    }
  },
  ZcBussinessType (state, data) {
    state.v5ZcBusTypeAndCarType = data
  },
  ZcQueryWaiter (state, data) {
    state.waiterNum = data
  },
  ZcCoupon (state, data) {
    state.couponList = data
  },
  ZcMakeAnAppointment (state, data) {
    state.orderInfo = data.data
  },
  ZcAwaitWaiter (state, data) {
    // console.log(data)
  },
  ZcQueryOrderInfo (state, data) { // 查询订单
    state.orderInfo = data.data
  },
  ZcPayInfo (state, data) { // 获取支付信息
    state.payInfo = data.data
  },
  ZcBalancePay (state, data) {
  },
  ZcQueryAllOrder (state, obj) {
    let type = obj.type
    let data = obj.data
    if (data) {
      state.AllOrder.total = data.total
      state.AllOrder.state = false
      if (type === 'upData') {
        state.AllOrder.val = []
      }
      for (let i = 0; i < data.rows.length; i++) {
        state.AllOrder.val.push(data.rows[i])
      }
    }
  },
  ZcQueryReview (state, data) {
    if (data && data.data && data.data.content !== '') {
      let tabs = data.data.content.substring(0, data.data.content.lastIndexOf('&*&'))
      tabs = tabs !== '' ? tabs.split('&|&') : []
      state.queryReview = {
        reviewContent: data.data.content.substring(data.data.content.lastIndexOf('&*&')).replace('&*&', ''),
        tabs: tabs.length > 0 ? tabs : null,
        score: data.data.score
      }
    }
  }
}

const getters = {
  v5FindZcOrderById: state => {
    return state.v5FindZcOrderById
  },
  v5ZcBusTypeAndCarType: state => { // v5专车业务类型和车型获取
    return state.v5ZcBusTypeAndCarType
  },
  ZcQueryCar: state => { // 查询车辆
    return state.carInfo
  },
  ZcQueryWaiter: state => { // 查询服务人员数量(专车)
    return state.waiterNum
  },
  ZcBudgetCost: state => { // 获取 预算金额
    return state.budgetCost
  },
  ZcCoupon: state => { // 返回 优惠券
    return state.couponList
  },
  ZcAwaitWaiter: state => { // 等待服务员
  },
  ZcOrderId: state => { // 获取orderID
    if (state.orderInfo) {
      return state.orderInfo.orderIds
    }
  },
  ZcQueryOrderInfo: state => {
    if (state.orderInfo) {
      return state.orderInfo
    }
  },
  ZcPayInfo: state => {
    if (state.payInfo) {
      return state.payInfo
    }
  },
  ZcQueryAllOrder: state => {
    return state.AllOrder
  },
  ZcQueryReview: state => {
    return state.queryReview
  }
}

const actions = {
  ZcQueryCar ({commit}, obj) { // 查询车辆车型
    zhuanche.queryCar(obj, (data) => {
      commit('ZcQueryCar', data)
    })
  },
  createZcOrder ({commit}, obj) { // v5专车下单
    zhuanche.createZcOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZcQueryCar', data)
    })
  },
  findZcOrderById ({commit}, obj) {  // 查询v5专车单个司机信息
    zhuanche.findZcOrderById(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('v5FindZcOrderById', data)
    })
  },
  v5ZcBalancePay ({commit}, obj) {  // v5专车余额支付
    zhuanche.v5ZcBalancePay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  v5ZcEvaluateOrder ({commit}, obj) {
    zhuanche.v5ZcEvaluateOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  v5ZcCancelOrder ({commit}, obj) { // v5专车取消订单
    zhuanche.v5ZcCancelOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZcCarTypeSelect ({commit}, obj) { // 选择车辆
    commit('ZcCarTypeSelect', obj)
  },
  ZcQueryWaiter ({commit}, obj) { // 查询服务人员数量
    zhuanche.queryWaiter(obj, (data) => {
      commit('ZcQueryWaiter', data)
    })
  },
  ZcBudgetCost ({commit}, obj) { // 预算金额
    zhuanche.budgetCost(obj, (data) => {
      commit('ZcBudgetCost', data)
    })
  },
  ZcBussinessType ({commit}, obj) {
    zhuanche.ZcBussinessType(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZcBussinessType', data)
    })
  },
  getV5ZcBudgetCost ({commit}, obj) {
    zhuanche.getV5ZcBudgetCost(obj, (data) => { // 专车预算费用
      if (data && obj.fn) {
        obj.fn(data)
        // commit('V5ZcBudgetCost', data)
      }
    })
  },
  v5GetZcOrderRecord ({commit}, obj) { // v5专车获取订单记录
    zhuanche.v5GetZcOrderRecord(obj, (data) => { // 专车预算费用
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZcCoupon ({commit}, obj) { // 查询优惠券
    zhuanche.coupon(obj, (data) => {
      commit('ZcCoupon', data)
    })
  },
  ZcMakeAnAppointment ({commit}, obj) { // 下单
    zhuanche.makeAnAppointment(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZcMakeAnAppointment', data)
    })
  },
  ZcAwaitWaiter ({commit}, obj) { // 查询是否有服务员  接单
    zhuanche.awaitWaiter(obj, (data) => {
      commit('ZcAwaitWaiter', data)
    })
  },
  ZcQueryOrderInfo ({commit}, obj) { // 查询订单
    zhuanche.awaitWaiter(obj, (data) => {
      commit('ZcQueryOrderInfo', data)
    })
  },
  ZcCancelOrder ({commit}, obj) { // 取消 订单
    zhuanche.cancelOrder(obj, (data) => {
      // if (data.code === 0 && data.message === '销单成功' && obj.fn) {
      //   obj.fn(true)
      // } else {
      //   obj.fn(false)
      // }
    })
  },
  ZcPayInfo ({commit}, obj) { // 查询支付信息
    zhuanche.payInfo(obj, (data) => {
      commit('ZcPayInfo', data)
    })
  },
  ZcBalancePay ({commit}, obj) { // 余额支付
    zhuanche.pay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('ZcBalancePay', data)
    })
  },
  ZcEvaluateOrder ({commit}, obj) { // 评价
    zhuanche.evaluateOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  ZcQueryAllOrder ({commit}, obj) {
    initdata.allOrder(obj, (data) => {
      if (data.code === 0 && obj.fn) {
        obj.fn(true)
      }
      commit('ZcQueryAllOrder', {data: data.data, type: obj.type})
    })
  },
  ZcQueryReview ({commit}, obj) {
    zhuanche.queryReview(obj, (data) => {
      commit('ZcQueryReview', data)
    })
  },
  ZcWXPay ({commit}, obj) {
    zhuanche.weixinPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



