/** 用车**/
<template>
  <div class="recordYongChe">
    <div class="yongcheContent">
      <ul class="contentList" v-if="HyQueryAllOrder.val.length>0">
        <xk-record-order-mod
          v-for="(time,index) in allOrderFn"
          :startAddress="time.startAddress"
          :endAddress="time.endAddress"
          :serverTime="time.serverTime"
          :status="time.status"
          :orderId="time.orderId"
          :key="index"
          @click="recordOrderAcFn"
        >
        </xk-record-order-mod>
      </ul>
      <div class="NullOrder" v-else="">
        <div class="img">
          <img src="../../assets/img/yonghu/orderImg.png"/>
          <span class="imgBut"><router-link to="/huoyun">去下单</router-link></span>
        </div>
      </div>
      <div class="loadDataBut" v-if="HyQueryAllOrder&&HyQueryAllOrder.val.length<(HyQueryAllOrder.total-1)">
        <span @click="loadDataButFn">点击加载更多</span>
      </div>
    </div>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
  </div>
</template>

<script>
  import XkRecordOrderMod from '../module/recordOrderMod/src/recordOrderMod'
  import { mapGetters } from 'vuex'
  import Toucth from '../../assets/js/method'
  import XkUploadMod from '../module/uploadMod/src/uploadMod'

  export default {
    components: {
      XkUploadMod,
      XkRecordOrderMod
    },
    name: 'recordDaiJia',
    computed: {
      ...mapGetters({
        HyQueryAllOrder: 'HyQueryAllOrder',
        orderNav: 'orderNav'
      }),
      allOrderFn () {
        let orderData = this.HyQueryAllOrder.val
        let orderList = []
        for (let i = 0; i < orderData.length; i++) {
          let obj = {
            startAddress: orderData[i].startAddress,
            endAddress: orderData[i].endAddress,
            serverTime: Toucth.TimestampDate(orderData[i].serverTime),
            status: ''
          }
          switch (orderData[i].status) {
            case 0:
              obj.status = {
                name: '新单',
                router: {path: '/huoyun/await', query: {orderId: orderData[i].id}}
              }
              break
            case 1:
              obj.status = {
                name: '等待中',
                router: {path: '/huoyun/await', query: {orderId: orderData[i].id}}
              }
              break
            case 2:
              obj.status = {
                name: '已接单',
                router: {path: '/huoyun/service', query: {orderId: orderData[i].id}}
              }
              break
            case 3:
              obj.status = {
                name: '到达预约地',
                router: {path: '/huoyun/service', query: {orderId: orderData[i].id}}
              }
              break
            case 4:
              if (orderData[i].treatment) {
                obj.status = {
                  name: '待结算',
                  router: {path: '/huoyun/settlement', query: {orderId: orderData[i].id}}
                }
                this.$router.push({path: '/huoyun/settlement', query: {orderId: orderData[i].id}})
              } else {
                obj.status = {
                  name: '前往目的地',
                  router: {path: '/huoyun/service', query: {orderId: orderData[i].id}}
                }
              }
              break
            case 5:
              if (orderData[i].review) {
                obj.status = {
                  name: '已完成',
                  router: {path: '/huoyun/checkOrder', query: {orderId: orderData[i].id}}
                }
              } else {
                obj.status = {
                  name: '待评价',
                  router: {path: '/huoyun/affirm', query: {orderId: orderData[i].id}}
                }
              }
              break
            case 6:
              obj.status = {
                name: '已销单',
                router: {path: '/huoyun/checkOrder', query: {orderId: orderData[i].id}}
              }
              break
          }
          orderList.push(obj)
        }
        // console.log(orderList)
        return orderList
      }
    },
    data () {
      return {
        page: 0,
        rows: 10,
        loadState: false
      }
    },
    watch: {},
    methods: {
      recordOrderAcFn: function (val) {
        switch (val.status) {
          case 0:
            this.$router.push({path: '/huoyun/await', query: {orderId: val.id}})
            break
          case 1:
            this.$router.push({path: '/huoyun/await', query: {orderId: val.id}})
            break
          case 2:
            this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            break
          case 3:
            this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            break
          case 4:
            if (val.treatment) {
              this.$router.push({path: '/huoyun/settlement', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            }
            break
          case 5:
            if (val.review) {
              this.$router.push({path: '/huoyun/checkOrder', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/huoyun/affirm', query: {orderId: val.id}})
            }
            break
          case 6:
            break
        }
      },
      loadDataButFn: function () { //  获取更多订单
        let that = this
        that.page++
        that.loadState = true
        that.$store.dispatch('HyQueryAllOrder', {
          'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
          'page': that.page,
          'rows': that.rows,
          fn: (state) => {
            if (state) {
              that.loadState = false
            }
          }
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.loadState = true
          that.$store.dispatch('HyQueryAllOrder', {
            'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
            'page': that.page,
            'rows': that.rows,
            type: 'upData',
            fn: (data) => {
              if (data) {
                that.loadState = false
              }
            }
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .recordYongChe {
    .yongcheContent {
      .loadDataBut {
        box-sizing: border-box;
        width: 100%;
        line-height: 3.5rem;
        padding: 0 1rem;
        span {
          width: 100%;
          background-color: #fff;
          border-radius: 0.333rem;
          text-align: center;
        }
      }
    }
  }
</style>



