/**侧滑菜单***/
<template>
  <div class='sideMenus' v-bind:class="{'showSideMenus':sideMenusState}">
    <div class='sideMenusContent'>
      <div class="personInfo clear" @click="compileNomFn">
        <div class="headImg">
          <img v-if="upImgFn" :src="upImgFn" alt="头像">
          <img v-else="" src="../../../../assets/img/yonghu/logo.png" alt="头像">
        </div>
        <div class="info">
          <h2 class="infoName">{{loginData && loginData.passenger ? loginData.passenger.name : ''}}</h2>
          <p class="vipRank">{{loginData && loginData.passenger ? loginData.passenger.grade_name : ''}}</p>
          <p class="phone">
            {{loginData && loginData.passenger && loginData.passenger.phone ? loginData.passenger.phone.replace(/(\d{3})(\d{4})(\d{4})/, '$1****$3') : ''}}</p>
        </div>
      </div>
      <div class="menusOption">
        <ul>
          <li @click="sideMenusFn">
            <router-link :to="orderNav&&orderNav.length>0?orderNav[0].url:''"><i class="iconfont icon-dingdan"></i><span
              class="text">我的订单</span>
            </router-link>
          </li>
          <li @click="sideMenusFn">
            <router-link :to="'/mewallet'"><i class="iconfont  icon-qianbao"></i><span class="text">我的钱包</span>
            </router-link>
          </li>
          <li @click="sideMenusFn">
            <router-link :to="'/qrCode'"><i class="iconfont  icon-erweima"></i><span class="text">我的二维码</span>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="bottomMeuns">
        <div class="content">
            <span class="informBut" @click="sideMenusFn"><router-link to="/inform"><i
              class="iconfont icon-tongzhi"></i></router-link>
              <xk-badge-mod v-if="imformNum.content!==''" :text="imformNum.num"></xk-badge-mod>
            </span>
          <span class="shareBut" @click="sideMenusFn"><router-link to="/friend"><i
            class="iconfont icon-fenxiang"></i></router-link></span>
          <span class="setBut" @click="sideMenusFn"><router-link to="/settings"><i
            class="iconfont icon-shezhi"></i></router-link></span>
          <span class="contentText">邀请好友</span>
        </div>
      </div>
    </div>
    <div class="modal" @click="sideMenusFn">
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import XkBadgeMod from '../../badge/src/badge.vue'

  export default {
    components: {XkBadgeMod},
    name: 'xk-sideMenus',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        imformNum: 'imformNum',
        orderNav: 'orderNav'
      }),
      upImgFn: function () {
        if (this.loginData.passenger && this.loginData.passenger.avatar) {
          return window.configure.imgUrl + this.loginData.passenger.avatar
        } else {
          return false
        }
      }
    },
    data () {
      return {
        sideMenusState: false,
        headPortrait: null
      }
    },
    watch: {
      sideMenusState (val) {
        let that = this
        if (val) {
          that.$store.dispatch('checkLogin', (state) => {
            if (state) {
              // let imformNumFn = () => {
              //   that.$store.dispatch('imformNum', {
              //     'passengerId': that.loginData.passenger.id
              //   })
              // }
              // imformNumFn()
            }
          })
        }
      }
    },
    methods: {
      sideMenusFn: function () { // 点击模态框  收回个人中心
        console.log(1231231)
        let State = this.sideMenusState
        this.sideMenusState = !State
      },
      compileNomFn: function () {
        this.$router.push('/compileNom')
      }
    },
    mounted: function () {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">

  .sideMenus {
    position: fixed;
    left: -110%;
    top: 0;
    height: 100%;
    z-index: -999;
    transition: 0.3s all;
    .sideMenusContent {
      box-sizing: border-box;
      width: 80vw;
      height: 100vh;
      background: #fff;
      position: relative;
      z-index: 555;
      padding-top: 5.25rem;
      .personInfo {
        padding: 0 1rem;
        height: 7.375rem;
        width: 100%;
        box-sizing: border-box;
        .headImg {
          box-sizing: border-box;
          width: 7.375rem;
          height: 7.375rem;
          border-radius: 50%;
          border: 1px solid #eee;
          overflow: hidden;
          float: left;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .info {
          box-sizing: border-box;
          width: calc(~'100% - 7.375rem');
          float: left;
          padding-left: 1.3rem;
          padding-top: 1rem;

          .infoName {
            font-size: 1.25rem;
            color: #333;
            line-height: 2rem;
            font-weight: bold;
          }
          .vipRank {
            font-size: 1rem;
            line-height: 1.66660rem;
            color: #363636;
          }
          .phone {
            color: #999;
          }
        }

      }
      .menusOption {
        margin-top: 5rem;
        width: 100%;
        box-sizing: border-box;
        ul {
          width: 100%;
          li {
            height: 3.25rem;
            width: 100%;
            box-sizing: border-box;
            padding: 0 1rem;
            a {
              width: 100%;
              color: #333;
              height: 100%;
              line-height: 2.4;
              .text {
                font-size: 1.25rem;
              }
              i {
                margin-right: 1.0833rem;
              }
              .icon-erweima {
                font-size: 1.2rem;
                margin-left: -0.1rem;
                margin-right: 0.9rem;
              }
            }
          }
        }
      }
      .bottomMeuns {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 5.08333rem;
        box-sizing: border-box;
        width: 100%;
        padding: 0 1rem;
        background: url(../../../../assets/img/yonghu/bottom_bg.png) no-repeat center bottom;
        background-size: auto 100%;
        .content {
          height: 100%;
          .informBut, .setBut, .shareBut, .contentText {
            a {
              border-radius: 50%;
              display: inline-block;
            }
          }
          .informBut, .setBut {
            position: absolute;
            top: 50%;
            transform: translate(0, -45%);
            a {
              width: 3.33333rem;
              height: 3.33333rem;
              border: 1px solid #d4d4d4;
              background: #fff;
              border-radius: 50%;
              position: relative;
              i {
                font-size: 1.4rem;
                position: absolute;
                left: 50%;
                top: 50%;
                color: #333;
                transform: translate(-50%, -50%);
              }
            }
          }
          .informBut {
            left: 1.43333rem;
            .badgeMod {
              position: absolute;
              right: -0.2rem;
              top: -0.2rem;
              border: 2px solid #E9E7E5;
            }
          }
          .setBut {
            right: 1.83333rem;
          }
          .shareBut {
            position: absolute;
            top: -2.4rem;
            left: 50%;
            transform: translate(-51%, 0);
            width: 5rem;
            height: 5rem;
            background: #f9554d;
            border-radius: 50%;
            overflow: hidden;
            a {
              width: 100%;
              height: 100%;
              i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                color: #fff;
                font-size: 3rem;
              }
            }
          }
          .contentText {
            position: absolute;
            bottom: 0.4rem;
            left: 50%;
            transform: translate(-50%, 0);
            color: #514f4c;
            font-size: 1rem;
          }
        }
      }
    }
    .modal {
      width: 200%;
      height: 200%;
      opacity: 0;
      background-color: rgba(0, 0, 0, 0.5);
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -100;
      transition: 0.3s all;
    }
  }

  .showSideMenus {
    left: 0;
    top: 0;
    z-index: 999;
    .modal {
      opacity: 1;
      z-index: 300;
    }
  }
</style>



