/***记录订单组件**/
<template>
  <div class="recordOrderMod" @click="actionFn">
    <div class="ListItem">
      <div class="itemContent">
        <div class="row row1">
          <div class="itemTime">
            {{serverTime}}
          </div>
          <div class="itemStatus">
            <i class="iconfont icon-jiantou"></i>
            <span class="text "
                  :class="status.name === '新单' ? 'textRed':status.name === 5||status.name === 6?'textAsh':''">
                  {{status.name}}
            </span>
          </div>
        </div>
        <div class="row row2">
          <i class="iconfont icon-yuan"></i>
          <span class="site">{{startAddress}}</span>
        </div>
        <div class="row row3">
          <i class="iconfont icon-yuan"></i>
          <span class="site">{{endAddress}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  //  import Toucth from '../../../../assets/js/method'

  export default {
    name: 'xk-recordOrderMod',
    props: ['startAddress', 'endAddress', 'serverTime', 'status', 'orderId', 'router'],
    computed: {},
    data () {
      return {}
    },
    methods: {
      actionFn: function () {
        let router = this.$props.status.router
        if (router) {
          this.$router.push({path: router.path, query: router.query})
        }
      }
    },
    mounted: function () {
//      let that = this
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .recordOrderMod {
    .ListItem {
      box-sizing: border-box;
      width: calc(~'100% - 1rem');
      margin: 1rem 0.5rem;
      padding: 0.5rem 1rem;
      background-color: #fff;
      border-radius: 0.33333rem;
      overflow: hidden;
      .itemContent {
        .row {
          height: 2rem;
          line-height: 2rem;
          overflow: hidden;
          .site {
            display: inherit;
            width: calc(~'100% - 5rem');
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
        .row1 {
          margin-bottom: 0.5rem;
          .itemTime {
            float: left;
            display: inline-block;
            font-size: 1rem;
            color: #333;
          }
          .itemStatus {
            float: right;
            display: inline-block;
            .text {
              float: right;
              color: #333;
              margin-right: 0.5rem;
            }
            .textRed {
              color: #f06553;
            }
            .textAsh {
              color: #999999;
            }
          }
          i {
            font-size: 0.8333rem;
            color: #999999;
            float: right;
          }
        }
        .row2, .row3 {
          color: #666666;
          font-size: 0.83333rem;
          i {
            line-height: 1.8rem;
            font-size: 0.66666rem;
            float: left;
            margin-right: 0.3333rem;
          }
        }
        .row2 {
          i {
            color: #3cafb9;
          }
        }
        .row3 {
          i {
            color: #f06553;
          }
        }
      }
    }
  }
</style>



