<template>
  <div class="ZX">
    <div class="ZXcontent">
      <div class="banner" v-if="zhuanxianBanner&&zhuanxianBanner.length>0">
        <xk-roll-banner :data="zhuanxianBanner"></xk-roll-banner>
      </div>
      <div class="circuit" v-if="!circuitSelState">
        <xk-circuit-mod :data="time" v-for="(time, index) in circuitData" @click="circuitFn(index)"
                        :key="index"></xk-circuit-mod>
      </div>
      <div class="selectType" v-if="circuitSelState">
        <div class="nav">
          <xk-type-tabs :cardata="typeAll" @click="typeTabFn"></xk-type-tabs>
        </div>
        <div class="selectTypeContent">
          <div class="row row3 clear" @click="selectTimeFn">
            <i class="iconfont icon-shijian"></i>
            <div class="val">
              <span>{{startTime.name}}</span>
            </div>
          </div>
          <div class="row row6 clear phone" v-if="typeAction==='pinche'">
            <i class="iconfont icon-mobile"></i>
            <div class="val">
              <input placeholder="请输入手机号" v-model="phoneNo"/>
            </div>
          </div>
          <div class="row row4 clear personNum" v-if="typeAction==='jihuo'">
            <i class="iconfont icon-man"></i>
            <div class="val">
              <input v-model="takeName" placeholder="收货人姓名"/>
            </div>
          </div>
          <div class="row row5 clear personPhone" v-if="typeAction==='jihuo'">
            <i class="iconfont icon-mobile"></i>
            <div class="val">
              <input placeholder="请输入收货人手机号" v-model="takePhone"/>
            </div>
          </div>
          <div class="row row1 clear findLine" @click.stop="lineStartFn('show')">
            <i class="iconfont icon-yuan"></i>
            <div class="val" v-if="findLineAction.startCity.city">
              <span>{{findLineAction.startCity.city}}</span>
              <span class="add">{{findLineAction.startCity.address}}</span>
            </div>
            <div v-else="" class="val placeholder">请选择起点城市</div>
          </div>
          <div class="row clear detailedStart">
            <xk-site-input placeholder="起点详细地址"
                           :name="findLineAction.detailedStart.name"
                           :city="findLineAction.startCity.city"
                           :state="findLineAction.startCity.city!=='' && findLineAction.startCity.address!==''"
                           @onClick="detailedStartFn"></xk-site-input>
            <div class="exchangeBut" @click="exchangeButFn">
              <i class="iconfont icon-zhuanhuan1" :class="{'exchangeButAc':exchangeButState}"></i>
            </div>
          </div>
          <div class="row row2 clear findLine" @click.stop="lineEndFn('show')">
            <i class="iconfont icon-yuan"></i>
            <div class="val" v-if="findLineAction.endCity.city">
              <span class="city">{{findLineAction.endCity.city}}</span>
              <span class="add">{{findLineAction.endCity.address}}</span>
            </div>
            <div v-else="" class="val placeholder">请选择终点城市</div>
          </div>
          <div class="row clear detailedEnd">
            <xk-site-input placeholder="终点详细地址"
                           :name="findLineAction.detailedEnd.name"
                           :city="findLineAction.endCity.city"
                           :state="findLineAction.endCity.city!=='' && findLineAction.endCity.address!==''"
                           @onClick="detailedEndFn"></xk-site-input>
          </div>
          <div v-if="typeAction==='pinche'" class="row passenger"
               @click="passengerNameFn('show')">
            <div>已选择<span>{{passengerNum}}</span>人乘车</div>
          </div>
          <div v-if="typeAction==='baoche'" class="row carNum"
               @click="passengerNameFn('show')">
            <div>已选择<span>{{carNum}}</span>辆车出行</div>
          </div>
          <div class="totalNum clear">
            <div class="row" @click="detailsBoxFn">
              约<span class="red">{{ZxBudgetMoney ? ZxBudgetMoneyFn : '0'}}</span>元
              <i class="iconfont icon-iconfontyiwen"></i>
            </div>
            <div class="line"></div>
            <div class="row">
              <xk-coupon-select
                @click="couponSlelectFn"
                :type="'zhuanxian'"
                :data="ZxCoupon"></xk-coupon-select>
            </div>
          </div>
        </div>
      </div>
      <div class="callOutBut">
        <span @click="placeAnOrderFn">立即下单</span>
      </div>
      <xk-unfilled-order-mod
        v-if="unfilledOrderState &&unfilledOrderState.zhuanxian &&  unfilledOrderFn.length>0"
        :data="unfilledOrderFn?unfilledOrderFn:null"
        :title="'专线'"
        @click="unfilledOrderAcFn"
        @close="unfilledOrdercCloseFn"
      ></xk-unfilled-order-mod>
      <xk-lineSelect v-if="(lineType==='start'?lineStartState:lineEndState)"
                     :data="(lineType==='start'?ZxLineStartCity:ZxLineEndCity)"
                     @click="lineClickFn"></xk-lineSelect>
      <xk-number-select v-if="passengerNumState"
                        :data="MaxPassengerNum"
                        @click="passengerNameFn"></xk-number-select>
      <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
      <xk-date-picker-mod @click="DatePickerFn" ref="DatePicker" v-if="datePickerState"></xk-date-picker-mod>
      <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
        <div slot="content">
          <div class="List">
            <ul>
              <li v-for="itme in BudgetCostDetails" v-if="itme.val !== 0" class="clear">
                <span class="name">{{itme.name}}</span>
                <span class="val" v-if="itme.val"><span class="sum">{{itme.val}}</span><span
                  class="unit">元</span></span>
                <span class="val" v-if="itme.car"><span class="sum">{{itme.car}}</span><span
                  class="unit">辆</span></span>
                <span class="val" v-if="itme.person"><span class="sum">{{itme.person}}</span><span class="unit">人</span></span>
                <span class="time" v-if="itme.time">({{itme.time}}<span class="unit">分钟)</span></span>
                <span class="time" v-if="itme.mileage">({{itme.mileage}}<span class="unit">公里)</span></span>
              </li>
            </ul>
          </div>
          <div class="introduce">
            {{ZxBudgetMoney && ZxBudgetMoney.pay_detail ? ZxBudgetMoney.pay_detail : ''}}
          </div>
        </div>
      </Xk-detailsBox>
      <xk-upload-mod :state="upLoadState"></xk-upload-mod>
    </div>
  </div>
</template>

<script>
  import Toucth from '../../../assets/js/method'
  import XkRollBanner from '../../module/rollBanner/src/rollBanner'
  import XkCircuitMod from '../../module/circuitMod/src/circuitMod'
  import XkTypeTabs from '../../module/typeTabs/src/typeTabs'
  import XkSiteInput from '../../module/siteInput/src/siteInput'
  import { mapGetters } from 'vuex'
  import XkLineSelect from '../../module/lineSelect/src/lineSelect'
  import XkNumberSelect from '../../module/numberSelect/src/numberSelect'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import XkCouponSelect from '../../module/couponSelect/src/couponSelect'
  import XkDatePickerMod from '../../module/datePickerMod/src/datePickerMod'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod.vue'

  export default {
    components: {
      XkUploadMod,
      XkUnfilledOrderMod,
      XkDatePickerMod,
      XkCouponSelect,
      XkHintBox,
      XkNumberSelect,
      XkLineSelect,
      XkSiteInput,
      XkTypeTabs,
      XkCircuitMod,
      XkRollBanner
    },
    name: 'ZhuanXian',
    computed: {
      ...mapGetters({
        zhuanxianBanner: 'zhuanxianBanner',
        positioning: 'positioning',
        ZxLineStartCity: 'ZxLineStartCity',
        unfilledOrderState: 'unfilledOrderState',
        ZxLineEndCity: 'ZxLineEndCity',
        companyInfo: 'companyInfo',
        unfilledOrder: 'unfilledOrder',
        ZxBudgetMoney: 'ZxBudgetMoney',
        ZxCoupon: 'ZxCoupon',
        login: 'login',
        loginData: 'loginData'
      }),
      findLineSelectFn: function () {
        let arr = []
        if (this.ZxFindLineAll && this.ZxFindLineAll.length > 0) {
          let ZxFindLineAll = this.ZxFindLineAll
          let startCity = this.findLineAction.startCity.city
          let findLineSelectType = this.findLineSelectType
          for (let i = 0; i < ZxFindLineAll.length; i++) {
            let obj = {}
            if (findLineSelectType === 'end') {
              if (ZxFindLineAll[i].startCity === startCity) {
                obj = {
                  city: ZxFindLineAll[i].endCity,
                  address: ZxFindLineAll[i].endAddress,
                  id: ZxFindLineAll[i].lineId
                }
                arr.push(obj)
              }
            } else {
              obj = {
                city: ZxFindLineAll[i].startCity,
                address: ZxFindLineAll[i].startAddress,
                id: ZxFindLineAll[i].lineId
              }
              arr.push(obj)
            }
          }
        }
        return arr
      },
      ZxBudgetMoneyFn: function () {
        let money = 0
        let that = this
        let val = that.ZxBudgetMoney
        if (that.ZxBudgetMoney && that.typeAction && that.budgetMoneyState) {
          money = that.ZxBudgetMoney.money
          switch (that.typeAction) {
            case 'pinche':
              money = money * that.passengerNum
              val.money = val.money * that.passengerNum
              break
            case 'baoche':
              money = money * that.carNum
              val.money = val.money * that.carNum
              break
          }
          if (that.couponMoney.type === 'dikou') {
            money = money - that.couponMoney.val
          }
          if (that.couponMoney.type === 'dazhe') {
            if (that.typeAction === 'baoche') {
              money = money - (that.ZxBudgetMoney.money * (1 - (that.couponMoney.val / 10)))
            } else {
              money = money - (money * (1 - (that.couponMoney.val / 10)))
            }
          }
          that.budgetMoney = money.toFixed(1)
          that.BudgetCostDetails = []
          if (val) {
            if (val) {
              if (that.typeAction === 'pinche') {
                that.BudgetCostDetails.push(
                  {
                    name: '起步价',
                    val: val.startPrice ? val.startPrice.toFixed(1) * that.passengerNum : val.startPrice
                  }
                )
                that.BudgetCostDetails.push(
                  {
                    name: '公里总价',
                    mileage: val.mileage,
                    val: val.mileagePrice ? val.mileagePrice.toFixed(1) * that.passengerNum : val.mileagePrice
                  }
                )
              }
              if (that.typeAction === 'baoche') {
                that.BudgetCostDetails.push(
                  {
                    name: '起步价',
                    val: val.startPrice ? val.startPrice.toFixed(1) * that.carNum : val.startPrice
                  }
                )
                that.BudgetCostDetails.push(
                  {
                    name: '公里总价',
                    mileage: val.mileage,
                    val: val.mileagePrice ? val.mileagePrice.toFixed(1) * that.carNum : val.mileagePrice
                  }
                )
              }
              that.BudgetCostDetails.push(
                {
                  name: '行驶时间',
                  time: val.travelTime,
                  val: val.travelTimePrice ? val.travelTimePrice.toFixed(1) : val.travelTimePrice
                }
              )
              if (that.typeAction === 'pinche') {
                that.BudgetCostDetails.push(
                  {
                    name: '人数',
                    person: that.passengerNum
                  }
                )
              }
              if (that.typeAction === 'baoche') {
                that.BudgetCostDetails.push(
                  {
                    name: '车辆',
                    car: that.carNum
                  }
                )
              }
              that.BudgetCostDetails.push(
                {
                  name: '优惠券',
                  val: (val.money - money.toFixed(1)) > 0 ? '-' + (val.money - money.toFixed(1)).toFixed(1) : 0
                }
              )
            }
          }
          if (money >= 0) {
            return money.toFixed(1)
          } else {
            return 0
          }
        } else {
          return 0
        }
      },
      unfilledOrderFn: function () {
        let unfilledOrder = []
        let zhuanxianData = this.unfilledOrder ? this.unfilledOrder.zhuanxian : null
        if (zhuanxianData && zhuanxianData.length > 0) {
          let obj = {}
          for (let i = 0; i < zhuanxianData.length; i++) {
            obj = {
              startAddress: zhuanxianData[i].startAddress,
              endAddress: zhuanxianData[i].endAddress,
              serverTime: Toucth.TimestampDate(zhuanxianData[i].serverTime),
              status: ''
            }
            switch (zhuanxianData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/zhuanxian/await', query: {orderId: zhuanxianData[i].id}}
                }
                break
              case 1:
                obj.status = {
                  name: '等待中',
                  router: {path: '/zhuanxian/await', query: {orderId: zhuanxianData[i].id}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/zhuanxian/service', query: {orderId: zhuanxianData[i].id}}
                }
                break
              case 3:
                obj.status = {
                  name: '到达预约地',
                  router: {path: '/zhuanxian/service', query: {orderId: zhuanxianData[i].id}}
                }
                break
              case 4:
                if (zhuanxianData[i].treatment) {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/zhuanxian/settlement', query: {orderId: zhuanxianData[i].id}}
                  }
                  this.$router.push({path: '/zhuanxian/settlement', query: {orderId: zhuanxianData[i].id}})
                } else {
                  obj.status = {
                    name: '前往目的地',
                    router: {path: '/zhuanxian/service', query: {orderId: zhuanxianData[i].id}}
                  }
                }
                break
              case 5:
                if (zhuanxianData[i].review) {
                  obj.status = {
                    name: '已完成',
                    router: {path: '/zhuanxian/checkOrder', query: {orderId: zhuanxianData[i].id}}
                  }
                } else {
                  obj.status = {
                    name: '待评价',
                    router: {path: '/zhuanxian/affirm', query: {orderId: zhuanxianData[i].id}}
                  }
                }
                break
              case 6:
                obj.status = {
                  name: '已销单',
                  router: {path: '/zhuanxian/checkOrder', query: {orderId: zhuanxianData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      }
    },
    data () {
      return {
        typeAll: {
          action: 0,
          data: [
            {
              icon: '',
              name: window.configure.appKey === '6b529cb5c07b4ad6a312db2273c580a3' ? '出租车' : '拼车',
              anchor: 'pinche'
            },
            {
              icon: '',
              name: window.configure.appKey === '6b529cb5c07b4ad6a312db2273c580a3' ? '中巴' : '包车',
              anchor: 'baoche'
            },
            {
              icon: '',
              name: window.configure.appKey === '6b529cb5c07b4ad6a312db2273c580a3' ? '物流' : '寄货',
              anchor: 'jihuo'
            }
          ]
        },
        typeAction: 'pinche',
        circuitSelState: true,
        city: {
          lng: '',
          lat: '',
          city: '',
          address: '',
          addressDetail: ''
        },
        phoneNo: '',
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        findLineAction: {
          startCity: {
            city: '',
            address: ''
          },
          detailedStart: {
            detailedAddress: '',
            name: '',
            lat: '',
            lng: ''
          },
          endCity: {
            city: '',
            address: ''
          },
          detailedEnd: {
            detailedAddress: '',
            name: '',
            lat: '',
            lng: ''
          },
          pincheAreaId: '',
          jihuoAreaId: '',
          baocheAreaId: '',
          id: ''
        },
        lineStartState: false,
        exchangeButState: false,
        lineStartType: null,
        lineType: 'start',
        upLoadState: false,
        lineEndState: false,
        passengerNum: 1,
        carNum: 1,
        takePhone: '',
        takeName: '',
        passengerNumState: false,
        MaxPassengerNum: 7,
        couponMoney: {
          type: '',
          id: '',
          num: ''
        },
        budgetMoney: '',
        budgetMoneyState: false,
        startTime: {
          name: '',
          val: ''
        },
        datePickerState: false,
        BudgetCostDetails: []
      }
    },
    methods: {
      circuitFn: function (val) {
        this.circuitSelState = !this.circuitSelState
      },
      typeTabFn: function (val) {
        var that = this
        that.typeAction = val.data.anchor
        that.typeAll.action = val.index
        if (val.anchor === 'baoche') {
          that.MaxPassengerNum = 10
        } else if (val.anchor === 'pinche') {
          that.MaxPassengerNum = 7
        }

        that.budgetMoneyFn()
      },
      lineStartFn: function (val) {
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            if (val === 'show') {
              that.lineStartType = null
              that.lineType = 'start'
              that.lineStartState = true
              return
            }
            if (val && val.city && val.address) {
              that.findLineAction.startCity.city = val.city
              that.findLineAction.startCity.address = val.address
              that.findLineAction.detailedStart = {
                detailedAddress: '',
                name: '',
                lat: '',
                lng: ''
              }
              that.findLineAction.endCity = {
                city: '',
                address: ''
              }
              that.findLineAction.detailedEnd = {
                detailedAddress: '',
                name: '',
                lat: '',
                lng: ''
              }
              that.budgetMoneyFn()
            }
            that.lineStartState = false
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      lineClickFn (val) {
        if (this.lineType === 'start') {
          this.lineStartFn(val)
        } else {
          this.lineEndFn(val)
        }
      },
      budgetMoneyFn: function (val) { // 预算费用
        let that = this
        if ((that.findLineAction.detailedStart.lat && that.findLineAction.detailedStart.lng) || (that.findLineAction.detailedEnd.lat && that.findLineAction.detailedEnd.lng)) {
          that.budgetMoneyState = true
          let areaId = ''
          switch (that.typeAction) {
            case 'pinche':
              areaId = that.findLineAction.pincheAreaId
              break
            case 'jihuo':
              areaId = that.findLineAction.jihuoAreaId
              break
            case 'baoche':
              areaId = that.findLineAction.baocheAreaId
              break
          }
          that.$store.dispatch('ZxBudgetMoney', {
            'areaId': areaId,
            'startLat': that.findLineAction.detailedStart.lat,
            'startLng': that.findLineAction.detailedStart.lng,
            'endLat': that.findLineAction.detailedEnd.lat,
            'endLng': that.findLineAction.detailedEnd.lng
          })
        }
      },
      lineEndFn: function (val) {
        let that = this
        if (that.findLineAction.startCity.city && that.findLineAction.startCity.address) {
          if (val === 'show') {
            that.$store.dispatch('ZxLineEndCity', {
              startCity: that.findLineAction.startCity.city,
              startAddress: that.findLineAction.startCity.address,
              fn () {
                that.lineType = 'end'
                that.lineEndState = true
              }
            })
            return
          }
          if (val && val.city && val.address) {
            that.findLineAction.baocheAreaId = val.address.baocheAreaId
            that.findLineAction.jihuoAreaId = val.address.jihuoAreaId
            that.findLineAction.pincheAreaId = val.address.pincheAreaId
            that.findLineAction.endCity.city = val.city
            that.findLineAction.endCity.address = val.address.endAddress || val.address.startAddress
            that.findLineAction.id = val.address.lineId
            that.findLineAction.detailedEnd = {
              detailedAddress: '',
              name: '',
              lat: '',
              lng: ''
            }
            that.budgetMoneyFn()
          }
          that.lineEndState = false
        } else {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '起点不能为空'
          }
          setTimeout(() => {
            that.hintBox.stateShow = false
          }, 1500)
        }
      },
      passengerNameFn: function (val) {
        if (val === 'show') {
          this.passengerNumState = true
          return
        }
        if (val && val.val) {
          if (this.typeAction === 'pinche') {
            this.passengerNum = val.val
          }
          if (this.typeAction === 'baoche') {
            this.carNum = val.val
          }
        }
        this.passengerNumState = false
        this.budgetMoneyFn()
      },
      detailedStartFn: function (val) {
        let that = this
        if (that.findLineAction.startCity.city !== '' && that.findLineAction.startCity.address !== '') {
          if (that.findLineAction.startCity.city === val.city) {
            that.findLineAction.detailedStart = {
              detailedAddress: val.detailedAddress,
              name: val.name,
              lat: val.lat,
              lng: val.lng
            }
            that.budgetMoneyFn()
          } else {
            that.findLineAction.detailedStart = {
              detailedAddress: '',
              name: '',
              lat: '',
              lng: ''
            }
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请选择起点城市地址'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        } else {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '起点不能为空'
          }
          setTimeout(() => {
            that.hintBox.stateShow = false
          }, 1500)
        }
      },
      detailedEndFn: function (val) {
        let that = this
        if (that.findLineAction.endCity.city !== '' && that.findLineAction.endCity.address !== '') {
          if (that.findLineAction.endCity.city === val.city) {
            that.findLineAction.detailedEnd = {
              detailedAddress: val.detailedAddress,
              name: val.name,
              city: val.city,
              lat: val.lat,
              lng: val.lng
            }
            that.budgetMoneyFn()
          } else {
            that.findLineAction.detailedEnd = {
              detailedAddress: '',
              name: '',
              lat: '',
              lng: ''
            }
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请选择中终点城市的地址'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        } else {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '终点不能为空'
          }
          setTimeout(() => {
            that.hintBox.stateShow = false
          }, 1500)
        }
      },
      couponSlelectFn: function (obj) {
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            if (obj.type === 'nonuse') {
              that.couponMoney = {
                type: '',
                id: '',
                num: ''
              }
              return true
            }
            if (obj.type === 'action' && obj.obj && obj !== '') {
              if (obj.obj.couponRule.discount && obj.obj.couponRule.discount !== '') {
                that.couponMoney = {
                  type: 'dazhe',
                  val: obj.obj.couponRule.discount,
                  id: obj.obj.id
                }
              } else if (obj.obj.couponRule.money && obj.obj.couponRule.money !== '') {
                that.couponMoney = {
                  type: 'dikou',
                  val: obj.obj.couponRule.money,
                  id: obj.obj.id
                }
              }
              return true
            }
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      placeAnOrderFn: function (obj) { // 下单
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            (() => {
              let state = false
              let content = ''

              function hintBox () {
                if (state) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: content
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1500)
                  return
                }
              }

              if (that.typeAction === 'pinche' && that.phoneNo === '') {
                content = '手机号不能为空'
                state = true
                hintBox()
                return
              }

              if (that.typeAction === 'pinche' && !Toucth.checkPhone(that.phoneNo)) {
                content = '请输入正确手机号'
                state = true
                hintBox()
                return
              }
              if (that.typeAction === 'jihuo' && that.takeName === '') {
                content = '收货人不能为空'
                state = true
                hintBox()
                return
              }
              if (that.typeAction === 'jihuo' && that.takePhone === '') {
                content = '收货人手机号不能为空'
                state = true
                hintBox()
                return
              }
              if (that.typeAction === 'jihuo' && !Toucth.checkPhone(that.takePhone)) {
                content = '请输入正确收货人手机号'
                state = true
                hintBox()
                return
              }
              if (that.findLineAction.startCity.city === '' && that.findLineAction.startCity.address === '') {
                content = '起点不能为空'
                state = true
                hintBox()
                return
              }
              if (that.findLineAction.detailedStart.lng === '' && that.findLineAction.detailedStart.lat === '') {
                content = '详细起点不能为空'
                state = true
                hintBox()
                return
              }
              if (that.findLineAction.endCity.city === '' && that.findLineAction.endCity.address === '') {
                content = '终点不能为空'
                state = true
                hintBox()
                return
              }
              if (that.findLineAction.detailedEnd.lng === '' && that.findLineAction.detailedEnd.lat === '') {
                content = '详细终点不能为空'
                state = true
                hintBox()
                return
              }
              that.upLoadState = true
              let params = {
                'orderType': '',
                'passengerId': that.loginData.passenger.id,
                'passengerPhone': that.loginData.passenger.phone,
                'lineId': that.findLineAction.id,
                'startAddress': that.findLineAction.detailedStart.name,
                'startLat': that.findLineAction.detailedStart.lat,
                'startLng': that.findLineAction.detailedStart.lng,
                'endAddress': that.findLineAction.detailedEnd.name,
                'endLat': that.findLineAction.detailedEnd.lat,
                'endLng': that.findLineAction.detailedEnd.lng,
                'lat': that.positioning.lat !== '' ? Number(that.positioning.lat).toFixed(6) : '',
                'lng': that.positioning.lng !== '' ? Number(that.positioning.lng).toFixed(6) : '',
                'startTime': that.startTime.val,
                'budgetPay': that.budgetMoney,
                'memo': '',
                'couponId': that.couponMoney.id,
                'peopleNumber': '',
                'takeDeliveryName': '',
                'takeDeliveryPhone': '',
                'openId': that.loginData && that.loginData.openId ? that.loginData.openId : '',
                fn: function (data) {
                  that.upLoadState = false
                  if (data.code === 0) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'yes',
                      content: '下单成功'
                    }
                    setTimeout(() => {
                      that.hintBox.stateShow = false
                      if (data.data.length === 1) {
                        that.$router.push({path: '/zhuanxian/await', query: {orderId: data.data[0].id}})
                      }
                      if (data.data.length > 1) {
                        that.$router.push('/orderRecord/zhuanxian')
                      }
                    }, 1500)
                  }
                  if (data.code !== 0) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'no',
                      content: data.message
                    }
                    setTimeout(() => {
                      that.hintBox.stateShow = false
                      if (data.code === -86) {
                        that.$store.dispatch('unfilledOrderState', {type: 'zhuanxian', state: true})
                      }
                    }, 1500)
                  }
                }
              }
              switch (that.typeAction) {
                case 'pinche':
                  params.orderType = 0
                  params.peopleNumber = that.passengerNum
                  break
                case 'baoche':
                  params.orderType = 1
                  params.peopleNumber = that.carNum
                  break
                case 'jihuo':
                  params.orderType = 2
                  params.takeDeliveryName = that.takeName
                  params.takeDeliveryPhone = that.takePhone
                  break
              }
              that.$store.dispatch('ZxMakeAnAppointment', params)
            })()
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      DatePickerFn: function (time) { // 时间选择
        if (time) {
          this.startTime.val = time.val
          this.startTime.name = time.name
        }
        this.datePickerState = false
      },
      selectTimeFn: function () {
        this.datePickerState = true
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'zhuanxian', state: false})
      },
      unfilledOrderAcFn: function (val) { // 未完成订单
        switch (val.status) {
          case 0:
            this.$router.push({path: '/zhuanxian/await', query: {orderId: val.id}})
            break
          case 1:
            this.$router.push({path: '/zhuanxian/await', query: {orderId: val.id}})
            break
          case 2:
            this.$router.push({path: '/zhuanxian/service', query: {orderId: val.id}})
            break
          case 3:
            this.$router.push({path: '/zhuanxian/service', query: {orderId: val.id}})
            break
          case 4:
            if (val.treatment) {
              this.$router.push({path: '/zhuanxian/settlement', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/zhuanxian/service', query: {orderId: val.id}})
            }
            break
          case 5:
            if (val.review) {
//              this.$router.push({path: '/zhuanxian', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/zhuanxian/affirm', query: {orderId: val.id}})
            }
            break
          case 6:
            break
        }
      },
      exchangeButFn: function () {
        let that = this
        let findLineAction = that.findLineAction
        if (!that.exchangeButState) {
          return
        }
        findLineAction.startCity = [findLineAction.endCity, findLineAction.endCity = findLineAction.startCity][0]
        findLineAction.detailedStart = [findLineAction.detailedEnd, findLineAction.detailedEnd = findLineAction.detailedStart][0]
        that.$store.dispatch('ZxLineEndCity', {
          startCity: that.findLineAction.startCity.city,
          startAddress: that.findLineAction.startCity.address
        })
      },
      detailsBoxFn: function () { // 详情事件
        if (this.ZxBudgetMoney && this.ZxBudgetMoney.money > 0) {
          this.$refs.detailsBox.switchFn()
        }
      }
    },
    watch: {
      positioning: function (val) {
        let that = this
        if (val) {
          that.findLineAction.startCity.city = val.city
          that.startAdd = {
            lng: that.positioning.lng,
            lat: that.positioning.lat,
            city: that.positioning.city,
            address: that.positioning.name,
            addressDetail: that.positioning.detailedAddress
          }
        } else {
          that.startAdd = {
            lng: '',
            lat: '',
            city: '',
            address: '定位失败，请手动选择',
            addressDetail: ''
          }
        }
      },
      login: function (val) {
        let that = this
        if (val) {
          that.phoneNo = that.loginData.passenger.phone
          that.$store.dispatch('ZxLineStartCity', {
            fn: function (data) {
              let ZxLineStartCity = data
              let state = null;
              (() => {
                if (ZxLineStartCity.length > 0) {
                  for (let i = 0; i < ZxLineStartCity.length; i++) {
                    if (ZxLineStartCity[i].city === that.positioning.city) {
                      state = true
                      that.findLineAction.startCity.city = ZxLineStartCity[i].city
                      that.findLineAction.startCity.address = ZxLineStartCity[i].address[0]
                      if (that.positioning.name !== '' && that.positioning.lat !== '' && that.positioning.lng !== '') {
                        that.findLineAction.detailedStart = {
                          detailedAddress: that.positioning.detailedAddress,
                          name: that.positioning.name,
                          lat: that.positioning.lat,
                          lng: that.positioning.lng
                        }
                      } else {
                        that.findLineAction.detailedStart = {
                          detailedAddress: '',
                          name: '定位失败，手动选择地址',
                          lat: '',
                          lng: ''
                        }
                      }
                    }
                  }
                }
              })()
              if (state) {
                that.findLineAction.startCity.city = ZxLineStartCity[0].city
                that.findLineAction.startCity.address = ZxLineStartCity[0].address[0]
              }
              that.$store.dispatch('ZxLineEndCity', {
                startCity: that.findLineAction.startCity.city,
                startAddress: that.findLineAction.startCity.address
              })
            }
          })
        }
      },
      findLineAction: {
        handler: function (val) {
          let startCity = val.startCity
          let endCity = val.endCity
          if (endCity.city && endCity.address && startCity.city && startCity.address) {
            this.exchangeButState = true
          } else {
            this.exchangeButState = false
          }
        },
        deep: true
      }
    },
    mounted: function () {
      let that = this
      let date = new Date()
      that.startTime.name = '现在出发'
      that.startTime.val = date.valueOf()
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.phoneNo = that.loginData.passenger.phone
          let date = new Date()
          that.time = {
            val: date.valueOf(),
            name: '现在出发'
          }
          that.$store.dispatch('unfilledOrder', {passengerId: that.loginData.passenger.id})
          that.findLineAction.startCity.city = that.positioning.city
          that.startAdd = {
            lng: that.positioning.lng,
            lat: that.positioning.lat,
            city: that.positioning.city,
            address: that.positioning.name,
            addressDetail: that.positioning.detailedAddress
          }
          that.$store.dispatch('ZxLineStartCity', {
            fn: function (data) {
              let ZxLineStartCity = data
              let state = null;
              (() => {
                if (ZxLineStartCity && ZxLineStartCity.length > 0) {
                  for (let i = 0; i < ZxLineStartCity.length; i++) {
                    if (ZxLineStartCity[i].city === that.positioning.city) {
                      state = true
                      that.findLineAction.startCity.city = ZxLineStartCity[i].city
                      that.findLineAction.startCity.address = ZxLineStartCity[i].address[0]
                      if (that.positioning.name !== '' && that.positioning.lat !== '' && that.positioning.lng !== '') {
                        that.findLineAction.detailedStart = {
                          detailedAddress: that.positioning.detailedAddress,
                          name: that.positioning.name,
                          lat: that.positioning.lat,
                          lng: that.positioning.lng
                        }
                      } else {
                        that.findLineAction.detailedStart = {
                          detailedAddress: '',
                          name: '定位失败，手动选择地址',
                          lat: '',
                          lng: ''
                        }
                      }
                    }
                  }
                }
              })()
              if (state) {
                that.findLineAction.startCity.city = ZxLineStartCity[0].city
                that.findLineAction.startCity.address = ZxLineStartCity[0].address[0]
              }
              that.$store.dispatch('ZxLineEndCity', {
                startCity: that.findLineAction.startCity.city,
                startAddress: that.findLineAction.startCity.address
              })
            }
          })
          that.$store.dispatch('ZxCoupon', {passengerId: that.loginData.passenger.id})
        } else {
          that.$store.dispatch('loginAction', {state: false})
        }
      })
    }
  }
</script>

