<script>
  import XkSiteInput from '../../module/siteInput/src/siteInput'
  import XkInput from '../../module/input/src/input'
  import Toucth from '../../../assets/js/method'
  import { mapGetters } from 'vuex'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkCouponSelect from '../../module/couponSelect/src/couponSelect'
  import XkDatePickerMod from '../../module/datePickerMod/src/datePickerMod'
  import XkUpImgDom from '../../module/upImg/src/upImg'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import XkCheckInput from '../../module/checkInput/src/checkInput'
  import XkDetailsBox from '../../module/detailsBox/src/detailsBox.vue'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod.vue'

  export default {
    components: {
      XkUnfilledOrderMod,
      XkDetailsBox,
      XkCheckInput,
      XkHintBox,
      XkUpImgDom,
      XkDatePickerMod,
      XkCouponSelect,
      XkUploadMod,
      XkInput,
      XkSiteInput
    },
    name: '',
    computed: {
      ...mapGetters({
        infoData: 'infoData',
        PtBudgetCost: 'PtBudgetCost',
        PtCoupon: 'PtCoupon',
        mobileState: 'mobileState',
        unfilledOrderState: 'unfilledOrderState',
        companyInfo: 'companyInfo',
        unfilledOrder: 'unfilledOrder',
        positioning: 'positioning',
        loginData: 'loginData'
      }),
      unfilledOrderFn: function () {
        let unfilledOrder = []
        let paotuiData = this.unfilledOrder && this.unfilledOrder.paotui ? this.unfilledOrder.paotui : null
        if (paotuiData && paotuiData.length > 0) {
          let obj = {}
          for (let i = 0; i < paotuiData.length; i++) {
            obj = {
              startAddress: paotuiData[i].startAddress,
              endAddress: paotuiData[i].endAddress,
              serverTime: paotuiData[i].startTime,
              orderId: paotuiData[i].id,
              status: ''
            }
            switch (paotuiData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].id}}
                }
                break
              case 1:
                obj.status = {
                  name: '已派单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].id}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/paotui/server', query: {orderId: paotuiData[i].id}}
                }
                break
              case 3:
                if (paotuiData[i].treatment) {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/paotui/settlement', query: {orderId: paotuiData[i].id}}
                  }
                } else {
                  obj.status = {
                    name: '执行中',
                    router: {path: '/paotui/server', query: {orderId: paotuiData[i].id}}
                  }
                }
                break
              case 4:
                if (paotuiData[i].treatment) {
                  if (paotuiData[i].review) {
                    obj.status = {
                      name: '已完成',
                      router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].id}}
                    }
                  } else {
                    obj.status = {
                      name: '待评价',
                      router: {path: '/paotui/affirm', query: {orderId: paotuiData[i].id}}
                    }
                  }
                }
                break
              case 5:
                obj.status = {
                  name: '已销单',
                  router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      },
      sumFn () {
        let that = this
        let sum = 0
        if (that.PtBudgetCost) {
          sum += Number(that.PtBudgetCost.money)
        }
        if (that.couponData) {
          if (that.couponData.couponRule.money) {
            sum -= that.couponData.couponRule.money
          } else {
            sum = sum * (that.couponData.couponRule.discount / 10)
          }
        }
        sum = sum > 0 ? sum : 0
        if (that.orderData.award.val) {
          sum += Number(that.orderData.award.val)
        }
        if (that.orderData.price.val) {
          sum += Number(that.orderData.price.val)
        }
        return sum.toFixed(1)
      },
      PtCouponFn () {
        let that = this
        let obj = []
        let business = that.infoData.services ? that.infoData.services.paotui.services : []
        let couponTypeName = ''
        for (let i = 0; i < business.length; i++) {
          if (business[i].id === Number(Toucth.AcquireUrlParameter().id)) {
            couponTypeName = business[i].typeName
          }
        }
        if (that.PtCoupon && that.PtCoupon.length > 0) {
          for (let i = 0; i < that.PtCoupon.length; i++) {
            if (couponTypeName === that.PtCoupon[i].couponRule.couponTypeName) {
              that.PtCoupon[i].couponRule.end = that.PtCoupon[i].couponRule.end ? Toucth.TimestampDate(that.PtCoupon[i].couponRule.end) : that.PtCoupon[i].couponRule.end
              obj.push(that.PtCoupon[i])
            }
          }
          if (obj.length > 0) {
            return obj
          } else {
            return []
          }
        } else {
          return []
        }
      }
    },
    data () {
      return {
        labelsData: {
          checkState: false,
          data: [],
          actionNum: []
        },
        orderData: {
          demand: {
            isRequire: true,
            showState: false,
            name: '购买需求',
            placeholder: '请填写商品的详细描述',
            val: ''
          },
          add: {
            startAdd: {
              isRequire: true,
              showState: false,
              name: '',
              placeholder: '',
              val: '',
              id: '',
              city: '',
              longitude: {
                lng: '',
                lat: ''
              }
            },
            endAdd: {
              isRequire: true,
              showState: false,
              name: '',
              placeholder: '',
              val: '',
              id: '',
              city: '',
              longitude: {
                lng: '',
                lat: ''
              }
            },
            detailStartAdd: {
              isRequire: true,
              showState: false,
              name: '详细地址',
              placeholder: '请输入详细起点',
              val: '成都市-温江区-小咖科技'
            },
            detailEndAdd: {
              isRequire: true,
              showState: false,
              name: '详细地址',
              placeholder: '请输入详细终点',
              val: ''
            }
          },
          contact: {
            deliver: {
              isRequire: true,
              showState: false,
              name: '发货人电话',
              placeholder: '请填写发货人电话',
              phoneNo: '18108162771'
            },
            take: {
              isRequire: true,
              showState: false,
              name: '收货人电话',
              placeholder: '请填写收货人电话',
              phoneNo: ''
            }
          },
          weight: {
            isRequire: true,
            showState: false,
            name: '1123',
            placeholder: '请填写物品重量',
            val: ''
          },
          upImg: {
            isRequire: true,
            showState: false,
            state: true,
            name: '上传图片',
            urls: []
          },
          appointTime: {
            isRequire: true,
            showState: false,
            state: true,
            name: '预约时间',
            placeholder: '请选择预约时间',
            val: '',
            showVal: ''
          },
          matter: {
            isRequire: true,
            showState: false,
            name: '注意事项',
            placeholder: '注意事项',
            val: ''
          },
          price: {
            isRequire: true,
            showState: false,
            state: true,
            buzhi: false,
            name: '商品价格',
            placeholder: '请选择预约时间',
            val: 0
          },
          award: {
            isRequire: true,
            showState: false,
            name: '小费打赏',
            placeholder: '添加打赏小费',
            val: 0
          },
          accessorialService: { // 附加服务
            isRequire: true,
            showState: false,
            checkState: false,
            content: [
              {
                state: false,
                name: '买花',
                id: '1'
              },
              {
                state: false,
                name: '买菜',
                id: '2'
              },
              {
                state: false,
                name: '卖东西',
                id: '3'
              }
            ],
            actionContent: []
          }
        },
        couponData: null,
        loadState: false,
        areaId: null,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        datePickerState: false,
        BudgetCostDetails: [],
        Time: '1',
        Mileage: '0'
      }
    },
    watch: {
      positioning (val) {
        let that = this
        alert(val.city)
        if (val && val.name && val.lng && val.lat && val.city) {
          if (that.orderData.add.startAdd.showState) {
            that.orderData.add.startAdd.val = val.name
            that.orderData.add.startAdd.longitude.lng = val.lng
            that.orderData.add.startAdd.longitude.lat = val.lat
            that.orderData.add.startAdd.city = val.city
            return true
          }
          if (that.orderData.add.endAdd.showState) {
            that.orderData.add.endAdd.val = val.name
            that.orderData.add.endAdd.longitude.lng = val.lng
            that.orderData.add.endAdd.longitude.lat = val.lat
            that.orderData.add.endAdd.city = val.city
            return true
          }
          that.distanceFn()
        } else {
          if (that.orderData.add.startAdd.showState) {
            that.orderData.add.startAdd.val = val.name
            that.orderData.add.startAdd.longitude.lng = ''
            that.orderData.add.startAdd.longitude.lat = ''
            that.orderData.add.startAdd.city = val.city
            return true
          }
          if (this.orderData.add.endAdd.showState) {
            that.orderData.add.endAdd.val = val.name
            that.orderData.add.endAdd.longitude.lng = ''
            that.orderData.add.endAdd.longitude.lat = ''
            that.orderData.add.endAdd.city = val.city
            return true
          }
          that.distanceFn()
        }
      },
      PtBudgetCost (val) {
        let that = this
        that.BudgetCostDetails = []
        if (val) {
          that.BudgetCostDetails.push(
            {
              name: '起步价',
              val: val.startPrice ? val.startPrice.toFixed(1) : val.startPrice
            }
          )
          that.BudgetCostDetails.push(
            {
              name: '公里总价',
              mileage: that.Mileage,
              val: val.mileagePrice ? val.mileagePrice.toFixed(1) : val.mileagePrice
            }
          )
          that.BudgetCostDetails.push(
            {
              name: '行驶时间',
              time: that.Time,
              val: val.travelTimePrice ? val.travelTimePrice.toFixed(1) : val.travelTimePrice
            }
          )
          that.BudgetCostDetails.push(
            {
              name: '优惠券',
              val: (val.money - that.sumFn + Number(that.orderData.price.val) + Number(that.orderData.award.val)) >= 0 ? '-' + (val.money - that.sumFn + Number(that.orderData.price.val) + Number(that.orderData.award.val)).toFixed(1) : 0
            }
          )
          if (that.orderData.price.showState && Number(that.orderData.price.val) >= 0) {
            that.BudgetCostDetails.push(
              {
                name: '商品金额',
                val: that.orderData.price.val > 0 ? Number(that.orderData.price.val).toFixed(1) : 0
              }
            )
          }
          if (that.orderData.award.showState && Number(that.orderData.award.val) >= 0) {
            that.BudgetCostDetails.push(
              {
                name: '感谢费',
                val: that.orderData.award.val > 0 ? Number(that.orderData.award.val).toFixed(1) : 0
              }
            )
          }
        }
      }
    },
    methods: {
      startAddFn: function (obj) {
        let that = this
        that.orderData.add.startAdd.val = obj.name
        that.orderData.add.startAdd.longitude.lng = obj.lng
        that.orderData.add.startAdd.longitude.lat = obj.lat
        that.orderData.add.startAdd.city = obj.city
        that.distanceFn()
      },
      endAddFn: function (obj) {
        let that = this
        that.orderData.add.endAdd.val = obj.name
        that.orderData.add.endAdd.longitude.lng = obj.lng
        that.orderData.add.endAdd.longitude.lat = obj.lat
        that.orderData.add.endAdd.city = obj.city
        that.distanceFn()
      },
      distanceFn: function () {
        let that = this
        if (that.orderData.add.startAdd.showState && that.orderData.add.startAdd.name !== '' && that.orderData.add.startAdd.name !== '获取定位失败' && that.orderData.add.startAdd.longitude.lat !== '' && that.orderData.add.startAdd.longitude.lng !== '' && that.orderData.add.endAdd.showState && that.orderData.add.endAdd.name !== '' && that.orderData.add.endAdd.name !== '获取定位失败' && that.orderData.add.endAdd.longitude.lng !== '' && that.orderData.add.endAdd.longitude.lat !== '') {
          this.$store.dispatch('countLine', {
            origin: that.orderData.add.startAdd.longitude.lat.toFixed(6) + ',' + that.orderData.add.startAdd.longitude.lng.toFixed(6),
            origin_region: that.orderData.add.startAdd.city,
            destination: that.orderData.add.endAdd.longitude.lat.toFixed(6) + ',' + that.orderData.add.endAdd.longitude.lng.toFixed(6),
            destination_region: that.orderData.add.endAdd.city,
            fn: (data) => {
              if (data.status === 0) {
                let mileage = 0
                let Time = 0
                if (data.result.routes[0]) {
                  mileage = (data.result.routes[0].distance / 1000).toFixed(1)
                  Time = (data.result.routes[0].duration / 60).toFixed(0)
                }
                that.$store.dispatch('PtBudgetCost', {
                  areaId: that.areaId,
                  time: Time,
                  mileage: mileage
                })
                that.Time = Time
                that.Mileage = mileage
              } else {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '路程规划失败'
                }
                that.Time = 1
                that.Mileage = 0
                that.$store.dispatch('PtBudgetCost', {
                  areaId: that.areaId,
                  time: 1,
                  mileage: 0
                })
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
              }
            }
          })
          return false
        }
        that.Time = 1
        that.Mileage = 0
        that.$store.dispatch('PtBudgetCost', {
          areaId: that.areaId,
          time: 1,
          mileage: 0
        })
      },
      labelsFn: function (val) { // 标签事件
        if (this.labelsData.checkState) {
          this.labelsData.data[val].actionState = !this.labelsData.data[val].actionState
          if (this.labelsData.data[val].actionState) {
            this.labelsData.actionNum.push(this.labelsData.data[val].id)
          } else {
            for (let i = 0; i < this.labelsData.actionNum.length; i++) {
              if (this.labelsData.data[val].id === this.labelsData.actionNum[i]) {
                this.labelsData.actionNum.splice(i, 1)
              }
            }
          }
        } else {
          this.labelsData.actionNum = this.labelsData.data[val].id
          for (let i = 0; i < this.labelsData.data.length; i++) {
            this.labelsData.data[i].actionState = false
          }
          this.labelsData.data[val].actionState = !this.labelsData.data[val].actionState
          if (this.labelsData.data[val].queryState && this.labelsData.data[val].queryName !== '') {
            if (this.orderData.add.startAdd.showState) {
              this.$refs.startAdd.query = this.labelsData.data[val].queryName
              this.orderData.add.startAdd.val = this.labelsData.data[val].queryName
              this.$refs.startAdd.queryShowFn()
            }
          }
        }
      },
      detaiFn: function (val) { // 常用地
        let that = this
        let add = this.orderData.add
        switch (val) {
          case 'start':
            add.startAdd.val = that.loginData.passenger.other.memberUsual
            add.startAdd.longitude = {
              lat: that.loginData.passenger.other.lng,
              lng: that.loginData.passenger.other.lat
            }
            this.$refs.startAdd.query = add.startAdd.val
            break
          case 'end':
            add.endAdd.val = that.loginData.passenger.other.memberUsual
            add.endAdd.longitude = {
              lat: that.loginData.passenger.other.lng,
              lng: that.loginData.passenger.other.lat
            }
            this.$refs.endAdd.query = add.startAdd.val
            break
        }
        this.orderData.add = add
        that.distanceFn()
      },
      priceFn: function (val) {
        if (val === 'input') {
          if (this.$refs.priceState && this.$refs.priceState.state) {
            this.$refs.priceState.checkInputAcFn()
          }
          return
        }
        this.orderData.price.val = ''
        if (val.state) {
          this.orderData.price.buzhi = true
        } else {
          this.orderData.price.buzhi = false
        }
      },
      accessorialServiceButFn: function (state, index) {
        let that = this
        let content = that.orderData.accessorialService.actionContent
        let id = this.orderData.accessorialService.content[index].id
        let states = true;
        (() => {
          for (let i = 0; i < content.length; i++) {
            if (content[i] === id) {
              that.orderData.accessorialService.actionContent.splice(i, 1)
              states = false
              return false
            }
          }
        })()
        if (states) {
          that.orderData.accessorialService.actionContent.push(id)
        }
      },
      couponSlelectFn: function (val) {
        let that = this
        if (val.type === 'action') {
          that.couponData = val.obj
          that.distanceFn()
        }
        if (val.type === 'nonuse') {
          that.couponData = val.obj
          that.distanceFn()
        }
      },
      inputFn: function (e, type) {
        let that = this
        let val = e.target.value
        // console.log(type)
        let orderData = that.orderData
        switch (type) {
          case 'demand': // 需求
            orderData.demand.val = val
            break
          case 'detailStartAdd': // 详细起点
            orderData.add.detailStartAdd.val = val
            break
          case 'detailEndAdd': // 详细终点
            orderData.add.detailEndAdd.val = val
            break
          case 'deliver': // 联系电话
            orderData.contact.deliver.phoneNo = val.replace(/[^0-9]/ig, '')
            break
          case 'take': // 收件人 电话
            orderData.contact.take.phoneNo = val.replace(/[^0-9]/ig, '')
            break
          case 'weight': // 重量
            orderData.weight.val = val.replace(/[^0-9]/ig, '')
            break
          case 'matter': // 事项
            orderData.matter.val = val
            break
          case 'price': // 价格
            orderData.price.val = val.replace(/[^0-9]/ig, '')
            that.distanceFn()
            break
          case 'award': // 小费
            orderData.award.val = val.replace(/[^0-9]/ig, '')
            that.distanceFn()
            break
        }
      },
      DatePickerFn: function (time) { // 时间选择
        // console.log(time)
        if (time) {
          this.orderData.appointTime.val = time.name
          this.orderData.appointTime.showVal = time.val
        }
        this.datePickerState = false
      },
      selectTimeFn: function () { // 时间选择
        this.datePickerState = true
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'paotui', state: false})
      },
      placeAnOrderFn: function () {
        let that = this
        let orderData = that.orderData
        try {
          for (let key in orderData) {
            switch (key) {
              case 'demand':
                if (orderData[key].isRequire && orderData[key].val === '') {
                  // console.log('需求')
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
              case 'add':
                for (let keyS in orderData.add) {
                  switch (keyS) {
                    case 'startAdd':
                      if (orderData.add[keyS].isRequire && orderData.add[keyS].val === '' && orderData.add[keyS].longitude.lng === '' && orderData.add[keyS].longitude.lat === '') {
                        // console.log(orderData[key])
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.add[keyS].name + '不能为空'
                        }
                        return false
                      }
                      break
                    case 'endAdd':
                      if (orderData.add[keyS].isRequire && orderData.add[keyS].val === '' && orderData.add[keyS].longitude.lng === '' && orderData.add[keyS].longitude.lat === '') {
                        // console.log('终点')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.add[keyS].name + '不能为空'
                        }
                        return false
                      }
                      break
                    case 'detailStartAdd':
                      if (orderData.add[keyS].isRequire && orderData.add[keyS].val === '') {
                        // console.log('详细起点')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.add[keyS].name + '不能为空'
                        }
                        return false
                      }
                      break
                    case 'detailEndAdd':
                      if (orderData.add[keyS].isRequire && orderData.add[keyS].val === '') {
                        // console.log('详细终点')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.add[keyS].name + '不能为空'
                        }
                        return false
                      }
                      break
                  }
                }
                break
              case 'contact':
                for (let keyS in orderData.contact) {
                  switch (keyS) {
                    case 'deliver':
                      if (orderData.contact[keyS].isRequire && orderData.contact[keyS].phoneNo === '') {
                        // console.log('联系人')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.contact[keyS].name + '不能为空'
                        }
                        return false
                      }
                      if (orderData.contact[keyS].isRequire && !Toucth.checkPhone(orderData.contact[keyS].phoneNo)) {
                        // console.log('联系人')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.contact[keyS].name + '填写错误'
                        }
                        return false
                      }
                      break
                    case 'take':
                      if (orderData.contact[keyS].isRequire && orderData.contact[keyS].phoneNo === '') {
                        // console.log('收货人')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.contact[keyS].name + '不能为空'
                        }
                        return false
                      }
                      if (orderData.contact[keyS].isRequire && !Toucth.checkPhone(orderData.contact[keyS].phoneNo)) {
                        // console.log('收货人')
                        that.hintBox = {
                          stateShow: true,
                          state: 'no',
                          content: orderData.contact[keyS].name + '填写错误'
                        }
                        return false
                      }
                      break
                  }
                }
                break
              case 'weight':
                if (orderData[key].isRequire && orderData[key].val === '') {
                  // console.log('重量')
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
              case 'appointTime':
                if (orderData[key].isRequire && orderData[key].val === '') {
                  // console.log('时间')
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
              case 'price':
                if (orderData[key].isRequire && (orderData[key].val === '' && !orderData[key].buzhi)) {
                  // console.log('价格')
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
              case 'award':
                if (orderData[key].isRequire && orderData[key].val === '') {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
              case 'upImg':
                if (orderData[key].isRequire && orderData[key].urls.length < 1) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: orderData[key].name + '不能为空'
                  }
                  return false
                }
                break
            }
          }
          if (!that.PtBudgetCost) {
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '预算错误，请重新选择地址'
            }
            return false
          }
          that.loadState = true
          that.$store.dispatch('PtPlaceAnOrder', {
            'passengerId': that.loginData.passenger.id,
            'passengerName': that.loginData.passenger.name,
            'passengerPhone': that.loginData.passenger.phone,
            'companyName': that.companyInfo.companyName,
            'companyId': that.companyInfo.companyId,
            'channelName': '微信预约',
            'startAddress': orderData.add.startAdd.val,
            'startLat': orderData.add.startAdd.longitude.lat,
            'startLng': orderData.add.startAdd.longitude.lng,
            'deliverAddress': orderData.add.endAdd.val,
            'deliverLat': orderData.add.endAdd.longitude.lat,
            'deliverLng': orderData.add.endAdd.longitude.lng,
            'startTime': orderData.appointTime.showVal,
            'startPrice': that.PtBudgetCost.money,
            'mileagePrice': '',
            'travelTimePrice': '',
            'mileage': '',
            'travelTime': '',
            'shouldPay': that.PtBudgetCost.money,
            'couponId': that.couponData ? that.couponData.id : '',
            'photos': orderData.upImg.urls,
            'orderTypeId': Toucth.AcquireUrlParameter().id,
            'content': orderData.matter.val,
            'recipients': '',
            'phone': orderData.contact.take.phoneNo,
            'weight': orderData.weight.val,
            'buyNeeds': orderData.demand.val,
            'goodsCash': orderData.price.val,
            'thanksCash': orderData.award.val,
            'addServices': orderData.accessorialService.actionContent.join(','),
            'tips': (typeof that.labelsData.actionNum) === 'number' || (typeof that.labelsData.actionNum) === 'string' ? that.labelsData.actionNum : that.labelsData.actionNum.join(','),
            'startDetailed': orderData.add.detailStartAdd.val,
            'endDetailed': orderData.add.detailEndAdd.val,
            fn: (data) => {
              that.loadState = false
              if (data.code === 0) {
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '下单成功！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/paotui/await', query: {orderId: data.data.orderId}})
                }, 1200)
              } else {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: data.message
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                  if (data.code === -86) {
                    if (that.unfilledOrder && that.unfilledOrder.paotui.length > 0) {
                      that.$store.dispatch('unfilledOrderState', {type: 'paotui', state: true})
                      that.loadState = false
                      return false
                    }
                  }
                }, 1500)
              }
            }
          })
        } catch (val) {
          // console.log(val)
        }
      },
      upImgFn: function (val) {
        this.orderData.upImg.urls = val.join(',')
      },
      hintFn: function () {
        this.hintBox.stateShow = false
      },
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      }
    },
    mounted: function () {
      let that = this
      that.loadState = true

      function infoData () {
        let services = that.infoData.services.paotui.services
        let id = Toucth.AcquireUrlParameter().id
        for (let i = 0; i < services.length; i++) {
          if (services[i].id === Number(id)) {
            that.labelsData.data = []
            that.orderData.accessorialService.content = []
            that.orderData.accessorialService.actionContent = []
            that.areaId = services[i].areaId
            for (let y = 0; y < services[i].typeServise.length; y++) {
              let typeServise = services[i].typeServise[y]
              that.orderData.accessorialService.content.push({
                state: false,
                name: typeServise.otherServices,
                id: typeServise.id
              })
            }
            that.labelsData.checkState = services[i].allowTips === 1
            for (let j = 0; j < services[i].typeTips.length; j++) {
              let typeTips = services[i].typeTips[j]
              that.labelsData.data.push({
                imgUrL: '//' + typeTips.tipspicture,
                name: typeTips.tipName,
                queryName: typeTips.tipQueryName,
                queryState: Number(typeTips.ifCheck) === 1,
                actionState: false,
                id: typeTips.id
              })
            }
            for (let k = 0; k < services[i].typeDetailed.length; k++) {
              let typeDetailed = services[i].typeDetailed[k]
              switch (services[i].typeDetailed[k].seq) {
                case 1:
                  that.orderData.demand = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '购买需求',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: '',
                    id: typeDetailed.id
                  }
                  break
                case 2:
                  let time = new Date().valueOf()
                  that.orderData.appointTime = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    state: true,
                    name: typeDetailed.name ? typeDetailed.name : '预约时间',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: '现在',
                    showVal: time,
                    id: typeDetailed.id
                  }
                  break
                case 3:
                  that.orderData.add.startAdd.showState = typeDetailed.isShow === 1
                  that.orderData.add.startAdd.isRequire = typeDetailed.isRequire === 1
                  that.orderData.add.startAdd.name = typeDetailed.name ? typeDetailed.name : '起点'
                  that.orderData.add.startAdd.placeholder = typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt
                  that.orderData.add.startAdd.id = typeDetailed.id
                  break
                case 4:
                  that.orderData.add.detailStartAdd = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '详细起点',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: '',
                    longitude: {
                      lng: '',
                      lat: ''
                    },
                    id: typeDetailed.id
                  }
                  break
                case 5:
                  that.orderData.add.endAdd.showState = typeDetailed.isShow === 1
                  that.orderData.add.endAdd.isRequire = typeDetailed.isRequire === 1
                  that.orderData.add.endAdd.name = typeDetailed.name ? typeDetailed.name : '终点'
                  that.orderData.add.endAdd.placeholder = typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt
                  that.orderData.add.endAdd.id = typeDetailed.id
                  break
                case 6:
                  that.orderData.add.detailEndAdd = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '详细终点',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: '',
                    longitude: {
                      lng: '',
                      lat: ''
                    },
                    id: typeDetailed.id
                  }
                  break
                case 7:
                  that.orderData.contact.deliver = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '发件人电话',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    phoneNo: that.loginData && that.loginData.passenger ? that.loginData.passenger.phone : ''
                  }
                  break
                case 8:
                  that.orderData.contact.take = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '收件人电话',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    phoneNo: ''
                  }
                  break
                case 9:
                  that.orderData.weight = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '重量',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: ''
                  }
                  break
                case 10:
                  that.orderData.price = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    state: true,
                    buzhi: false,
                    name: typeDetailed.name ? typeDetailed.name : '商品金额',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: ''
                  }
                  break
                case 11:
                  that.orderData.award = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '感谢费',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: ''
                  }
                  break
                case 12:
                  that.orderData.upImg = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    state: true,
                    name: typeDetailed.name ? typeDetailed.name : '上传图片',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    urls: []
                  }
                  break
                case 13:
                  that.orderData.matter = {
                    showState: typeDetailed.isShow === 1,
                    isRequire: typeDetailed.isRequire === 1,
                    name: typeDetailed.name ? typeDetailed.name : '备注',
                    placeholder: typeDetailed.isRequire === 1 ? '(必填)' + typeDetailed.prompt : typeDetailed.prompt,
                    val: ''
                  }
                  break
                case 14:
                  that.orderData.accessorialService.showState = typeDetailed.isShow === 1
                  that.orderData.accessorialService.isRequire = typeDetailed.isRequire === 1
                  break
              }
            }
          }
        }
        setTimeout(function () {
          if (that.positioning && that.positioning.name && that.positioning.lng && that.positioning.lat) {
            if (that.orderData.add.startAdd.showState) {
              that.orderData.add.startAdd.val = that.positioning.name
              that.orderData.add.startAdd.longitude.lng = that.positioning.lng
              that.orderData.add.startAdd.longitude.lat = that.positioning.lat
              that.orderData.add.startAdd.city = that.positioning.city
              that.distanceFn()
              return true
            }
            if (that.orderData.add.endAdd.showState) {
              that.orderData.add.endAdd.val = that.positioning.name
              that.orderData.add.endAdd.longitude.lng = that.positioning.lng
              that.orderData.add.endAdd.longitude.lat = that.positioning.lat
              that.orderData.add.endAdd.city = that.positioning.city
              that.distanceFn()
              return true
            }
          } else {
            if (that.orderData.add.startAdd.showState) {
              that.orderData.add.startAdd.val = '定位失败，请手动选择地址'
              that.orderData.add.startAdd.longitude.lng = ''
              that.orderData.add.startAdd.longitude.lat = ''
              that.orderData.add.startAdd.city = '选择城市'
              return true
            }
            if (that.orderData.add.endAdd.showState) {
              that.orderData.add.endAdd.val = '定位失败，请手动选择地址'
              that.orderData.add.endAdd.longitude.lng = ''
              that.orderData.add.endAdd.longitude.lat = ''
              that.orderData.add.endAdd.city = '选择城市'
              return true
            }
            that.distanceFn()
          }
        }, 100)
        that.loadState = false
      }

      function infoDataSetFn () {
        setTimeout(function () {
          if (that.infoData) {
            infoData()
          } else {
            infoDataSetFn()
          }
        }, 1000)
      }

      that.$store.dispatch('checkLogin', (state) => {
        if (state && that.loginData && that.loginData.passenger) {
          that.$store.dispatch('unfilledOrder', {passengerId: that.loginData.passenger.id})
          that.$store.dispatch('PtCoupon', {
            passengerId: that.loginData.passenger.id,
            couponTypeId: Toucth.TimestampDate().id
          })
          infoDataSetFn()
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .downOrder {
    width: 100%;
    .content {
      width: 100%;
      padding-bottom: 1rem;
      .labels {
        box-sizing: border-box;
        width: 100%;
        background-color: #fff;
        padding: 0.5rem;
