            input {
              font-size: 1rem !important;
            }
            float: left;
          }
          .detai {
            box-sizing: border-box;
            width: 7.5rem;
            text-align: center;
            float: right;
            position: relative;
            z-index: 5;
          }
          .detai:after {
            content: '';
            display: inline-block;
            width: 1px;
            height: 50%;
            position: absolute;
            background-color: #eee;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
