              width: 100%;
              font-size: 1.2rem;
              border: none;
            }
          }
        }
        .row:nth-last-child(1) {
          border: none;
        }
        .row:nth-child(1) {
          margin: 0;
        }
      }
      .page1 {
        .row {
          margin-top: -3px;
          .title {
          }
          .val {
            width: calc(~'100% - 10rem');
            input {
              width: 100%;
              font-size: 1.2rem;
              border: none;
            }
          }
        }
        .row:nth-last-child(1) {
          border: none;
        }
        .row:nth-child(1) {
          margin: 0;
        }
      }
      .page2 {
