            }
          }
        }
        .row:nth-last-child(1) {
          border: none;
        }
        .row:nth-child(1) {
          margin: 0;
        }
        .price {
          .val {
            width: calc(~'100% - 13rem');
          }
          .checkInput {
            padding-left: 0.6rem;
          }
        }
      }
      .Sum {
        text-align: center;
        margin-top: 2rem;
        i {
          float: none;
          color: #999;
          font-size: 0.8333rem;
        }
        p {
          color: #3f3f3f;
          font-size: 0.833rem;
