/** 服务中***/
<template>
  <div class="PTserver">
    <div class="PTserverContent">
      <div class="describe">
        <div class="describeContent">
          <h2>{{title}}</h2>
          <h3>{{titleCont}}</h3>
        </div>
      </div>
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="PtQueryOrder?PtQueryOrder.employName:''"
                 :paotuiType="PtQueryOrder?PtQueryOrder.orderType:''"
                 :phone="PtQueryOrder?PtQueryOrder.employPhone:''"
                 :headImg="PtQueryOrder?PtQueryOrder.employPhoto:''"
      >
      </xk-waiter>
      <div class="orderInform">
        <div class="row time" v-if="statrTimeFn">
          <i class="iconfont icon-shijian"></i>
          <span class="val"><p>{{PtQueryOrder ? statrTimeFn : ''}}</p></span>
        </div>
        <div class="row startAdd"
             v-if="PtQueryOrder && (PtQueryOrder.startAddress!=='' || PtQueryOrder.startDetailed!=='')">
          <div class="top" v-if="PtQueryOrder&&PtQueryOrder.startAddress!==''">
            <i class="iconfont icon-yuan"></i>
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.startAddress : ''}}</p></span>
          </div>
          <div class="bottom" v-if="PtQueryOrder&&PtQueryOrder.startDetailed!==''">
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.startDetailed : ''}}</p></span>
          </div>
        </div>
        <div class="row endAdd" v-if="PtQueryOrder&& (PtQueryOrder.endAddress!==''||PtQueryOrder.endDetailed!=='')">
          <div class="top" v-if="PtQueryOrder&&PtQueryOrder.endAddress!==''">
            <i class="iconfont icon-yuan"></i>
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.endAddress : ''}}</p></span>
          </div>
          <div class="bottom" v-if="PtQueryOrder&&PtQueryOrder.endDetailed!==''">
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.endDetailed : ''}}</p></span>
          </div>
        </div>
        <div class="row more" v-if="PtQueryOrder&&(PtQueryOrder.buyNeeds!==''||PtQueryOrder.shouldPay!=='')">
          <div class="moreContent">
            <div class="list" v-if="PtQueryOrder&&PtQueryOrder.buyNeeds!==''">
              <h3>购买需求</h3>
              <p>{{PtQueryOrder ? PtQueryOrder.buyNeeds : ''}}</p>
            </div>
            <div class="allSum" v-if="PtQueryOrder && PtQueryOrder.shouldPay!==''">
              <p>订单总金额约<span
                class="Sum">{{PtQueryOrder ? PtQueryOrder.shouldPay + PtQueryOrder.goodsCash + PtQueryOrder.thanksCash : ''}}</span>元
              </p>
            </div>
          </div>
        </div>
        <div class="row money" :class="moneyState?'moneyShow':''"
             v-if="moreInfoFn&&(moreInfoFn[0].sum||moreInfoFn[1].sum)">
          <span class="moneyBut" @click="moneyFn"></span>
          <div class="moreContent">
            <div class="list" v-if="item.sum" v-for="item in moreInfoFn" :class="item.point?'point':''">
              <p>{{item.name}}<span class="sum">{{item.sum}}</span>元</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import Toucth from '../../../assets/js/method'
  import { mapGetters } from 'vuex'
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod,
      XkWaiter
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        PtQueryOrder: 'PtQueryOrder'
      }),
      statrTimeFn: function () {
        if (this.PtQueryOrder && this.PtQueryOrder.startTime) {
          return Toucth.TimestampDate(this.PtQueryOrder.startTime)
        }
      },
      moreInfoFn: function () {
        let data = null
        if (this.PtQueryOrder) {
          data = [
            {
              name: '服务费',
              sum: this.PtQueryOrder.shouldPay > 0 ? this.PtQueryOrder.shouldPay.toFixed(1) : null,
              point: true
            },
            {
              name: '商品价格',
              sum: this.PtQueryOrder.goodsCash > 0 ? this.PtQueryOrder.goodsCash.toFixed(1) : null,
              point: true
            },
            {
              name: '打赏费',
              sum: this.PtQueryOrder.thanksCash > 0 ? this.PtQueryOrder.thanksCash.toFixed(1) : null,
              point: true
            }
          ]
          return data
        }
      }
    },
    data () {
      return {
        orderData: {
          type: '帮我买',
          serverNanm: '王师傅',
          time: '2017-06-08 18:18',
          startAdd: {
            add: '科林路小咖科技',
            detailAdd: '中央花园三期20号炸鸡店',
            longitude: {
              lng: '',
              lat: ''
            }
          },
          endAdd: {
            add: '小咖科技',
            detailAdd: '中央花园三期19号炸鸡店',
            longitude: {
              lng: '',
              lat: ''
            }
          },
          demand: '买份炸鸡腿 多加辣椒',
          allSunm: '50.50',
          moreInfo: [
            {
              name: '商品价格',
              sum: '40.50',
              point: true
            },
            {
              name: '打赏费',
              sum: '30.50',
              point: true
            }
          ]
        },
        moneyState: false,
        title: '',
        titleCont: '',
        titleNum: 0,
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    watch: {
      PtQueryOrder (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/paotui')
          return
        }
        that.loadState = false
        if (val.status === 2) {
          that.title = '服务人员已接单'
          that.titleCont = '正在向您狂奔...'
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/paotui/server') {
              that.$store.dispatch('PtQueryOrder', {
                orderId: Toucth.AcquireUrlParameter().orderId
              })
            }
          }, 3000)
        }
        if (val.status === 3) {
          if (val.treatment) {
            that.$router.push({path: '/paotui/settlement', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
          } else {
            that.title = '任务执行中'
            switch (that.titleNum) {
              case 0:
                that.titleCont = '服务人员正在战场前线奋斗中 ...'
                break
              case 1:
                that.titleCont = '为了更好的完成您的任务 ...'
                break
              case 2:
                that.titleCont = '请您耐心等待服务人员凯旋归来 ...'
                break
              case 3:
                that.titleCont = '如有需求点击电话呼叫服务人员 ...'
                break
            }
            setTimeout(function () {
              if (that.$route.fullPath.split('?')[0] === '/paotui/server') {
                that.titleNum++
                if (that.titleNum > 3) {
                  that.titleNum = 0
                }
                that.$store.dispatch('PtQueryOrder', {
                  orderId: Toucth.AcquireUrlParameter().orderId
                })
              }
            }, 3000)
          }
        }
        if (val.status === 4) {
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/paotui/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
          }, 1500)
        }
        if (val.status === 5) {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/paotui'})
          }, 1500)
        }
      }
    },
    methods: {
      moneyFn: function () {
        this.moneyState = !this.moneyState
        if (this.moneyState) {
          document.body.scrollTop = document.body.scrollHeight
        } else {
          document.body.scrollTop = 0
        }
      }
    },
    mounted: function () {
      let that = this
      that.loadState = true
      that.$store.dispatch('checkLogin', (state) => {
        if (state && that.loginData && that.loginData.passenger) {
          that.$store.dispatch('PtQueryOrder', {
            orderId: Toucth.AcquireUrlParameter().orderId
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .PTserver {
    .PTserverContent {
      .describe {
        height: 10rem;
        padding-top: 1rem;
        background-color: #fff;
        position: relative;
        .describeContent {
          width: 100%;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
        h2 {
          font-size: 2rem;
          color: #333;
          text-align: center;
          margin-top: 1rem;
        }
        h3 {
          color: #999999;
          font-size: 1.25rem;
          text-align: center;
          margin-top: 1.4rem;
        }
      }
      .orderInform {
        margin-top: 1rem;
        padding: 0 1rem;
        padding-bottom: 1rem;
        margin-bottom: 3rem;
        background-color: #fff;
        .row {
          line-height: 5rem;
          border-bottom: 1px solid #eee;
          padding: 0 1rem;
          .iconfont {
            margin-right: 0.1rem;
          }
          .val {
            font-size: 1.3rem;
            color: #333333;
          }
        }
        .row:nth-last-child(1) {
          border-bottom: none;
        }
        .time {
          .iconfont {
            color: #fab10a;
          }
        }
        .startAdd, .endAdd {
          padding: 0 1rem;
          line-height: 2rem;
          .bottom {
            .val {
              font-size: 1rem;
              padding-left: 1.6rem;
              color: #999999;
            }
          }
        }
        .startAdd {
          padding-top: 1.2rem;
          padding-bottom: 0.3rem;
          border-bottom: none;
          .iconfont {
            color: #3cafb9;
          }

        }
        .endAdd {
          padding-bottom: 1.8rem;
          padding-top: 0.3rem;
          .iconfont {
            color: #f06553;
          }
        }
        .more {
          border-top: 1px solid #eee;
          position: relative;
          height: auto;
          transition: 0.3s all;
          padding-bottom: 1.8rem;
          margin-top: 0.5rem;
          .moreBut {
            display: inline-block;
            position: absolute;
            left: 50%;
            top: -1.7rem;
            transform-origin: 50% 50%;
            transform: translate(-50%, 0) scaleY(1);
            width: 3.5rem;
            height: 3.5rem;
            background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
            background-size: 2rem 2rem;
            transition: 0.3s all;
            border-radius: 50%;
            overflow: hidden;
          }
          .moreContent {
            height: auto;
            line-height: 2.5rem;
            padding: 1rem 0;
            padding-top: 1.8rem;
            text-align: center;
            .list {
              h3 {
                font-size: 1.25rem;
                color: #333;
              }
              p {
                font-size: 1rem;
                line-height: 1.3rem;
                color: #999999;
              }
            }
            .allSum {
              text-align: center;

            }
          }
        }
        .money {
          position: relative;
          height: auto;
          margin-top: -1px;
          transition: 0.3s all;
          .moreContent {
            overflow: hidden;
            line-height: 1.9rem;
            height: 0;
            text-align: center;
            .list {
              line-height: 1.666rem;
              color: #999999;
              font-size: 1rem;
            }
            .point {
              line-height: 2.3rem;
              font-size: 1rem;
              color: #333;
              .sum {
                color: #f06553;
                font-size: 1.5rem;
                margin: 0 0.3rem;
              }
            }
            .list:nth-last-child(1) {
              padding-bottom: 1.5rem;
            }
          }
          .moneyBut {
            display: inline-block;
            position: absolute;
            left: 50%;
            top: -1.7rem;
            transform-origin: 50% 50%;
            transform: translate(-50%, 0) scaleY(1);
            width: 3.5rem;
            height: 3.5rem;
            background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
            background-size: 2rem 2rem;
            transition: 0.3s all;
            border-radius: 50%;
            overflow: hidden;
          }
        }

        .moneyShow {
          .moneyBut {
            transform: translate(-50%, 0) scaleY(-1);
          }
          .moreContent {
            height: auto;
            padding-top: 2rem;
          }
        }
      }
    }
  }
</style>



