import huoyun from '../../../api/huoyun/huoyun'
import Toucth from '../../../assets/js/method'
// import data from '../../configure'

const state = {
  queryCarType: null,
  couponList: [],
  budgetCost: null,
  orderInfo: null,
  payInfo: null,
  AllOrder: {
    state: true,
    total: true,
    val: []
  },
  queryReview: null,
  unfilledOrder: null
}

const mutations = {
  HyQueryCarType (state, obj) {
    state.queryCarType = []
    state.queryCarType = obj
  },
  HyBudgetCost (state, obj) {
    state.budgetCost = obj
  },
  HyCoupon (state, data) {
    if (data && data.length > 0) {
      var obj = []
      for (let i = 0; i < data.length > 0; i++) {
        obj.push({
          id: data[i].couponId,
          couponRule: {
            discount: data[i].couponDiscount,
            id: data[i].couponId,
            start: data[i].startTime,
            money: data[i].couponMoney,
            end: data[i].endTime ? Toucth.TimestampDate(data[i].endTime) : null,
            endTime: data[i].endTime ? Toucth.TimestampDate(data[i].endTime) : null,
            title: data[i].couponTitle
          }
        })
      }
      state.couponList = obj
    }
  },
  HyOrderInfo (state, data) {
    state.orderInfo = data
  },
  HyPayInfo (satte, data) {
    satte.payInfo = data
  },
  HyQueryReview (state, data) {
    if (data && data.data && data.data.content !== '') {
      let tabs = data.data.content.substring(0, data.data.content.lastIndexOf('&*&'))
      tabs = tabs !== '' ? tabs.split('&|&') : []
      state.queryReview = {
        reviewContent: data.data.content.substring(data.data.content.lastIndexOf('&*&')).replace('&*&', ''),
        tabs: tabs.length > 0 ? tabs : null,
        score: data.data.score
      }
    }
  },
  HyQueryAllOrder (state, obj) {
    let type = obj.type
    let data = obj.data
    if (data) {
      state.AllOrder.total = data.total
      state.AllOrder.state = false
      if (type === 'upData') {
        state.AllOrder.val = []
      }
      for (let i = 0; i < data.rows.length; i++) {
        state.AllOrder.val.push(data.rows[i])
      }
    }
  },
  HyUnfilledOrder (state, data) {
    state.unfilledOrder = data
  }
}

const getters = {
  HyQueryCarType: state => {
    return state.queryCarType
  },
  HyBudgetCost: state => {
    return state.budgetCost
  },
  HyCoupon: state => {
    return state.couponList
  },
  HyOrderInfo: state => {
    return state.orderInfo
  },
  HyPayInfo: satte => {
    return satte.payInfo
  },
  HyQueryReview: state => {
    return state.queryReview
  },
  HyQueryAllOrder: state => {
    return state.AllOrder
  },
  HyUnfilledOrder: state => {
    return state.unfilledOrder
  }
}

const actions = {
  HyQueryCarType ({commit}, obj) {
    huoyun.queryCarType(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('HyQueryCarType', data.data)
    })
  },
  HyBudgetCost ({commit}, obj) {
    huoyun.budgetCost(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('HyBudgetCost', data.data)
    })
  },
  HyPlaceAnOrder ({commit}, obj) {
    huoyun.placeAnOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  HyCoupon ({commit}, obj) {
    huoyun.coupon(obj, (data) => {
      commit('HyCoupon', data.data)
    })
  },
  HyOrderInfo ({commit}, obj) {
    huoyun.queryOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('HyOrderInfo', data.data)
    })
  },
  HyCancelOrder ({commit}, obj) {
    huoyun.cancelOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  HyPayInfo ({commit}, obj) {
    huoyun.payInfo(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('HyPayInfo', data.data)
    })
  },
  HyWXPay ({commit}, obj) {
    huoyun.weixinPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  HyBalancePay ({commit}, obj) {
    huoyun.balancePay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  HyEvaluateOrder ({commit}, obj) {
    huoyun.evaluateOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  HyQueryReview ({commit}, obj) {
    huoyun.queryReview(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('HyQueryReview', data)
    })
  },
  HyQueryAllOrder ({commit}, obj) {
    huoyun.allOrder(obj, (data) => {
      if (data.code === 0 && obj.fn) {
        obj.fn(true)
      }
      commit('HyQueryAllOrder', {data: data.data, type: obj.type})
    })
  },
  HyUnfilledOrder ({commit}, obj) {
    huoyun.unfilledOrder(obj, (data) => {
      commit('HyUnfilledOrder', data.data)
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



