/***               ***/
<template>
  <div class="HuoYunOrderInfo">
    <div class="content">
      <div class="OrderForm">
        <div class="row clear time" @click="selectTimeFn">
          <i class="iconfont icon-shijian"></i>
          <div class="val">
            <span>{{startTime.name}}</span>
          </div>
        </div>
        <div class="row clear startAdd">
          <i class="iconfont icon-yuan"></i>
          <div class="val" :class="startAdd.name===''?'weiVal':''" @click="startAddBoxFn">
            {{startAdd.name !== '' ? startAdd.name : '起点地址'}}
          </div>
          <div class="line"></div>
        </div>
        <div class="passAddrList" v-if="passAddrList.length>0">
          <div class="row clear passAddr" v-for="(item,index) in passAddrList" :key="index">
            <i class="iconfont icon-yuan1"></i>
            <div class="val" @click="passAddrAlterFn(index)">
              <span class="showAddr" v-if="item.address!==''">{{item.address}}</span>
              <span class="placeholder" v-else="">选择途径地</span>
            </div>
            <span class="removePassAddrBut" @click="removePassAddrFn(index)">
            <i class="iconfont icon-jian1"></i>
            </span>
            <div class="line"></div>
          </div>
        </div>
        <div class="row clear endAdd">
          <i class="iconfont icon-yuan"></i>
          <div class="val" :class="endAdd.name===''?'weiVal':''" @click="endAddBoxFn">
            {{endAdd.name !== '' ? endAdd.name : '终点地址'}}
          </div>
          <span class="pushPassAddrBut" @click="pushPassAddrFn" v-if="passAddrList.length<10">
            <i class="iconfont icon-jia1"></i>
          </span>
        </div>
      </div>
      <div class="additionalServer clear">
        <div class="title clear">
          <i class="iconfont icon-fuwu"></i>
          <h2>额外服务</h2>
        </div>
        <div class="Cont clear">
          <div class="radioBox flitting" :class="flitting?'radioBoxAc':''" @click="additionalServerFn('flitting')">
            <div class="Icon">
              <i class="iconfont icon-chenggong"></i>
            </div>
            <div class="val">
              <h2>需要搬运</h2>
              <p>(与司机议价)</p>
            </div>
          </div>
          <div class="radioBox receipt" :class="receipt?'radioBoxAc':''" @click="additionalServerFn('receipt')">
            <div class="Icon">
              <i class="iconfont icon-chenggong"></i>
            </div>
            <div class="val">
              <h2>需要回单</h2>
              <p></p>
            </div>
          </div>
          <div class="radioBox receivedPay" :class="receivedPay?'radioBoxAc':''"
               @click="additionalServerFn('receivedPay')">
            <div class="Icon">
              <i class="iconfont icon-chenggong"></i>
            </div>
            <div class="val">
              <h2>需要回款</h2>
              <p v-if="receivedPay">金额:{{payMount.val !== '' ? payMount.val : 0}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="remark clear" @click="remarkInputFn">
        <i class="iconfont icon-bianji"></i>
        <div class="val">
          {{remark.content ? remark.content : '给司机捎句话'}}
        </div>
      </div>
      <div class="bottom">
        <div class="row clear">
          <div class="left" @click="detailsBoxFn">
            约<span>{{HyBudgetCost && BudgetCostFn ? BudgetCostFn : 0}}</span>元
            <i class="iconfont icon-iconfontyiwen"></i>
          </div>
          <div class="line"></div>
          <div class="right">
            <xk-coupon-select
              @click="couponSlelectFn"
              :type="'huoyun'"
              :data="HyCoupon"></xk-coupon-select>
          </div>
        </div>
        <div class="submitOrderBut" @click="submitOrderFn">
          立即下单
        </div>
      </div>
    </div>
    <xk-date-picker-mod @click="DatePickerFn" ref="DatePicker" v-if="datePickerState"></xk-date-picker-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
    <div class="inputBoxSite">
      <xk-site-input :placeholder="placeholder"
                     :name="passAddrAlter.content.address"
                     :state="true"
                     ref="siteInput"
                     :city="passAddrAlter.content.city"
                     @onClick="passAddrAlterAddFn">
      </xk-site-input>
    </div>
    <xk-input-box v-if="passAddrAlter.state" title="添加途径点" @click="passAddrAlterFn" :closeState="true">
      <div slot="content" class="HYAdd">
        <div class="row">
          <i class="iconfont icon-yuan"></i>
          <div class="val" @click="actionTJFn">
            {{passAddrAlter.content.address !== '' ? passAddrAlter.content.address : '请选择途径地'}}
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-man"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.contacts" maxlength="10" placeholder="请输入联系人姓名"/>
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-mobile"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.phone" type="tel" placeholder="请输入联系人电话(必填)"/>
          </div>
        </div>
      </div>
    </xk-input-box>
    <xk-input-box v-if="startAdd.state" title="添加起点" @click="startAddBoxFn" :closeState="true">
      <div slot="content" class="HYAdd">
        <div class="row">
          <i class="iconfont icon-yuan"></i>
          <div class="val" @click="actionTJFn">
            {{passAddrAlter.content.address !== '' ? passAddrAlter.content.address : '请选择终点地'}}
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-man"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.contacts" maxlength="10" placeholder="请输入联系人姓名"/>
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-mobile"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.phone" type="tel" placeholder="请输入联系人电话"/>
          </div>
        </div>
      </div>
    </xk-input-box>
    <xk-input-box v-if="endAdd.state" title="添加终点" @click="endAddBoxFn" :closeState="true">
      <div slot="content" class="HYAdd">
        <div class="row">
          <i class="iconfont icon-yuan"></i>
          <div class="val" @click="actionTJFn">
            {{passAddrAlter.content.address !== '' ? passAddrAlter.content.address : '请选择终点地'}}
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-man"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.contacts" maxlength="10" placeholder="请输入联系人姓名"/>
          </div>
        </div>
        <div class="row">
          <i class="iconfont icon-mobile"></i>
          <div class="val">
            <input v-model="passAddrAlter.content.phone" type="tel" placeholder="请输入联系人电话(必填)"/>
          </div>
        </div>
      </div>
    </xk-input-box>
    <xk-input-box v-if="remark.state" title="给司机捎句话" @click="remarkInputFn" :closeState="true">
      <div slot="content" class="remarkInput">
        <textarea v-model="remark.content" maxlength="200" placeholder="给司机捎句话(最多200字)"></textarea>
      </div>
    </xk-input-box>
    <xk-input-box v-if="receivedPayBox" title="回款金额" @click="receivedPayBoxFn" :closeState="true">
      <div slot="content" class="receivedPayBox">
        <input v-model="payMount.show" maxlength="6" @input="inputFn($event,'payMount')" type="tel"
               placeholder="回款金额不能超过10万"/>
      </div>
    </xk-input-box>
    <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <div class="List">
          <ul>
            <li v-for="item in BudgetCostDetails" v-if="item.val !== 0" class="clear">
              <span class="name">{{item.name}}</span>
              <span class="val"><span class="sum">{{item.val}}</span><span class="unit">元</span></span>
              <span class="time" v-if="item.time">({{item.time}}<span class="unit">分钟)</span></span>
              <span class="time" v-if="item.mileage">({{item.mileage}}<span class="unit">公里)</span></span>
            </li>
          </ul>
        </div>
        <div class="introduce">
          {{HyBudgetCost && HyBudgetCost.pay_detail ? HyBudgetCost.pay_detail : ''}}
        </div>
      </div>
    </Xk-detailsBox>
    <xk-upload-mod :state="upLoadSate"></xk-upload-mod>
    <xk-unfilled-order-mod
      v-if="unfilledOrderState && unfilledOrderState.huoyun &&  unfilledOrderFn.length>0"
      :data="unfilledOrderFn?unfilledOrderFn:null"
      :title="'货运'"
      @close="unfilledOrdercCloseFn"
    ></xk-unfilled-order-mod>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import XkCouponSelect from '../../module/couponSelect/src/couponSelect.vue'
  import XkDatePickerMod from '../../module/datePickerMod/src/datePickerMod.vue'
  import XkSiteInput from '../../module/siteInput/src/siteInput.vue'
  import XkHintBox from '../../module/hintBox/src/hintBox.vue'
  import XkInputBox from '../../module/inputBox/src/inputBox.vue'
  import Toucth from '../../../assets/js/method'
  import XkDetailsBox from '../../module/detailsBox/src/detailsBox.vue'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod.vue'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod.vue'

  export default {
    components: {
      XkUnfilledOrderMod,
      XkUploadMod,
      XkDetailsBox,
      XkInputBox,
      XkHintBox,
      XkSiteInput,
      XkDatePickerMod,
      XkCouponSelect
    },
    name: 'HuoYunOrderInfo',
    computed: {
      ...mapGetters({
        positioning: 'positioning',
        infoData: 'infoData',
        loginData: 'loginData',
        companyInfo: 'companyInfo',
        HyQueryCarType: 'HyQueryCarType',
        unfilledOrderState: 'unfilledOrderState',
        HyUnfilledOrder: 'HyUnfilledOrder',
        HyCoupon: 'HyCoupon',
        HyBudgetCost: 'HyBudgetCost'
      }),
      BudgetCostFn: function () {
        let that = this
        let val = that.HyBudgetCost
        if (that.HyBudgetCost) {
          let money = that.HyBudgetCost.money

          if (that.couponMoney.id !== '') {
            if (that.couponMoney.type === 'dikou') {
              money = money - that.couponMoney.val
            }
            if (that.couponMoney.type === 'dazhe') {
              money = money * (that.couponMoney.val * 0.1)
            }
          }

          that.BudgetCostDetails = []
          if (val) {
            that.BudgetCostDetails.push(
              {
                name: '起步价',
                val: val.startPrice > 0 ? val.startPrice.toFixed(1) : val.startPrice
              }
            )
            that.BudgetCostDetails.push(
              {
                name: '公里总价',
                mileage: val.mileage,
                val: val.mileagePrice > 0 ? val.mileagePrice.toFixed(1) : val.mileagePrice
              }
            )
            that.BudgetCostDetails.push(
              {
                name: '行驶时间',
                time: val.travelTime,
                val: val.travelTimePrice > 0 ? val.travelTimePrice.toFixed(1) : val.travelTimePrice
              }
            )
            that.BudgetCostDetails.push(
              {
                name: '优惠券',
                val: money > 0 ? ((val.money - money.toFixed(1)) > 0 ? '-' + (val.money - money.toFixed(1)).toFixed(1) : 0) : '-' + val.money
              }
            )
          }

          if (Number(money) >= 0) {
            return money.toFixed(1)
          } else {
            return 0
          }
        }
      },
      unfilledOrderFn: function () {
        let unfilledOrder = []
        let huoyunData = this.HyUnfilledOrder ? this.HyUnfilledOrder : null
        if (huoyunData && huoyunData.length > 0) {
          let obj = {}
          for (let i = 0; i < huoyunData.length; i++) {
            obj = {
              startAddress: huoyunData[i].startAddress,
              endAddress: huoyunData[i].destination,
              serverTime: Toucth.TimestampDate(huoyunData[i].startTime),
              status: ''
            }
            switch (huoyunData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/huoyun/await', query: {orderId: huoyunData[i].id}}
                }
                break
              case 1:
                obj.status = {
                  name: '等待中',
                  router: {path: '/huoyun/await', query: {orderId: huoyunData[i].id}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                }
                break
              case 3:
                obj.status = {
                  name: '到达预约地',
                  router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                }
                break
              case 4:
                if (huoyunData[i].treatment) {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/huoyun/settlement', query: {orderId: huoyunData[i].id}}
                  }
                  this.$router.push({path: '/huoyun/settlement', query: {orderId: huoyunData[i].id}})
                } else {
                  obj.status = {
                    name: '前往目的地',
                    router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                  }
                }
                break
              case 5:
                if (huoyunData[i].review) {
                  obj.status = {
                    name: '已完成',
                    router: {path: '/huoyun/checkOrder', query: {orderId: huoyunData[i].id}}
                  }
                } else {
                  obj.status = {
                    name: '待评价',
                    router: {path: '/huoyun/affirm', query: {orderId: huoyunData[i].id}}
                  }
                }
                break
              case 6:
                obj.status = {
                  name: '已销单',
                  router: {path: '/huoyun/checkOrder', query: {orderId: huoyunData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      }
    },
    data () {
      return {
        startTime: {
          val: '',
          name: '现在出发'
        },
        datePickerState: false,
        startAdd: {
          state: false,
          contacts: '',
          phone: '',
          name: '',
          detailedAddress: '',
          city: '',
          lat: '',
          lng: ''
        },
        endAdd: {
          state: false,
          contacts: '',
          phone: '',
          name: '',
          detailedAddress: '',
          city: '',
          lat: '',
          lng: ''
        },
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        couponMoney: {
          type: '',
          id: '',
          val: ''
        },
        passAddrList: [],
        passAddrAlter: {
          state: false,
          index: 0,
          content: {
            contacts: '',
            city: '',
            lng: '',
            lat: '',
            phone: '',
            address: ''
          }
        },
        remark: {
          state: false,
          content: ''
        },
        additionalServer: [],
        BudgetCostDetails: [],
        placeholder: '请选择途径地',
        payMount: {
          show: '',
          val: ''
        },
        flitting: false,
        receipt: false,
        receivedPay: false,
        receivedPayBox: false,
        upLoadSate: false
      }
    },
    watch: {
      positioning: function (val) {
        var that = this
        if (val && val.lng !== '' && val.lat !== '') {
          that.startAdd.name = val.name
          that.startAdd.detailedAddress = val.detailedAddress
          that.startAdd.city = val.city
          that.startAdd.lat = val.lat
          that.startAdd.lng = val.lng
        } else {
          that.startAdd.name = ''
          that.startAdd.detailedAddress = ''
          that.startAdd.city = ''
          that.startAdd.lat = ''
          that.startAdd.lng = ''
        }
      }
    },
    methods: {
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'huoyun', state: false})
      },
      selectTimeFn: function () { // 时间选择
        this.datePickerState = true
      },
      DatePickerFn: function (time) { // 时间选择
        if (time) {
          this.startTime.val = time.val
          this.startTime.name = time.name
        }
        this.datePickerState = false
      },
      pushPassAddrFn: function () {
        let that = this
        if (that.passAddrList.length < 5) {
          for (let i = 0; i < that.passAddrList.length; i++) {
            if (that.passAddrList[i].lng === '' || that.passAddrList[i].lat === '' || that.passAddrList[i].address === '') {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '途径路线不能为空'
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1500)
              return false
            }
          }
          that.passAddrList.push({
            contacts: '',
            lng: '',
            lat: '',
            phone: '',
            address: ''
          })
          let projectContent = document.getElementsByClassName('projectContent')[0]
          if (projectContent) {
            setTimeout(() => {
              projectContent.scrollTop = projectContent.offsetHeight + 5000
            }, 0)
          }
        } else {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '最多只能添加5个途径点'
          }
          setTimeout(() => {
            that.hintBox.stateShow = false
          }, 1500)
        }
      },
      removePassAddrFn: function (index) {
        let that = this
        that.passAddrList.splice(index, 1)
        let projectContent = document.getElementsByClassName('projectContent')[0]
        if (projectContent) {
          projectContent.scrollTop = projectContent.offsetHeight + 5000
        }
      },
      passAddrAlterFn: function (obj) {
        let that = this
        let content = that.passAddrAlter.content
        let index = that.passAddrAlter.index
        if (typeof obj + '' === 'number') {
          that.passAddrAlter.index = obj
          that.passAddrAlter.content = that.passAddrList[obj]
        }
        if (!that.passAddrAlter.state) {
          that.placeholder = '请输入途径点地址'
          that.passAddrAlter.state = true
        }
        if (obj === 'affirm') {
          if (content && content.lng !== '' && content.lat !== '' && content.address !== '' && content.phone !== '' && Toucth.checkPhone(content.phone)) {
            if (that.passAddrList[index]) {
              that.passAddrList[index] = that.passAddrAlter.content
            } else {
              that.passAddrList.push(that.passAddrAlter.content)
            }
            that.passAddrAlter.state = !that.passAddrAlter.state
            that.computedBudegtCostFn()
          } else {
            let hintBoxCont = ''

            let hintBoxFn = () => {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: hintBoxCont
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1200)
            }

            if (content && content.lng === '' && content.lat === '' && content.address === '') {
              hintBoxCont = '途径点不能为空'
              hintBoxFn()
              return
            }
            if (content.phone === '') {
              hintBoxCont = '联系人电话不能为空'
              hintBoxFn()
              return
            }
            if (!Toucth.checkPhone(content.phone)) {
              hintBoxCont = '请填写正确联系人电话'
              hintBoxFn()
              return
            }
          }
        }
        if (obj === 'cancel') {
          that.passAddrAlter.state = false
        }
      },
      startAddFn: function (obj) {
        let that = this
        that.startAdd.lat = obj.lat
        that.startAdd.lng = obj.lng
        that.startAdd.detailedAddress = obj.name
        that.startAdd.name = obj.name
        that.startAdd.city = obj.city
        that.computedBudegtCostFn()
      },
      startAddBoxFn: function (obj) {
        let that = this
        if (!that.startAdd.state) {
          that.placeholder = '请输入起点地址'
          that.passAddrAlter.content = {
            contacts: that.startAdd.contacts,
            city: that.startAdd.city,
            lng: that.startAdd.lng,
            lat: that.startAdd.lat,
            phone: that.startAdd.phone,
            address: that.startAdd.name
          }
          that.startAdd.state = true
        }
        let content = that.passAddrAlter.content
        if (obj === 'affirm') {
          if (content && content.lng !== '' && content.lat !== '' && content.address !== '' && content.phone !== '' && Toucth.checkPhone(content.phone)) {
            that.startAdd.contacts = content.contacts
            that.startAdd.phone = content.phone
            that.startAdd.name = content.address
            that.startAdd.detailedAddress = content.detailedAddress
            that.startAdd.city = content.city
            that.startAdd.lat = content.lat
            that.startAdd.lng = content.lng
            that.startAdd.state = !that.startAdd.state
            that.computedBudegtCostFn()
          } else {
            let hintBoxCont = ''

            let hintBoxFn = () => {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: hintBoxCont
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1200)
            }

            if (content && content.lng === '' && content.lat === '' && content.address === '') {
              hintBoxCont = '起点不能为空'
              hintBoxFn()
              return
            }
            if (content.phone === '') {
              hintBoxCont = '联系人电话不能为空'
              hintBoxFn()
              return
            }
            if (!Toucth.checkPhone(content.phone)) {
              hintBoxCont = '请填写正确联系人电话'
              hintBoxFn()
              return
            }
          }
        }
        if (obj === 'cancel') {
          that.startAdd.state = false
        }
      },
      endAddBoxFn: function (obj) {
        let that = this
        if (!that.endAdd.state) {
          that.placeholder = '请输入终点地址'
          that.passAddrAlter.content = {
            contacts: that.endAdd.contacts,
            city: that.endAdd.city,
            lng: that.endAdd.lng,
            lat: that.endAdd.lat,
            phone: that.endAdd.phone,
            address: that.endAdd.name
          }
          that.endAdd.state = true
        }
        let content = that.passAddrAlter.content
        if (obj === 'affirm') {
          if (content && content.lng !== '' && content.lat !== '' && content.address !== '' && content.phone !== '' && Toucth.checkPhone(content.phone)) {
            that.endAdd.contacts = content.contacts
            that.endAdd.phone = content.phone
            that.endAdd.name = content.address
            that.endAdd.detailedAddress = content.detailedAddress
            that.endAdd.city = content.city
            that.endAdd.lat = content.lat
            that.endAdd.lng = content.lng
            that.endAdd.state = !that.endAdd.state
            that.computedBudegtCostFn()
          } else {
            let hintBoxCont = ''

            let hintBoxFn = () => {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: hintBoxCont
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1200)
            }

            if (content && content.lng === '' && content.lat === '' && content.address === '') {
              hintBoxCont = '终点不能为空'
              hintBoxFn()
              return
            }
            if (content.phone === '') {
              hintBoxCont = '联系人电话不能为空'
              hintBoxFn()
              return
            }
            if (!Toucth.checkPhone(content.phone)) {
              hintBoxCont = '请填写正确联系人电话'
              hintBoxFn()
              return
            }
          }
        }
        if (obj === 'cancel') {
          that.endAdd.state = false
        }
      },
      endAddFn: function (obj) {
        let that = this
        that.endAdd.lat = obj.lat
        that.endAdd.lng = obj.lng
        that.endAdd.detailedAddress = obj.name
        that.endAdd.name = obj.name
        that.endAdd.city = obj.city
        that.computedBudegtCostFn()
      },
      passAddrAlterAddFn: function (obj) {
        let that = this
        that.passAddrAlter.content.lat = obj.lat
        that.passAddrAlter.content.lng = obj.lng
        that.passAddrAlter.content.address = obj.name
        that.passAddrAlter.content.city = obj.city
      },
      actionTJFn: function () {
        let siteInput = this.$refs.siteInput
        siteInput.queryShowFn()
      },
      remarkInputFn: function (obj) {
        if (!this.remark.state) {
          this.remark.state = true
        }
        if (obj === 'affirm') {
          if (this.remark.content !== '') {
            this.remark.state = false
          }
        }
        if (obj === 'cancel') {
          this.remark.state = false
        }
      },
      couponSlelectFn: function (obj) {
        if (obj.type === 'nonuse') {
          this.couponMoney = {
            type: '',
            id: '',
            num: ''
          }
          return true
        }
        if (obj.type === 'action' && obj.obj && obj !== '') {
          if (obj.obj.couponRule.discount && obj.obj.couponRule.discount !== '') {
            this.couponMoney = {
              type: 'dazhe',
              val: obj.obj.couponRule.discount,
              id: obj.obj.id
            }
          } else if (obj.obj.couponRule.money && obj.obj.couponRule.money !== '') {
            this.couponMoney = {
              type: 'dikou',
              val: obj.obj.couponRule.money,
              id: obj.obj.id
            }
          }
          return true
        }
      },
      computedBudegtCostFn: function () { // 计算预计公里及时间
        let that = this
        let origins = ''
        let destinations = ''
        let waypoints = ''
        if ((that.startAdd.lng !== '', that.startAdd.lat !== '') && (that.endAdd.lng !== '', that.endAdd.lat !== '')) {
          if (that.passAddrList && that.passAddrList.length > 0) {
            origins = that.startAdd.lat + ',' + that.startAdd.lng
            for (let i = 0; i < that.passAddrList.length; i++) {
              if (that.passAddrList[i].lat !== '' && that.passAddrList[i].lng !== '') {
                if (i === 0) {
                  waypoints = waypoints + that.passAddrList[i].lat + ',' + that.passAddrList[i].lng
                } else {
                  waypoints = waypoints + '|' + that.passAddrList[i].lat + ',' + that.passAddrList[i].lng
                }
              }
            }
            destinations = that.endAdd.lat + ',' + that.endAdd.lng
          } else {
            origins = that.startAdd.lat + ',' + that.startAdd.lng
            destinations = that.endAdd.lat + ',' + that.endAdd.lng
          }
          that.$store.dispatch('countLine', {
            origin: origins,
            waypoints: waypoints !== '' ? waypoints : null,
            destination: destinations,
            origin_region: that.startAdd.city.replace('市', ''),
            destination_region: that.endAdd.city.replace('市', ''),
            fn: (data) => {
              if (data.status === 0) {
                let time = 0
                let mileage = 0
                let result = data.result
                if (result.taxi) {
                  time = (result.taxi.duration / 60).toFixed(0)
                  mileage = (result.taxi.distance / 1000).toFixed(1)
                }
                that.$store.dispatch('HyBudgetCost', {
                  'companyId': that.loginData.passenger.companyId,
                  'areaId': Toucth.AcquireUrlParameter().areaid,
                  'time': time,
                  'mileage': mileage
                })
              }
            }
          })
        }
      },
      submitOrderFn: function () { // 下单
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            let hintBoxContent = ''

            let hintBoxStateFn = () => {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: hintBoxContent
              }

              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1500)
            }

            if (that.startAdd.lat === '' && that.startAdd.lng === '' && that.startAdd.phone === '' && that.startAdd.contacts === '') {
              hintBoxContent = '起点不能为空'
              hintBoxStateFn()
              return false
            }

            if (that.endAdd.lat === '' && that.endAdd.lng === '' && that.endAdd.phone === '' && that.endAdd.contacts === '') {
              hintBoxContent = '终点不能为空'
              hintBoxStateFn()
              return false
            }

            for (let i = 0; i < that.passAddrList.length; i++) {
              if (that.passAddrList[i].lng === '' && that.passAddrList[i].lat === '' && that.passAddrList[i].phone === '' && that.passAddrList[i].contacts === '') {
                hintBoxContent = '途径点不能为空'
                hintBoxStateFn()
                return false
              }
            }

            let carTypeName = ''
            for (let i = 0; i < that.HyQueryCarType.length; i++) {
              if (Number(Toucth.AcquireUrlParameter().id) === that.HyQueryCarType[i].id) {
                carTypeName = that.HyQueryCarType[i].typeName
              }
            }
            let waypoint = []
            waypoint.push({
              'contacts': that.startAdd.contacts,
              'lng': that.startAdd.lng,
              'lat': that.startAdd.lat,
              'phone': that.startAdd.phone,
              'address': that.startAdd.name
            })
            let passAddrList = that.passAddrList
            if (passAddrList && passAddrList.length > 0) {
              for (let i = 0; i < passAddrList.length; i++) {
                waypoint.push({
                  'contacts': passAddrList[i].contacts,
                  'lng': passAddrList[i].lng,
                  'lat': passAddrList[i].lat,
                  'phone': passAddrList[i].phone,
                  'address': passAddrList[i].address
                })
              }
            }
            waypoint.push({
              'contacts': that.endAdd.contacts,
              'lng': that.endAdd.lng,
              'lat': that.endAdd.lat,
              'phone': that.endAdd.phone,
              'address': that.endAdd.name
            })
            that.upLoadSate = true
            that.$store.dispatch('HyPlaceAnOrder', {
              'passengerName': that.loginData.passenger.name, // 客户名称
              'passengerPhone': that.loginData.passenger.phone, // 手机s
              'truckTypeId': Toucth.AcquireUrlParameter().id, // 货车类型Id
              'truckTypeName': carTypeName, // 货车类型名称
              'startTime': that.startTime.val, // 预约时间
              'waypoint': waypoint.length > 0 ? JSON.stringify(waypoint) : null, // 途径点
              'preStartPrice': that.HyBudgetCost ? that.HyBudgetCost.startPrice : 0, // 预算起步价
              'preMileage': that.HyBudgetCost ? that.HyBudgetCost.mileage : 0, // 预算公里数
              'preRunTime': that.HyBudgetCost ? that.HyBudgetCost.travelTime : 0, // 预算行驶时间
              'preRunTimePrice': that.HyBudgetCost ? that.HyBudgetCost.travelTimePrice : 0, // 预算时间价格
              'budgetPay': that.HyBudgetCost ? that.HyBudgetCost.money : 0, // 预算价格
              'passengerId': that.loginData.passenger.id,
              'companyId': that.companyInfo.companyId,
              'companyName': that.companyInfo.companyName,
              'memo': that.remark.content,
              'flitting': that.flitting, // 搬运
              'receipt': that.receipt, // 收据
              'receivedPay': that.receivedPay, // 收钱
              'payMount': that.payMount.val, // 汇款金额
              'couponId': that.couponMoney.id ? that.couponMoney.id : null,
              'preMileagePrice': that.HyBudgetCost ? that.HyBudgetCost.mileagePrice : 0, // 预算回来的公里费用
              'payDetail': that.HyBudgetCost ? that.HyBudgetCost.pay_detail : 0, // 预算 中文
              'tip': 0, // 小费
              fn: (data) => {
                that.upLoadSate = false
                if (data.code === 0) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '下单成功'
                  }
                  setTimeout(() => {
                    that.hintBox = false
                    that.$router.push({path: '/huoyun/await', query: {orderId: data.data.id}})
                  }, 1500)
                }
                if (data.code !== 0) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: data.msg
                  }
                  setTimeout(() => {
                    that.hintBox = false
                    if (data.code === -86) {
                      this.$store.dispatch('unfilledOrderState', {type: 'huoyun', state: true})
                    }
                  }, 1500)
                }
              }
            })
          }
        })
      },
      additionalServerFn: function (type) { // 选择额外服务
        let that = this
        switch (type) {
          case 'flitting': // 搬运
            that.flitting = !that.flitting
            break
          case 'receipt': // 回单
            that.receipt = !that.receipt
            break
          case 'receivedPay': // 收钱
            that.receivedPay = !that.receivedPay
            if (that.receivedPay) {
              that.payMount.val = ''
              that.payMount.show = ''
              that.receivedPayBox = true
            } else {
              that.receivedPayBox = false
            }
            break
        }
      },
      receivedPayBoxFn: function (obj) {
        var that = this
        if (obj === 'affirm') {
          if (that.payMount.show > 0) {
            if (that.payMount.show <= 100000) {
              that.receivedPayBox = false
              that.payMount.val = that.payMount.show
            } else {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '金额不能大于10万'
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1500)
            }
          } else {
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '金额不能小于0元'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        }
        if (obj === 'cancel') {
          that.payMount.show = that.payMount.val
          that.receivedPay = false
          that.receivedPayBox = false
        }
      },
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      },
      inputFn: function (e, type) {
        let that = this
        let val = e.target.value
        switch (type) {
          case 'payMount': // 联系电话
            that.hintBox.stateShow = false
            if (Number(val) > 100000) {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '回款金额不能超过10万'
              }
              that.payMount.val = 100000
              that.payMount.show = that.payMount.val
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
            break
        }
      }
    },
    mounted: function () {
      let that = this
      let date = new Date()
      that.startTime.name = '现在出发'
      that.startTime.val = date.valueOf()
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          if (that.positioning && that.positioning.lng !== '' && that.positioning.lat !== '') {
            that.startAdd = {
              state: false,
              contacts: that.loginData.passenger.name,
              phone: that.loginData.passenger.phone,
              name: that.positioning.name,
              detailedAddress: that.positioning.detailedAddress,
              city: that.positioning.city,
              lat: that.positioning.lat,
              lng: that.positioning.lng
            }
          } else {
            that.startAdd = {
              state: false,
              contacts: that.loginData.passenger.name,
              phone: that.loginData.passenger.phone,
              name: '',
              detailedAddress: '',
              city: '',
              lat: '',
              lng: ''
            }
          }
          that.$store.dispatch('HyQueryCarType', {
            'companyId': that.loginData.passenger && that.loginData.passenger.companyId ? that.loginData.passenger.companyId : that.infoData.companyInfo.data.companyId
          })
          that.$store.dispatch('HyBudgetCost', {
            'areaId': Toucth.AcquireUrlParameter().areaid,
            'time': 0,
            'mileage': 0
          })
          that.$store.dispatch('HyCoupon', {
            passengerId: that.loginData.passenger.id
          })
          that.$store.dispatch('HyUnfilledOrder', {passengerId: that.loginData.passenger.id})
        }
      })
    }
  }
</script>

