/**查看订单**/
<template>
  <div class="checkOrder">
    <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
               :name="v5FindZcOrderById?v5FindZcOrderById.employ_name:''"
               :carNo="v5FindZcOrderById?v5FindZcOrderById.vehicle_no:''"
               :carName="v5FindZcOrderById?v5FindZcOrderById.vehicle_brand:''"
               :carColor="v5FindZcOrderById?v5FindZcOrderById.vehicle_color:''"
               :carType="v5FindZcOrderById?v5FindZcOrderById.vehicle_type:''"
               :headImg="v5FindZcOrderById?v5FindZcOrderById.employ_head_path:''">
    </xk-waiter>
    <div class="money">
      <h2>
        <span class="max">{{v5FindZcOrderById ? v5FindZcOrderById.real_pay.toFixed(1): '0'}}元</span>
        <!--<span>.{{v5FindZcOrderById && v5FindZcOrderById.payMoney ? v5FindZcOrderById.payMoney.toFixed(1).split('.')[1] : '0'}}元</span>-->
      </h2>
      <div class="detail">
        <span @click="detailsBoxFn"><i class="iconfont icon-iconfontyiwen"></i>查看明细</span>
      </div>
      <xk-star :defScore="v5FindZcOrderById?v5FindZcOrderById.rate_score:5" :state="true"></xk-star>
      <div class="label">
        <xk-tag-item :name="item" :AcState="false"
                     v-for="(item, index) in v5FindZcOrderById?v5FindZcOrderById.rate_sign:null"
                     :key="index"></xk-tag-item>
      </div>
      <div class="reviewContent">
        {{v5FindZcOrderById && v5FindZcOrderById.rate_content && v5FindZcOrderById.rate_content !== '' ?
        v5FindZcOrderById.rate_content : '暂无评价'}}
      </div>
    </div>
    <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <ul>
          <li v-for="itme in payDataFn" v-if="itme.val !== 0" class="clear">
            <span class="name">{{itme.name}}</span>
            <span class="val"><span class="sum">{{itme.val}}</span><span class="unit">元</span></span>
            <span class="time" v-if="itme.time">{{itme.time}}<span class="unit">分钟</span></span>
          </li>
        </ul>
      </div>
    </Xk-detailsBox>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import {mapGetters} from 'vuex'
  import XkStar from '../../module/star/src/star'
  import XkTagItem from '../../module/tagitem/src/tagitem'

  export default {
    components: {
      XkTagItem,
      XkStar,
      XkWaiter
    },
    name: '',
    computed: {
      ...mapGetters({
        ZcQueryReview: 'ZcQueryReview',
        ZcPayInfo: 'ZcPayInfo',
        loginData: 'loginData',
        ZcQueryOrderInfo: 'ZcQueryOrderInfo',
        v5FindZcOrderById: 'v5FindZcOrderById'
      }),
      payDataFn: function () {
        let that = this
        let obj = []
        let order = that.orderFee
        if (order) {
          if (order.pre_pay) {
            obj.push({
              name: '预付金额',
              val: order.pre_pay
            })
          }
          if (order.start_price) {
            obj.push({
              name: '起步价',
              val: order.start_price
            })
          }
          if (order.distance_fee) {
            obj.push({
              name: '里程费',
              val: order.distance_fee
            })
          }
          if (order.cross_fee) {
            obj.push({
              name: '垫付费用',
              val: order.cross_fee
            })
          }
          if (order.time_fee) {
            obj.push({
              name: '时间费',
              val: order.time_fee
            })
          }
          if (order.wait_fee) {
            obj.push({
              name: '等待费',
              val: order.wait_fee
            })
          }
          if (order.other_fee) {
            obj.push({
              name: '附加服务费',
              val: order.other_fee
            })
          }
          if (order.peak_cost) {
            obj.push({
              name: '运营高峰费',
              val: order.peak_cost
            })
          }
          if (order.night_price) {
            obj.push({
              name: '夜间里程费',
              val: order.night_price
            })
          }
          if (order.night_time_price) {
            obj.push({
              name: '夜间时间费',
              val: order.night_time_price
            })
          }
          if (order.low_speed_cost) {
            obj.push({
              name: '低速费',
              val: order.low_speed_cost
            })
          }
          if (order.add_fee) {
            obj.push({
              name: '调价费用',
              val: order.add_fee
            })
          }
          if (order.total_fee) {
            obj.push({
              name: '订单总金额',
              val: order.total_fee
            })
          }
          if (order.coupon_fee) {
            obj.push({
              name: '优惠券抵扣',
              val: order.coupon_fee
            })
          }
        }
        return obj
      },
      carTypeFn: function () {
        let ZcQueryOrderInfo = this.ZcQueryOrderInfo
        if (this.ZcQueryOrderInfo) {
          if (ZcQueryOrderInfo.ifTaxi && ZcQueryOrderInfo.ifTaxi === '1') {
            return '出租车'
          }
          if (ZcQueryOrderInfo.carTypeName) {
            return ZcQueryOrderInfo.carTypeName
          }
        }
      }
    },
    data () {
      return {
        orderFee: ''
      }
    },
    watch: {
      ZcQueryOrderInfo (val) {
        var that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/zhuanche')
          return
        }
      }
    },
    methods: {
      getOrderInforByid () {
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('findZcOrderById', {
            order_id: that.$route.query.orderId,
            fn (data) {
              if (data.code === 1) {
                let order = data.order
                if (order.rate_sign && order.rate_sign !== '') {
                  order.rate_sign = order.rate_sign.split(',')
                }
                data.order = order
                resolve(data)
              } else {
                reject(data)
              }
            }
          })
        })
      },
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      }
    },
    mounted: function () {
      let that = this
      let order = that.getOrderInforByid()
      order.then(function (res) {
        that.orderFee = res.orderFee
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .checkOrder {
    .money {
      padding-top: 3rem;
      text-align: center;
      h2 {
        font-size: 1.7rem;
        text-align: center;
        color: #f06553;
        .max {
          font-size: 3.2rem;
        }
      }
      .detail {
        margin-top: 0rem;
        line-height: 1.2rem;
        text-align: center;
        color: #878787;
      }
      .score {
        margin-top: 3rem;
        margin-bottom: 3rem;
      }
      .label {
        margin: 0 auto;
        margin-top: 1rem;
        width: 80%;
        text-align: center;
        .TagItem {
          box-sizing: border-box;
          width: calc(~'50% - 1rem');
          border-radius: 0;
          background: rgba(0, 0, 0, 0.04);
          font-size: 1rem;
          color: #999;
          border: none;
          line-height: 2.5rem;
          height: 2.5rem;
          margin: 0.3rem 0.5rem;
        }
      }
      .reviewContent {
        margin: 0 auto;
        box-sizing: border-box;
        width: 80%;
        margin-top: 1rem;
        line-height: 1.5rem;
        padding: 0.5rem;
        min-height: 5rem;
        border-radius: 0.333rem;
        text-align: center;
        color: #999;
      }
    }
    .costBox {
      ul {
        li {
          height: 2rem;
          line-height: 2rem;
          color: #999999;
          .name {
            float: left;
          }
          .time, .val {
            float: right;
          }
          .time {
            margin-right: 0.8rem;
            color: #333333;
          }
          .sum {
            color: #f06553;
          }
          .unit {
            margin-left: 0.3rem;
          }
        }
      }
    }
  }
</style>



