/**完成订单 评价订单**/
<template>
  <div class="affirm">
    <div class="content">
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="HyOrderInfo?HyOrderInfo.employName:''"
                 :headImg="HyOrderInfo?HyOrderInfo.employPhoto:''"
                 :carNo="HyOrderInfo?HyOrderInfo.truckNumber:''"
                 :carName="HyOrderInfo?HyOrderInfo.truckName:''"
                 :carType="HyOrderInfo ? HyOrderInfo.truckTypeName :''"
      ></xk-waiter>
      <div class="grade">
        <xk-star @clickChange="starFn" :defScore="5"></xk-star>
        <div class="evaluate">
          <div class="evaluateTag clear">
            <div class="TagList clear">
              <xk-tag-item v-for="item in tags" :name="item.name" :value="item.val" @clickChange="tagsFn"
                           :state="false" :key="item.name"></xk-tag-item>
            </div>
          </div>
          <div class="remark">
            <xk-textarea @input="remarkFn" defaulttext="请输入其它意见和建议"></xk-textarea>
          </div>
        </div>
      </div>
      <div class="submit">
        <span @click="submitFn">确定</span>
      </div>
    </div>
    <xk-hintBox :time="1500" v-if="hintBox.stateShow"
                :state="hintBox.state"
                :content="hintBox.content"></xk-hintBox>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
  </div>
</template>

<script>
  import XkTagItem from '../../module/tagitem/src/tagitem'
  import XkStar from '../../module/star/src/star'
  import XkTextarea from '../../module/textarea/src/textarea'
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'

  export default {
    components: {
      XkUploadMod,
      XkStar,
      XkTagItem,
      XkTextarea,
      XkWaiter,
      XkHintBox
    },
    name: 'affirm',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        HyOrderInfo: 'HyOrderInfo'
      })
    },
    data () {
      return {
        score: '',
        tagsVal: [], // 选择的标签
        tags: [], // 标签
        remark: '', // 备注
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    methods: {
      starFn: function (msg) { // 星星的回掉函数
        let that = this
        that.score = msg
        that.tagsVal = []
        switch (msg) {
          case 1 :
            that.tags = [
              {
                name: '货品丢失',
                val: '货品丢失'
              }, {
                name: '送货太慢',
                val: '送货太慢'
              }, {
                name: '服务态度恶劣',
                val: '服务态度恶劣'
              }, {
                name: '开车玩手机',
                val: '开车玩手机'
              }
            ]
            break
          case 2 :
            that.tags = [
              {
                name: '货品丢失',
                val: '货品丢失'
              }, {
                name: '送货太慢',
                val: '送货太慢'
              }, {
                name: '服务态度差',
                val: '服务态度差'
              }, {
                name: '不遵守交通规则',
                val: '不遵守交通规则'
              }
            ]
            break
          case 3 :
            that.tags = [
              {
                name: '货品严重破损',
                val: '货品严重破损'
              }, {
                name: '服务态度一般',
                val: '服务态度一般'
              }, {
                name: '送货慢',
                val: '送货慢'
              }, {
                name: '司机责任心一般',
                val: '司机责任心一般'
              }
            ]
            break
          case 4 :
            that.tags = [
              {
                name: '货品有轻微破损',
                val: '货品有轻微破损'
              }, {
                name: '服务态度还行',
                val: '服务态度还行'
              }, {
                name: '送货速度待提高',
                val: '送货速度待提高'
              }, {
                name: '司机有责任心',
                val: '司机有责任心'
              }
            ]
            break
          case 5 :
            that.tags = [
              {
                name: '货品保存完好',
                val: '货品保存完好'
              }, {
                name: '送货快',
                val: '送货快'
              }, {
                name: '风雨无阻',
                val: '风雨无阻'
              }, {
                name: '服务态度超赞',
                val: '服务态度超赞'
              }
            ]
            break
          default:
            that.tags = []
        }
      },
      tagsFn: function (msg) { // 标签的回掉函数
        if (msg.state) {
          this.tagsVal.push(msg.val)
        } else {
          for (let i = 0; i < this.tags.length; i++) {
            if (msg.val === this.tagsVal[i]) {
              this.tagsVal.splice(i)
              return
            }
          }
        }
      },
      remarkFn: function (msg) { // 文本事件
        this.remark = msg
      },
      submitFn: function () { // 保存事件
        let that = this
        let content = that.tagsVal.join('&|&') + '&*&' + that.remark
        if (that.score !== '' && that.score) {
          that.loadState = true
          that.$store.dispatch('HyEvaluateOrder', {
            orderId: Toucth.AcquireUrlParameter().orderId,
            score: that.score,
            content: content,
            fn: (data) => {
              if (data.code === 0) {
                that.loadState = false
                that.$router.push({path: '/huoyun/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '评价成功！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/huoyun'})
                }, 1500)
              } else {
                that.loadState = false
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '评价失败！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
              }
            }
          })
        }
      }
    },
    watch: {
      HyOrderInfo (val) {
        var that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/huoyun')
          return
        }
        if (val.status === 5 && val.review) {
          this.$router.push('/huoyun')
        }
      }
    },
    mounted: function () {
      let that = this
      this.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .affirm {
    min-height: 100%;
    .content {
      box-sizing: border-box;
      width: 100vw;
      background-color: #f3f4f5;
      .grade {
        width: 100%;
        padding: 0 3.25rem;
        box-sizing: border-box;
        text-align: center;
        .evaluate {
          width: 100%;
          padding-top: 1.6rem;
          .evaluateTag {
            width: 100%;
            .TagList {
              width: 100%;

            }
          }
          .remark {
            box-sizing: border-box;
            margin-top: 1rem;
            height: 8.83rem;
            width: 100%;
          }
        }

      }
      .submit {
        box-sizing: border-box;
        width: 100%;
        padding: 0 3.25rem;
        margin-top: 1.5rem;
        padding-bottom: 1rem;
        span {
          width: 100%;
          height: 3.75rem;
          line-height: 3.75rem;
          background-color: #f06553;
          color: #fff;
          text-align: center;
          border-radius: 0.333rem;
          overflow: hidden;
          font-size: 1rem;
        }
      }
    }
  }
</style>



