/**等待中**/
<template>
  <div class="huoyunAwait">
    <div class="AwaitContent">
      <div class="page page1">
        <div class="pageCarType">
          <span v-if="HyOrderInfo">{{HyOrderInfo ? HyOrderInfo.truckTypeName : ''}}</span>
        </div>
        <div class="upload">
          <div class="img">
            <img src="../../../assets/img/zhuanche/await_img.png" :style="{transform: 'rotate('+rotateVal+'deg)'}"
                 alt="专车">
          </div>
          <span class="text">等待中</span>
        </div>
        <h2>
          请稍等正在为您联络司机
        </h2>
        <span class="cancelBut" @click="cancelOrderFn">取消订单</span>
      </div>
      <xk-Add :time="HyOrderInfo?TimeFn:''"
              :origin="HyOrderInfo?HyOrderInfo.startAddress:''"
              :endPoint="HyOrderInfo?HyOrderInfo.endAddress:''"
              :waypoint="HyOrderInfo?wayPointsFn:null"
      >
      </xk-Add>
    </div>
    <xk-input-box v-if="reson.state" title="取消订单理由" @click="cancelOrderFn" :closeState="true">
      <div slot="content" class="DjCancelOrder">
        <textarea v-model="reson.val" placeholder="请输入取消订单理由">
        </textarea>
      </div>
    </xk-input-box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkAdd from '../../module/add/src/add.vue'
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod,
      XkInputBox,
      XkAdd
    },
    name: 'huoyunAwait',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        HyOrderInfo: 'HyOrderInfo'
      }),
      TimeFn: function () {
        if (this.HyOrderInfo && this.HyOrderInfo.serverTime) {
          return Toucth.TimestampDate(this.HyOrderInfo.serverTime)
        }
      },
      wayPointsFn: function () {
        var that = this
        var obj = []
        if (that.HyOrderInfo && that.HyOrderInfo.wayPoints.length > 2) {
          var wayPoints = that.HyOrderInfo.wayPoints
          for (let i = 0; i < wayPoints.length; i++) {
            if (i !== 0 && i !== (wayPoints.length - 1)) {
              obj.push(wayPoints[i])
            }
          }
        }
        return obj
      }
    },
    data () {
      return {
        rotateVal: 0, // 旋转值
        reson: {
          state: false,
          val: ''
        },
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    watch: {
      HyOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/huoyun')
          return
        }
        val.status = val.status + ''
        if (val.status === '0' || val.status === '1') {
          setTimeout(() => {
            if (that.$route.fullPath.split('?')[0] === '/huoyun/await') {
              that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
            }
          }, 3000)
        }
        if (val.status === '2' || val.status === '3' || val.status === '4') {
          that.$router.push({path: '/huoyun/service', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
        if (val.status === '5') {
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/huoyun/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          }, 1500)
          return
        }
        if (val.status === '6') {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/huoyun'})
            return false
          }, 1500)
          return
        }
      }
    },
    methods: {
      cancelOrderFn: function (val) {
        let that = this
        this.reson.state = !this.reson.state
        if (val === 'affirm') {
          if (this.reson.val !== '') {
            that.loadState = true
            this.$store.dispatch('HyCancelOrder', {
              orderId: Toucth.AcquireUrlParameter().orderId,
              cause: this.reson.val,
              fn: (state) => {
                that.loadState = false
                if (state && state.code === 0) {
                  this.reson.state = false
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单成功！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/huoyun'})
                  }, 1500)
                }
              }
            })
          } else {
            this.reson.state = true
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请输入销单理由！'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        }
      }
    },
    mounted: function () {
      let that = this

      function rotateFn () {
        setTimeout(function () {
          that.rotateVal = that.rotateVal + 8
          rotateFn()
        }, 39)
      }

      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })

      rotateFn()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .huoyunAwait {
    .AwaitContent {
      height: 100vh;
      width: 100vw;
      box-sizing: border-box;
      overflow: hidden;
      overflow-y: auto;
      background-color: #f3f4f5;
      .page {
        margin-bottom: 1rem;
        background-color: #fff;
        box-shadow: 0 0 5px rgba(0, 0, 0, .1);
        text-align: center;
        padding: 2rem 0;
        .pageCarType {
          text-align: center;
          margin-top: 2.125rem;
          span {
            padding: 0 1rem;
            height: 1.8rem;
            line-height: 1.9rem;
            color: #fff;
            background-color: #f06553;
            font-size: 1rem;
            border-radius: 0.9rem;
          }
        }
        .upload {
          width: 100%;
          height: 7rem;
          text-align: center;
          margin-top: 3.125rem;
          position: relative;
          img {
            height: 7rem;
            transform-origin: 50% 50%;
            transform: rotate(0);
          }
          .text {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: #999999;
            font-size: 1rem
          }

        }

        h2 {
          font-size: 1.25rem;
          color: #333;
          margin-top: 2.7rem;
          text-align: center;
        }
        .cancelBut {
          color: #f06553;
          font-size: 0.8333rem;
          margin-top: 1rem;
        }
      }
      .page1 {
        text-align: center;
        padding: 1rem 2rem;
        padding-bottom: 2rem;

      }
    }
    .DjCancelOrder {
      border: 1px solid #eee;
      height: 10rem;
      textarea {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        border: none;
        resize: none;
        font-size: 1rem;
        padding: 0.5rem;
      }
    }
  }
</style>



