/**服务中**/
<template>
  <div class="Service">
    <div class="Content">
      <div class="describe">
        <div class="describeContent">
          <h2>{{title}}</h2>
          <h3>{{titleCont}}</h3>
        </div>
      </div>
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="HyOrderInfo?HyOrderInfo.employName:''"
                 :phone="HyOrderInfo?HyOrderInfo.employPhone:''"
                 :headImg="HyOrderInfo?HyOrderInfo.employPhoto:''"
                 :carNo="HyOrderInfo?HyOrderInfo.truckNumber:''"
                 :carName="HyOrderInfo?HyOrderInfo.truckName:''"
                 :carType="HyOrderInfo ? HyOrderInfo.truckTypeName :''"
      >
      </xk-waiter>
      <xk-Add :time="HyOrderInfo?TimeFn:''"
              :origin="HyOrderInfo?HyOrderInfo.startAddress:''"
              :endPoint="HyOrderInfo?HyOrderInfo.endAddress:''"
              :waypoint="HyOrderInfo?wayPointsFn:null"
      >
      </xk-Add>
    </div>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkAdd from '../../module/add/src/add.vue'
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkWaiter,
      XkAdd
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        HyOrderInfo: 'HyOrderInfo'
      }),
      TimeFn: function () {
        if (this.HyOrderInfo && this.HyOrderInfo.serverTime) {
          return Toucth.TimestampDate(this.HyOrderInfo.serverTime)
        }
      },
      wayPointsFn: function () {
        var that = this
        var obj = []
        if (that.HyOrderInfo && that.HyOrderInfo.wayPoints.length > 2) {
          var wayPoints = that.HyOrderInfo.wayPoints
          for (let i = 0; i < wayPoints.length; i++) {
            if (i !== 0 && i !== (wayPoints.length - 1)) {
              obj.push(wayPoints[i])
            }
          }
        }
        return obj
      }
    },
    watch: {
      HyOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/huoyun')
          return
        }
        val.status = val.status + ''
        if (val.status === '2') {
          that.title = '司机已接单'
          that.titleCont = '正在向您狂奔...'
        }
        if (val.status === '3') {
          that.title = '司机已到达预约地'
          that.titleCont = '准备装货啦...'
        }
        if (val.status === '4') {
          if (val.treatment) {
            that.$router.push({path: '/huoyun/settlement', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          } else {
            var wayPoints = that.HyOrderInfo.wayPoints
            if (wayPoints.length > 2) {
              (() => {
                for (let i = 0; i < wayPoints.length; i++) {
                  if (!wayPoints[i].arrive) {
                    that.title = '正在护送您的货物！'
                    that.titleCont = '前往' + wayPoints[i].address
                    return false
                  }
                }
              })()
            } else {
              that.title = '正在前往目的地...'
              that.titleCont = '正在为您的货物保驾护航！'
            }
          }
        }
        if (val.status === '5') {
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/huoyun/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
          }, 1500)
          return
        }
        if (val.status === '6') {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/huoyun'})
          }, 1500)
          return
        }
        setTimeout(function () {
          if (that.$route.fullPath.split('?')[0] === '/huoyun/service') {
            that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
          }
        }, 3000)
      }
    },
    data () {
      return {
        title: '',
        titleCont: '',
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Service {
    .Content {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      .describe {
        height: 16.666rem;
        background-color: #fff;
        position: relative;
        .describeContent {
          width: 80%;
          position: absolute;
          left: 50%;
          text-align: center;
          top: 50%;
          transform: translate(-50%, -50%);
        }
        h2 {
          display: inline-block;
          width: 100%;
          font-size: 2rem;
          color: #333;
          text-align: center;
          margin-top: 1rem;
          height: 2.5rem;
          line-height: 2.5rem;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        h3 {
          display: inline-block;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          color: #999999;
          font-size: 1.25rem;
          text-align: center;
          margin-top: 1.4rem;
          line-height: 1.5rem;
          height: 1.5rem;
        }
      }
      .Add {
        margin-top: 1rem;
      }
    }
  }
</style>



