/***       货运        ***/
<template>
  <div class="HuoYun">
    <div class="content">
      <div class="banner" v-if="huoyunBanner && huoyunBanner.length>0">
        <xk-roll-banner :data="huoyunBanner"></xk-roll-banner>
      </div>
      {{queryCarType}}
      <div class="contentList">
        <div v-if="carType && carType.length">
          <div class="row clear" v-for="(item,index) in carType" @click="actionFn(item.id,item.areaId)">
            <div class="left">
              <img :src=" serverUrl+ item.img">
            </div>
            <div class="right">
              <div class="title clear">
                <h2 class="name">{{item.typeName}}</h2>
                <h2 class="price" v-if="item.cangointotown">
                  <span class="val">入城</span>
                </h2>
              </div>
              <div class="item">
                <p>载重: {{item.carryingCapacity.toFixed(1)}}(吨)</p>
              </div>
              <div class="item">
                <p>长*宽*高: {{item.len.toFixed(1)}}*{{item.width.toFixed(1)}}*{{item.height.toFixed(1)}}(米)</p>
              </div>
              <div class="item">
                <p>可装载体积: {{(item.len.toFixed(1) * item.width.toFixed(1) * item.height.toFixed(1)).toFixed(1)}}(平方米)</p>
              </div>
            </div>
            <div class="icon">
              <i class="iconfont icon-jiantou"></i>
            </div>
          </div>
        </div>
        <div v-else="">
          <div class="tishi">该地区暂未开通该业务</div>
        </div>
      </div>
    </div>
    <xk-unfilled-order-mod
      v-if="unfilledOrderState && unfilledOrderState.huoyun &&  unfilledOrderFn.length>0"
      :data="unfilledOrderFn?unfilledOrderFn:null"
      :title="'货运'"
      @click="unfilledOrderAcFn"
      @close="unfilledOrdercCloseFn"
    ></xk-unfilled-order-mod>

  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkRollBanner from '../../module/rollBanner/src/rollBanner.vue'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod.vue'

  export default {
    components: {
      XkUnfilledOrderMod,
      XkRollBanner
    },
    name: 'huoyun',
    computed: {
      ...mapGetters({
        positioning: 'positioning',
        huoyunBanner: 'huoyunBanner',
        loginData: 'loginData',
        unfilledOrderState: 'unfilledOrderState',
        HyUnfilledOrder: 'HyUnfilledOrder',
        infoData: 'infoData',
        HyQueryCarType: 'HyQueryCarType'
      }),
      queryCarType: function () {
        let that = this
        let carType = that.HyQueryCarType
        let obj = []
        if (carType && carType.length > 0) {
          for (let i = 0; i < carType.length; i++) {
            obj.push(carType[i])
          }
        }
        that.carType = obj
      },
      unfilledOrderFn: function () {
        let unfilledOrder = []
        let huoyunData = this.HyUnfilledOrder ? this.HyUnfilledOrder : null
        if (huoyunData && huoyunData.length > 0) {
          let obj = {}
          for (let i = 0; i < huoyunData.length; i++) {
            obj = {
              startAddress: huoyunData[i].startAddress,
              endAddress: huoyunData[i].destination,
              serverTime: Toucth.TimestampDate(huoyunData[i].startTime),
              status: ''
            }
            switch (huoyunData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/huoyun/await', query: {orderId: huoyunData[i].id}}
                }
                break
              case 1:
                obj.status = {
                  name: '等待中',
                  router: {path: '/huoyun/await', query: {orderId: huoyunData[i].id}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                }
                break
              case 3:
                obj.status = {
                  name: '到达预约地',
                  router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                }
                break
              case 4:
                if (huoyunData[i].treatment) {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/huoyun/settlement', query: {orderId: huoyunData[i].id}}
                  }
                  this.$router.push({path: '/huoyun/settlement', query: {orderId: huoyunData[i].id}})
                } else {
                  obj.status = {
                    name: '前往目的地',
                    router: {path: '/huoyun/service', query: {orderId: huoyunData[i].id}}
                  }
                }
                break
              case 5:
                if (huoyunData[i].review) {
                  obj.status = {
                    name: '已完成',
                    router: {path: '/huoyun/checkOrder', query: {orderId: huoyunData[i].id}}
                  }
                } else {
                  obj.status = {
                    name: '待评价',
                    router: {path: '/huoyun/affirm', query: {orderId: huoyunData[i].id}}
                  }
                }
                break
              case 6:
                obj.status = {
                  name: '已销单',
                  router: {path: '/huoyun/checkOrder', query: {orderId: huoyunData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      }
    },
    data () {
      return {
        carType: [],
        serverUrl: window.configure.serverUrl
      }
    },
    watch: {
      infoData: function (val) {
        var that = this
        if (val) {
          that.$store.dispatch('HyQueryCarType', {
            'companyId': that.loginData.passenger && that.loginData.passenger.companyId ? that.loginData.passenger.companyId : that.infoData.companyInfo.data.companyId
          })
        }
      }
    },
    methods: {
      actionFn: function (id, areaId) {
        var that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            that.$store.dispatch('unfilledOrderState', {type: 'huoyun', state: true})
            that.$router.push({path: '/huoyun/orderInfo', query: {id: id, areaid: areaId}})
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'huoyun', state: false})
      },
      unfilledOrderAcFn: function (val) { // 未完成订单
        switch (val.status) {
          case 0:
            this.$router.push({path: '/huoyun/await', query: {orderId: val.id}})
            break
          case 1:
            this.$router.push({path: '/huoyun/await', query: {orderId: val.id}})
            break
          case 2:
            this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            break
          case 3:
            this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            break
          case 4:
            if (val.treatment) {
              this.$router.push({path: '/huoyun/settlement', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/huoyun/service', query: {orderId: val.id}})
            }
            break
          case 5:
            if (val.review) {
//              this.$router.push({path: '/huoyun', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/huoyun/affirm', query: {orderId: val.id}})
            }
            break
          case 6:
            break
        }
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('HyQueryCarType', {
            'companyId': that.loginData.passenger && that.loginData.passenger.companyId ? that.loginData.passenger.companyId : that.infoData.companyInfo.data.companyId
          })
          that.$store.dispatch('HyUnfilledOrder', {passengerId: that.loginData.passenger.id})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .HuoYun {
    min-height: 100%;
    .content {
      padding-bottom: 0.5rem;
      .banner {
        box-sizing: border-box;
        width: 100%;
        height: 5rem;
        border-radius: 0.333rem;
        padding: 0 1rem;
        overflow: hidden;
      }
      .contentList {
        position: relative;
        min-height: 30vh;
        .row {
          width: 100%;
          box-sizing: border-box;
          position: relative;
          padding: 1rem;
          margin: 1rem 0;
          background: #fff;
          .left {
            display: inline-block;
            width: 5.33rem;
            height: 5.33rem;
            margin-right: 1.3rem;
            float: left;
            border-radius: 50%;
            overflow: auto;
            position: absolute;
            left: 4rem;
            top: 50%;
            transform: translate(-50%, -50%);
            img {
              width: 100%;
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
          }
          .right {
            width: calc(~'100% - 7rem');
            float: right;
            .title {
              margin-bottom: 0.2rem;
              h2 {
                line-height: 1.5rem;
                height: 1.5rem;
              }
              .name {
                display: inline-block;
                float: left;
                font-size: 1.4rem;
                width: 80%;
                color: #333;
                overflow: hidden;
                text-overflow: ellipsis;
              }
              .price {
                float: right;
                font-size: 1rem;
                margin-right: 0.5rem;
                color: #666;
                span {
                  color: #f06553;
                }
              }
            }
            .item {
              font-size: 1rem;
              p {
                color: #666;
              }
            }
          }
          .icon {
            position: absolute;
            right: 0.5rem;
            top: 50%;
            transform: translate(0, -50%);
            i {
              color: rgba(0, 0, 0, 0.1);
            }
          }
        }
        .tishi {
          display: inline-block;
          width: 100%;
          margin-top: 20vh;
          position: absolute;
          text-align: center;
          font-size: 1.25rem;
        }
      }
    }
  }
</style>



