/**订单记录**/
<template>
  <div class="orderRecord">

    <div class="orderRecordNav">
      <xk-order-tabs :data="orderNav"></xk-order-tabs>
    </div>
    <div class="orderRecordContent">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import XkOrderTabs from '../module/orderTabs/src/orderTabs'
  export default {
    components: {
      XkOrderTabs
    },
    name: 'orderRecord',
    computed: {
      ...mapGetters({
        orderNav: 'orderNav'
      })
    },
    data () {
      return {
        statusFn: function (type) {
          let that = this
          that.status = type
        },
        typeAll: {
          action: 0,
          data: []
        }
      }
    },
    mounted: function () {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .orderRecord {
    width: 100vw;
    height: 100vh;
    background-color: #f3f4f5;
    .orderRecordNav {
      height: 3rem;
    }
    //列
    .contentList {
      box-sizing: border-box;
      width: 100%;
      padding: 0 0.5rem;
      overflow: hidden;
      overflow-y: auto;
    }

    .ListItem {
      box-sizing: border-box;
      width: calc(~'100% - 1rem');
      margin: 1rem 0.5rem;
      padding: 0.5rem 1rem;
      background-color: #fff;
      border-radius: 0.33333rem;
      overflow: hidden;
      .itemContent {
        .row {
          height: 2rem;
          line-height: 2rem;
          overflow: hidden;
        }
        .row1 {
          margin-bottom: 0.5rem;
          .itemTime {
            float: left;
            display: inline-block;
            font-size: 1rem;
            color: #333;
          }
          .itemStatus {
            float: right;
            display: inline-block;
            .text {
              float: right;
              color: #999999;
              margin-right: 0.5rem;
            }
            .textRed {
              color: #f06553;
            }
          }
          i {
            font-size: 0.8333rem;
            color: #999999;
            float: right;
          }
        }
        .row2, .row3 {
          color: #666666;
          font-size: 0.83333rem;
          i {
            line-height: 1.8rem;
            font-size: 0.66666rem;
            float: left;
            margin-right: 0.3333rem;
          }
        }
        .row2 {
          i {
            color: #3cafb9;
          }
        }
        .row3 {
          i {
            color: #f06553;
          }
        }
      }
    }

    .NullOrder {
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      .img {
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 50%;
        img {
          width: 100%;
        }
        a {
          margin-top: 1rem;
          padding: 0 1rem;
          line-height: 2rem;
          border-radius: 1rem;
          border: 1px solid #f06553;
          color: #f06553;
          text-align: center;
        }
      }
    }
  }
</style>



