/**home But**/
<template>
  <div class="homeBut" v-if="hideState">
    {{urlFns}}
    <img src="../../../../assets/img/yonghu/huome.png" @click="imgButFn()">
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'xk-homeBut',
    computed: {
      ...mapGetters({
        servesTypeNav: 'servesTypeNav'
      }),
      urlFns: function () {
        let fullPath = this.$route.fullPath
        for (let i = 0; i < this.servesTypeNav.length; i++) {
          if (fullPath === this.servesTypeNav[i].url) {
            this.hideState = false
            return true
          }
        }
        this.hideState = true
      }
    },
    data () {
      return {
        hideState: true
      }
    },
    methods: {
      imgButFn: function () {
        this.$router.push(this.servesTypeNav[0].url)
      },
      urlFn: function () {
        let fullPath = this.$route.path
        for (let i = 0; i < this.servesTypeNav.length; i++) {
          if (this.servesTypeNav[i].url.includes(fullPath)) {
            this.hideState = false
            return false
          }
        }
        if (fullPath.indexOf('/settlement') >= 0) {
          this.hideState = false
          return false
        }
        this.hideState = true
      }
    },
    watch: {
      '$route': 'urlFn'
    },
    mounted: function () {
      this.urlFn()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .homeBut {
    position: fixed;
    left: 0;
    padding-right: 1rem;
    top: 3rem;
    z-index: 250;
    transition: 0.3s all;

    img {
      width: 2.5rem;
    }
  }

  .homeButShow {
    padding-right: 0;
    left: 0;
  }
</style>



