/**好友**/
<template>
  <div class="friendOrder">
    <div class="content">
      <h2>您成功推荐了<span>{{queryRecommendRecord ? queryRecommendRecord.totalElements : '0'}}</span>位好友</h2>
      <div class="recordOrder">
        <div class="Title">
          <table>
            <thead>
            <tr>
              <th>推荐时间</th>
              <th>好友名称</th>
              <th>获得返利</th>
            </tr>
            </thead>
          </table>
        </div>
        <div class="Tbody">
          <table>
            <tbody>
            <tr v-for="(item, index) in queryRecommendRecord.content">
              <td>{{item.introducerDate}}</td>
              <td>{{item.introducerPhone}}</td>
              <td class="rebate">￥{{item.sumMoney.toFixed(1)}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'affirm',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        queryRecommendRecord: 'queryRecommendRecord'
      })
    },
    data () {
      return {
        rows: 100,
        page: 1
      }
    },
    methods: {},
    watch: {
      queryRecommendRecord (val) {
        // console.log(val)
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('queryRecommendRecord', {
            page: that.page,
            rows: that.rows,
            passengerId: that.loginData.passenger.id,
            type: 'upData'
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .friendOrder {
    text-align: center;
    padding-top: 4rem;
    .content {
      h2 {
        color: #333;
        font-size: 1.5rem;
        span {
          color: #f06553;
          font-size: 3rem;
        }
      }
      .recordOrder {
        box-sizing: border-box;
        margin-top: 1.5rem;
        width: 100%;
        padding: 0 1rem;
        table {
          width: 100%;
          border-collapse: collapse; /* 边框合并属性  */
          td, th {
            box-sizing: border-box;
            width: 25%;
            padding: 0 0.6rem;
            height: 2.5rem;
            line-height: 2.5rem;
            text-align: center;
          }
        }
        .Title {
          overflow: hidden;
          th {
            background: #ffd8d6;
            color: #666666;
          }
        }
        .Tbody {
          border: 1px solid #ffd8d6;
          max-height: 25rem;
          overflow: hidden;
          overflow-y: scroll;
          td {
            color: #333333;
          }
          .rebate {
            color: #f06553;
          }
        }
      }
    }
  }
</style>



