import Vue from 'vue'
import Vuex from 'vuex'
import initdata from './modules/initdata'
import mc from './modules/mc'
import daijia from './modules/daijia/daijia'
import paotui from './modules/paotui/paotui'
import zhuanche from './modules/zhuanche/zhuanche'
import zhuanxian from './modules/zhuanxian/zhuanxian'
import huoyun from './modules/huoyun/huoyun'
import userCenter from './modules/userCenter'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    initdata,
    mc,
    daijia,
    zhuanche,
    userCenter,
    zhuanxian,
    huoyun,
    paotui
  }
})



