/**我的钱包**/
<template>
  <div class="meWallet">
    <div class="balance">
      <div class="content">
        <p>账户余额（元）</p>
        <h2>
          {{((postCustomerPreSave.present_balance || 0) + (postCustomerPreSave.non_present_balance || 0)).toFixed(2)}}</h2>
        <span><i class="iconfont icon-gth"></i>支付费用时优先使用</span>
      </div>
    </div>
    <div class="balanceBut" v-if="wxState">
      <span class="But"><router-link to="/recharge">充值</router-link></span>
    </div>
    <div class="page1 page">
      <ul class="pageItme">
        <li>
          <router-link :to="'/capitalRecord'">
            <span class="Icon">
              <i class="iconfont icon-zijinmingxi"></i>
            </span>
            资金流水
            <i class="iconfont icon-jiantou"></i>
          </router-link>
        </li>
        <li>
          <router-link :to="'/coupon/nused'">
            <span class="Icon">
              <i class="iconfont icon-youhuiquanicon"></i>
            </span>
            优惠券
            <i class="iconfont icon-jiantou"></i>
            <span class="text">{{couponData.length}}张</span>
          </router-link>
        </li>
      </ul>
    </div>
    <!-- <div class="page2 page">
      <ul class="pageItme">
        <li>
          <router-link :to="'/invoice'">
            <span class="Icon">
              <i class="iconfont icon-fapiao"></i>
            </span>
            发票申请
            <i class="iconfont icon-jiantou"></i>
          </router-link>
        </li>
      </ul>
    </div> -->
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'meWallet',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        wxState: 'wxState',
        postCustomerPreSave: 'postCustomerPreSave',
        unusedCoupons: 'unusedCoupons'
      })
    },
    data () {
      return {
        couponData: []
      }
    },
    methods: {
      postCustomerPreSaveFn () { // 获取 用户余额
        let that = this
        that.$store.dispatch('postCustomerPreSave', {
          id: that.loginData.passenger.id
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        console.log(that.loginData)
        // that.$store.dispatch('updateLoginData', {passengerId: that.loginData.passenger.id})
        that.$store.dispatch('getCouponType', {
          channel: 'wechat',
          page: 1,
          limit: 80,
          fn (data) {
            if (data.code === 1) {
              let arr = []
              if (data.coupon) {
                data.coupon.forEach(item => {
                  if (item.state === 1) {
                    var endTime = item.end_time
                    if (endTime) {
                      if (new Date().valueOf() < endTime * 1000) {
                        arr.push(item)
                      }
                    } else {
                      arr.push(item)
                    }
                  }
                })
              }
              that.couponData = arr
            }
          }
        })
        that.postCustomerPreSaveFn()
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
