  .meWallet {
    width: 100vw;
    height: 100vh;
    background-color: #f3f4f5;
    .page {
      padding-top: 1rem;
      .pageItme {
        background-color: #fff;
      }
      li {
        box-sizing: border-box;
        padding: 0 1rem;
        margin: 0 1rem;
        border-bottom: 1px solid #f3f4f5;
        height: 3.666666rem;
        line-height: 3.666666rem;
        font-size: 1.25rem;
        a {
          color: #333333;
          width: 100%;
        }
        i {
          float: right;
          line-height: 3.666666rem;
          font-size: 1.2rem;
          color: #999999;
        }
        .Icon {
          float: left;
          margin-right: 0.91666rem;
          i {
            font-size: 1.4rem;
            color: #bbbbbb;
          }
        }
        .text {
          float: right;
          color: #999999;
          font-size: 1.25rem;
          margin-right: 1.025rem;
        }
      }
      li:nth-last-child(1) {
        border-bottom: none;
      }
    }
    .page2 {
      padding-bottom: 1rem;
    }
    .balance {
      text-align: center;
      background-color: #fff;
      padding: 1rem;
      p {
        margin-top: 3.33rem;
        color: #999999;
        font-size: 1rem;
      }
      h2 {
        color: #333333;
        font-size: 2.9rem;
        margin-top: 1.916rem;
        margin-bottom: 1.5rem;
      }
      span {
        font-size: 1rem;
        margin-bottom: 2.833rem;
        color: #999999;
        i {
          font-size: 1rem;
          margin-right: 0.1rem;
          color: #dfdfdf;
        }
      }
    }
    .balanceBut {
      padding: 0 2rem;
      margin-top: 2.5rem;
      margin-bottom: 1.5rem;
      .But {
        width: 100%;
        a {
          line-height: 4.25rem;
          height: 4.25rem;
          background-color: #f06552;
          overflow: hidden;
          border-radius: 0.333rem;
          width: 100%;
          text-align: center;
          color: #fff;
          font-size: 1.58rem;
        }
      }
    }
  }
</style>



