<template>
  <div class="PaoTui">
    <div class="content">
      <div class="banner" v-if="paotuiBanner && paotuiBanner.length>0">
        <xk-roll-banner :data="paotuiBanner"></xk-roll-banner>
      </div>
      <div class="business clear" v-if="businessTypeFn && businessTypeFn.length">
        <div v-for="(item,index) in businessTypeFn" class="list" @click="businessFn(item)">
          <span class="icon">
            <img :src="item.imgUrl">
          </span>
          <p>{{item.name}}</p>
        </div>
      </div>
      <div class="tishi" v-else="">
        该地区暂未开通该业务
      </div>
    </div>
    <xk-unfilled-order-mod
      v-if="unfilledOrderState && unfilledOrderState.paotui && unfilledOrderFn.length>0"
      :data="unfilledOrderFn?unfilledOrderFn:null"
      @close="unfilledOrdercCloseFn"
      :title="'跑腿'"
    ></xk-unfilled-order-mod>
  </div>
</template>

<script>
  import XkRollBanner from '../../module/rollBanner/src/rollBanner'
  import { mapGetters } from 'vuex'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod'

  export default {
    components: {
      XkUnfilledOrderMod,
      XkRollBanner
    },
    name: 'PaoTui',
    computed: {
      ...mapGetters({
        paotuiBanner: 'paotuiBanner',
        loginData: 'loginData',
        positioning: 'positioning',
        unfilledOrderState: 'unfilledOrderState',
        infoData: 'infoData',
        unfilledOrder: 'unfilledOrder'
      }),
      unfilledOrderFn: function () {
        let unfilledOrder = []
        let paotuiData = this.unfilledOrder && this.unfilledOrder.paotui ? this.unfilledOrder.paotui : null
        if (paotuiData && paotuiData.length > 0) {
          let obj = {}
          for (let i = 0; i < paotuiData.length; i++) {
            obj = {
              startAddress: paotuiData[i].startAddress,
              endAddress: paotuiData[i].endAddress,
              serverTime: paotuiData[i].startTime,
              orderId: paotuiData[i].id,
              status: ''
            }
            switch (paotuiData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].id}}
                }
                break
              case 1:
                obj.status = {
                  name: '已派单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].id}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/paotui/server', query: {orderId: paotuiData[i].id}}
                }
                break
              case 3:
                if (paotuiData[i].treatment) {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/paotui/settlement', query: {orderId: paotuiData[i].id}}
                  }
                } else {
                  obj.status = {
                    name: '执行中',
                    router: {path: '/paotui/server', query: {orderId: paotuiData[i].id}}
                  }
                }
                break
              case 4:
                if (paotuiData[i].treatment) {
                  if (paotuiData[i].review) {
                    obj.status = {
                      name: '已完成',
                      router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].id}}
                    }
                  } else {
                    obj.status = {
                      name: '待评价',
                      router: {path: '/paotui/affirm', query: {orderId: paotuiData[i].id}}
                    }
                  }
                }
                break
              case 5:
                obj.status = {
                  name: '已销单',
                  router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      },
      businessTypeFn: function () {
        let that = this
        if (that.infoData && that.infoData.services) {
          let business = that.infoData.services.paotui.services
          let services = []
          for (let i = 0; i < business.length; i++) {
            let obj = {
              id: business[i].id,
              imgUrl: '//' + business[i].typepicture,
              name: business[i].typeName
            }
            services.push(obj)
          }
          return services
        }
      }
    },
    data () {
      return {
        unfilledOrderShowState: true
      }
    },
    watch: {
      loginData (val) {
        let that = this
        if (val && val.passenger) {
          that.$store.dispatch('unfilledOrder', {passengerId: this.$store.state.mc.loginData.passenger.id})
          that.unfilledOrderShowState = true
        }
      }
    },
    methods: {
      businessFn: function (obj) {
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state && that.loginData && that.loginData.passenger) {
            that.$router.push({path: '/paotui/downOrder', query: {id: obj.id}})
            that.loadState = false
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'paotui', state: false})
      }
    },
    mounted: function () {
      let that = this
      if (that.loginData && that.loginData.passenger && that.unfilledOrderShowState) {
        that.$store.dispatch('unfilledOrder', {passengerId: this.loginData.passenger.id})
        that.unfilledOrderShowState = false
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .PaoTui {
    .content {
      box-sizing: border-box;
      width: 100%;
      padding: 0 1rem;
      .banner {
        width: 100%;
        height: 5rem;
        border-radius: 0.333rem;
        overflow: hidden;
      }
      .business {
        width: 100%;
        padding-bottom: 1rem;
        .list {
          box-sizing: border-box;
          width: 33.333%;
          float: left;
          text-align: center;
          margin-top: 3rem;
          .icon {
            width: 4.75rem;
            height: 4.75rem;
            position: relative;
            img {
              width: 100%;
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
          }
          p {
            margin-top: 0.8rem;
            font-size: 1rem;
            color: #333;
          }
        }
      }
      .tishi {
        margin-top: 20vh;
        display: inline-block;
        width: 100%;
        text-align: center;
        font-size: 1.25rem;
      }
    }
  }
</style>



