/**服务中**/
<template>
  <div class="Service">
    <div class="Content">
      <div class="describe">
        <div class="describeContent">
          <h2>{{title}}</h2>
          <h3>{{titleCont}}</h3>
        </div>
        <h4 class="cancelBut" v-if="cancelState" @click="cancelOrderFn">取消订单</h4>
      </div>
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="DjOrderInfo?DjOrderInfo.employ_name:''"
                 :location="DjOrderInfo?DjOrderInfo.book_address:''"
                 :phone="DjOrderInfo?DjOrderInfo.employ_phone:''"
                 :headImg="DjOrderInfo?DjOrderInfo.employ_head_path:''"
      >
      </xk-waiter>
      <xk-Add :time="DjOrderInfo?DjOrderInfo.book_time:''"
              :origin="DjOrderInfo?DjOrderInfo.book_address:''"
              :endPoint="DjOrderInfo?DjOrderInfo.destination:''">
      </xk-Add>
    </div>
    <xk-input-box v-if="reson.state" title="取消订单理由" @click="cancelOrderFn" :closeState="true">
      <div slot="content" class="DjCancelOrder">
        <textarea v-model="reson.val" placeholder="请输入取消订单理由">
        </textarea>
      </div>
    </xk-input-box>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkAdd from '../../module/add/src/add.vue'
  import { mapGetters } from 'vuex'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import XkInputBox from '../../module/inputBox/src/inputBox'

  export default {
    components: {
      XkInputBox,
      XkHintBox,
      XkWaiter,
      XkAdd
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        DjOrderInfo: 'DjOrderInfo'
      })
    },
    watch: {
      DjOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passenger_id) {
          that.$router.push({path: '/daijia?app_key=' + window.configure.appKey})
          return
        }
        if (val.status === 10) {
          that.title = (val.gender === '男' ? '服务人员' : '服务人员') + '已接单'
          that.titleCont = '正在向您狂奔...'
          that.cancelState = true
        } else if (val.status === 15) {
          that.title = (val.gender === '男' ? '服务人员' : '服务人员') + '正在前往预约地'
          that.titleCont = '请您耐心等待...'
          that.cancelState = true
        } else if (val.status === 20) {
          that.title = (val.gender === '男' ? '服务人员' : '服务人员') + '已到达预约地'
          that.titleCont = '准备开车啦...'
          that.cancelState = true
        } else if (val.status === 25) {
          that.title = '正在前往目的地...'
          that.titleCont = '请系上您的安全带，祝您旅途愉快！'
          that.cancelState = false
        } else if (val.status === 30) {
          that.$router.push({path: '/daijia/settlement', query: {order_id: that.$route.query.order_id}})
          that.cancelState = false
          return false
        } else if (val.status === 35) {
          that.cancelState = false
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/daijia/affirm', query: {order_id: that.$route.query.order_id}})
          }, 1500)
          return
        } else if (val.status === 45) {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/daijia?app_key=' + window.configure.appKey})
          }, 1500)
          return
        }
        setTimeout(function () {
          if (that.$route.fullPath.split('?')[0] === '/daijia/service') {
            that.$store.dispatch('DjOrderInfo', {order_id: that.$route.query.order_id})
          }
        }, 3000)
      }
    },
    data () {
      return {
        title: '',
        titleCont: '',
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        cancelState: false,
        reson: {
          state: false,
          val: ''
        }
      }
    },
    methods: {
      cancelOrderFn: function (val) {
        let that = this
        this.reson.state = !this.reson.state
        console.log('取消订单', this.reson.state)
        if (val === 'affirm') {
          if (this.reson.val !== '') {
            that.loadState = true
            this.$store.dispatch('putCancelOrder', {
              order_id: that.$route.query.order_id,
              remark: this.reson.val,
              fn: (state) => {
                that.loadState = false
                if (state && state.code === 1) {
                  this.reson.state = false
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单成功！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/daijia?app_key=' + window.configure.appKey})
                  }, 1500)
                }
              }
            })
          } else {
            this.reson.state = true
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请输入销单理由！'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        }
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('DjOrderInfo', {order_id: that.$route.query.order_id})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Service {
    .Content {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      .describe {
        height: 16.666rem;
        background-color: #fff;
        position: relative;
        .describeContent {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
        h2 {
          font-size: 2rem;
          color: #333;
          text-align: center;
          margin-top: 1rem;
        }
        h3 {
          color: #999999;
          font-size: 1.25rem;
          text-align: center;
          margin-top: 1.4rem;
        }
        .cancelBut {
          position: absolute;
          bottom: -10px;
          left: 50%;
          transform: translate(-50%, -50%);
          color: #f06553;
          font-size: 0.8333rem;
          text-align: center;
        }
      }
      .Add {
        margin-top: 1rem;
      }
    }
    .DjCancelOrder {
      border: 1px solid #eee;
      height: 10rem;
      textarea {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        border: none;
        resize: none;
        font-size: 1rem;
        padding: 0.5rem;
      }
    }
  }
</style>



