/** 代驾**/
<template>
  <div class="recordDaiJia">
    <div class="daijiaContent">
      <ul class="contentList" v-if="DjQueryAllOrder.val.length>0">
        <xk-record-order-mod
          v-for="(time,index) in allOrderFn"
          :startAddress="time.startAddress"
          :endAddress="time.endAddress"
          :serverTime="time.serverTime"
          :status="time.status"
          :orderId="time.order_id"
          :key="index"
        >
        </xk-record-order-mod>
      </ul>
      <div class="NullOrder" v-else="">
        <div class="img">
          <img src="../../assets/img/yonghu/orderImg.png"/>
          <span class="imgBut"><router-link to="/daijia">去下单</router-link></span>
        </div>
      </div>
      <div class="loadDataBut" v-if="DjQueryAllOrder&&DjQueryAllOrder.val.length<DjQueryAllOrder.total">
        <span @click="loadDataButFn">点击加载更多</span>
      </div>
    </div>
    <!--<xk-upload-mod :state="loadState"></xk-upload-mod>-->
  </div>
</template>

<script>
  import XkRecordOrderMod from '../module/recordOrderMod/src/recordOrderMod'
  import { mapGetters } from 'vuex'
  import Toucth from '../../assets/js/method'
  import XkUploadMod from '../module/uploadMod/src/uploadMod'

  export default {
    components: {
      XkUploadMod,
      XkRecordOrderMod
    },
    name: 'recordDaiJia',
    computed: {
      ...mapGetters({
        DjQueryAllOrder: 'DjQueryAllOrder'
      }),
      allOrderFn () {
        let unfilledOrder = []
        let daijiaData = this.DjQueryAllOrder ? this.DjQueryAllOrder.val : null
        // console.log(daijiaData)
        if (daijiaData && daijiaData.length > 0) {
          let obj = {}
          for (let i = 0; i < daijiaData.length; i++) {
            obj = {
              startAddress: daijiaData[i].book_address,
              endAddress: daijiaData[i].destination,
              serverTime: Toucth.TimestampDate(daijiaData[i].book_time),
              order_id: daijiaData[i].order_id,
              status: ''
            }
            switch (daijiaData[i].status) {
              case 1:
                obj.status = {
                  name: '新单',
                  router: {path: '/daijia/await', query: {order_id: daijiaData[i].id}}
                }
                break
              case 5:
                obj.status = {
                  name: '已派单',
                  router: {path: '/daijia/await', query: {order_id: daijiaData[i].id}}
                }
                break
              case 10:
                obj.status = {
                  name: '已接单',
                  router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                }
                break
              case 15:
                obj.status = {
                  name: '前往预约地',
                  router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                }
                break
              case 20:
                obj.status = {
                  name: '到达预约地',
                  router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                }
                break
              case 25:
                obj.status = {
                  name: '计费服务中',
                  router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                }
                break
              case 30:
                obj.status = {
                  name: '未支付',
                  router: {path: '/daijia/settlement', query: {order_id: daijiaData[i].id}}
                }
                this.$router.push({path: '/daijia/settlement', query: {order_id: daijiaData[i].id}})
                break
              case 35:
                obj.status = {
                  name: '已支付',
                  router: {path: '/daijia/affirm', query: {order_id: daijiaData[i].id}}
                }
                break
              case 40:
                obj.status = {
                  name: '已评价',
                  router: {path: '/daijia/affirm', query: {order_id: daijiaData[i].id}}
                }
                break
              case 45:
                obj.status = {
                  name: '已销单',
                  router: {path: '/daijia/checkOrder', query: {order_id: daijiaData[i].id}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      }
    },
    data () {
      return {
        page: 1,
        rows: 10,
        loadState: false,
        pageState: true
      }
    },
    methods: {
      loadDataButFn: function () { //  获取更多订单
        let that = this
        that.page++
        that.loadState = true
        that.$store.dispatch('DjQueryAllOrder', {
          'serviceType': 'daijia',
          'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
          'page': that.page,
          'limit': that.rows,
          fn: (data) => {
            if (data) {
              that.loadState = false
            }
          }
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.loadState = true
          setTimeout(() => {
            that.$store.dispatch('DjQueryAllOrder', {
              'business': 'daijia',
              'page': 1,
              'limit': 10,
              'type': 'upData',
              fn: (state) => {
                if (state) {
                  that.loadState = false
                }
              }
            })
          }, 1000)
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .recordDaiJia {
    .loadDataBut {
      box-sizing: border-box;
      width: 100%;
      line-height: 3.5rem;
      padding: 0 1rem;
      margin-bottom: 1rem;
      span {
        width: 100%;
        background-color: #fff;
        border-radius: 0.333rem;
        text-align: center;
      }
    }
  }
</style>



