import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  paotuType (obj, callback) { // 获取类型
    var params = {
      'app_key': InitData.appKey,
      companyId: '',
      employToken: ''
    }
    var url = InitData.serverUrl + '/news/weixin'
    Vue.http.get(url, params).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  budgetCost (obj, callback) { // 预算费用
    var params = {
      'app_key': InitData.appKey,
      areaId: obj.areaId,
      time: obj.time,
      mileage: obj.mileage
    }
    var url = InitData.weixinUrl + '/v5/paotui/calculationPrice'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { // 获取优惠券
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/v5/paotui/getCoupons'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  placeAnOrder (obj, callback) { // 下单
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey,
      'prePay': false,
      'payType': 'balance', // balance 余额 sign 签单
      'passengerId': obj.passengerId,
      'passengerName': obj.passengerName,
      'passengerPhone': obj.passengerPhone,
      'companyName': obj.companyName,
      'companyId': obj.companyId,
      'channelName': obj.channelName,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'deliverAddress': obj.deliverAddress,
      'deliverLat': obj.deliverLat,
      'deliverLng': obj.deliverLng,
      'startTime': obj.startTime,
      'startPrice': obj.startPrice,
      'mileagePrice': obj.mileagePrice,
      'travelTimePrice': obj.travelTimePrice,
      'mileage': obj.mileage,
      'travelTime': obj.travelTime,
      'shouldPay': obj.shouldPay,
      'couponId': obj.couponId,
      'photos': obj.photos,
      'orderTypeId': obj.orderTypeId,
      'content': obj.content,
      'recipients': obj.recipients,
      'phone': obj.phone,
      'weight': obj.weight,
      'buyNeeds': obj.buyNeeds,
      'goodsCash': obj.goodsCash,
      'thanksCash': obj.thanksCash,
      'addServices': obj.addServices,
      'tips': obj.tips,
      'startDetailed': obj.startDetailed,
      'endDetailed': obj.endDetailed
    }
    var url = InitData.weixinUrl + '/v5/paotui/createByPassenger'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data && data.body.success && obj.fn) {
          obj.fn(data.body.orderId)
        } else {
          obj.fn(false)
        }
        callback(data.body)
      }
    )
  },
  queryOrder (obj, callback) { // 获取单个订单
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }

    function ajax () {
      var url = InitData.weixinUrl + '/api/errand/v2/orders/findOne'
      Vue.http.get(url, {params}).then(
        data => {
          callback(data.body)
        },
        data => {
          ajax()
        }
      )
    }

    ajax()
  },
  payData (obj, callback) { // 获取支付信息
    var params = {
      'app_key': InitData.appKey,
      orderId: obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  toPay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'payType': obj.payType // balance 余额 sign 签单
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  review (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'score': obj.score,
      'content': obj.content
    }
    var url = InitData.weixinUrl + '/api/errand/v2/orders/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/pt/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      orderId: obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/paotui/getReview'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 微信支付
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/paotui/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: window.configure.WXappId, // 必填，公众号的唯一标识
            timeStamp: content.timestamp, // 必填，生成签名的时间戳
            nonceStr: content.nonceStr, // 必填，生成签名的随机串
            signature: content.signature, // 必填，签名，见附录1
            jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
          })

          WX.ready(function () {
            // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
            WX.chooseWXPay({
              timestamp: content.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              nonceStr: content.nonceStr, // 支付签名随机串，不长于 32 位
              package: content.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
              signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              paySign: content.paySign, // 支付签名
              success: function (res) {
                // 支付成功后的回调函数
                callback('支付成功')
              }
            })
          })
          WX.error(function (res) {
            callback('支付失败')
            // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
          })
        }
      }
    )
  }
}



