import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  lineStartCity (obj, callback) { // 查询开始城市
    var params = {
      'app_key': InitData.appKey
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findStartCitys'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  lineEndCity (obj, callback) { // 查询单个线路
    var params = {
      'app_key': InitData.appKey,
      'startCity': obj.startCity,
      'startAddress': obj.startAddress
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findEndByStart'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  budgetMoney (obj, callback) { // 预算费用
    var params = {
      'app_key': InitData.appKey,
      'areaId': obj.areaId,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'endLat': obj.endLat,
      'endLng': obj.endLng
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/budgetedExpense'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { // 获取 优惠券
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/findZXCouponV4'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  findOrder (obj, callback) { // 获取 单个订单信息
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/findZXOrder'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payInfo (obj, callback) { // 获取 单个订单信息
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/prepay'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  makeAnAppointment (obj, callback) { // 下单
    var params = {
      'app_key': InitData.appKey,
      'ack': InitData.acKey,
      'orderType': obj.orderType, // 0 拼车 1 包车 2 寄货
      'passengerId': obj.passengerId,
      'passengerPhone': obj.passengerPhone,
      'lineId': obj.lineId,
      'startAddress': obj.startAddress,
      'startLat': obj.startLat,
      'startLng': obj.startLng,
      'endAddress': obj.endAddress,
      'endLat': obj.endLat,
      'lat': obj.lat,
      'lng': obj.lng,
      'endLng': obj.endLng,
      'startTime': obj.startTime,
      'budgetPay': obj.budgetPay,
      'memo': obj.memo,
      'couponId': obj.couponId,
      'peopleNumber': obj.peopleNumber,
      'takeDeliveryName': obj.takeDeliveryName,
      'takeDeliveryPhone': obj.takeDeliveryPhone,
      'openId': obj.openId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/makeAnZXAppointmentV5'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  toPay (obj, callback) { // 下单
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/doPay'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  review (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'score': obj.score,
      'content': obj.content
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/review'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  cancelOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'reson': obj.reson
    }
    var url = InitData.weixinUrl + '/h5/v4/zhuanxian/cancelOrder'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/getReview'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  weixinPay (obj, callback) { // 微信支付
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/zhuanxian/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: window.configure.WXappId, // 必填，公众号的唯一标识
            timeStamp: content.timestamp, // 必填，生成签名的时间戳
            nonceStr: content.nonceStr, // 必填，生成签名的随机串
            signature: content.signature, // 必填，签名，见附录1
            jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
          })

          WX.ready(function () {
            // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
            WX.chooseWXPay({
              timestamp: content.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              nonceStr: content.nonceStr, // 支付签名随机串，不长于 32 位
              package: content.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
              signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              paySign: content.paySign, // 支付签名
              success: function (res) {
                // 支付成功后的回调函数
                callback('支付成功')
              }
            })
          })
          WX.error(function (res) {
            callback('支付失败')
            // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
          })
        }
      }
    )
  }
}



