/**支付费用**/
<template>
  <div class="Settlement">
    <div class="Settlementcont">
      <div class="feiyong">
        <div class="title">
          <h2>支付费用</h2>
          <div class="line"></div>
        </div>
        <div class="content">
        <span class="sum">
          <span class="daxie">{{PtPayData ? PtPayData.payMoney.toFixed(1).split('.')[0] : '00'}}</span>
          <span class="xsd">.</span>
          <span class="xiaoye">{{PtPayData ? PtPayData.payMoney.toFixed(1).split('.')[1] : '00'}}</span>
          <span class="danwei">元</span>
        </span>
        </div>
      </div>
      <div class="detailed">
        <span @click="detailsBoxFn">费用详情<i class="iconfont  icon-iconfontyiwen"></i></span>
      </div>
      <div class="payType">
        <div class="title">
          <h2>支付费用</h2>
          <div class="line"></div>
        </div>
        <div class="payTypeContent">
          <ul>
            <li @click="payTypeFn(1)"
                v-if="true">
              <span>
                <i class="iconfont icon-yue Icon"></i>
                余额支付
                <span class="select " :class="payType==1?'selectAction':''">
                  <i class="iconfont icon-chenggong "></i>
                </span>
              </span>
            </li>
            <li @click="payTypeFn(2)" v-if="wxState">
              <span>
                <i class="iconfont icon-weixin Icon"></i>
                微信支付
                <span class="select" :class="payType==2?'selectAction':''">
                  <i class="iconfont icon-chenggong "></i>
                </span>
              </span>
            </li>
            <li @click="payTypeFn(3)" v-if="PtPayData&&PtPayData.overdraw">
              <span>
                <i class="iconfont icon-qiandan-copy Icon"></i>
                签单支付
                <span class="select" :class="payType==3?'selectAction':''">
                  <i class="iconfont icon-chenggong "></i>
                </span>
              </span>
            </li>
          </ul>
        </div>
      </div>
      <div class="submitBut" @click="toPayFn">
        <span>立即支付</span>
      </div>
    </div>
    <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <ul>
          <li v-for="itme in payDataFn" v-if="itme.val != 0" class="clear">
            <span class="name" :class="itme.red?'sum':''">{{itme.name}}</span>
            <span class="val"><span class="sum">{{itme.val}}</span><span class="unit">元</span></span>
            <span class="time" v-if="itme.time">{{itme.time}}<span class="unit">分钟</span></span>
          </li>
        </ul>
      </div>
    </Xk-detailsBox>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
  </div>
</template>

<script>
  import XkdetailsBox from '../../module/detailsBox/src/detailsBox.vue'
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod
    },
    component: {
      XkdetailsBox
    },
    name: 'Settlement',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        PtQueryOrder: 'PtQueryOrder',
        wxState: 'wxState',
        PtPayData: 'PtPayData'
      }),
      payDataFn: function () {
        let that = this
        let obj = []
        if (that.PtPayData) {
          if (that.PtPayData.payMoney) {
            obj.push({
              name: '服务费',
              val: (that.PtPayData.realCash - that.PtPayData.otherMoney - that.PtPayData.yuanchengMoney).toFixed(1)
            })
          }
          if (that.PtPayData.goodsCash) {
            obj.push({
              name: '商品价格',
              val: that.PtPayData.goodsCash.toFixed(1)
            })
          }
          if (that.PtPayData.guoluMoney) {
            obj.push({
              name: '过路费',
              val: that.PtPayData.guoluMoney.toFixed(1)
            })
          }
          if (that.PtPayData.otherMoney) {
            obj.push({
              name: '其他费用',
              val: that.PtPayData.otherMoney.toFixed(1)
            })
          }
          if (that.PtPayData.thanksCash) {
            obj.push({
              name: '感谢费',
              val: that.PtPayData.thanksCash.toFixed(1)
            })
          }
          if (that.PtPayData.yuanchengMoney) {
            obj.push({
              name: '远程费',
              val: that.PtPayData.yuanchengMoney.toFixed(1)
            })
          }
          if (that.PtPayData.couponMoney) {
            obj.push({
              name: '优惠券',
              red: true,
              val: (-that.PtPayData.couponMoney) + '.00'
            })
          }
        }
        return obj
      },
      payMoneyFn: function () {
        let obj = 0
        if (this.PtQueryOrder) {
          if (this.PtQueryOrder.goodsCash) {
            obj += this.PtQueryOrder.goodsCash
          }
          if (this.PtQueryOrder.realPay) {
            obj += this.PtQueryOrder.realPay
          }
          if (this.PtQueryOrder.thanksCash) {
            obj += this.PtQueryOrder.thanksCash
          }
        }
        return obj
      }
    },
    data () {
      return {
        payType: 1, // 支付方式
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    watch: {
      PtQueryOrder (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/paotui')
          return
        }
        that.loadState = false
        if (val.status === 3 && val.treatment) {
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/paotui/settlement') {
              that.$store.dispatch('PtQueryOrder', {
                orderId: Toucth.AcquireUrlParameter().orderId
              })
            }
          }, 3000)
        } else if (val.status === 4) {
          that.$router.push({path: '/paotui/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
      }
    },
    methods: {
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      },
      payTypeFn: function (val) { // 支付方式事件
        let that = this
        switch (val) {
          case 1:
            that.payType = 1
            break
          case 2:
            that.payType = 2
            break
          case 3:
            that.payType = 3
            break
        }
      },
      toPayFn: function () {
        let that = this
        that.loadState = true
        if (that.payType === 1 || that.payType === 3) {
          this.$store.dispatch('PtToPay', {
            'orderId': Toucth.AcquireUrlParameter().orderId,
            'payType': that.payType === 1 ? 'balance' : 'sign', // balance 余额 sign 签单
            fn: (data) => {
              that.loadState = false
              if (data.code === 0) {
                this.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '支付成功！'
                }
                setTimeout(() => {
                  this.hintBox.stateShow = false
                  that.$router.push({path: '/paotui/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
                }, 1500)
              }
              if (data.code === -47) {
                this.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '余额不足！'
                }
                setTimeout(() => {
                  this.hintBox.stateShow = false
                }, 1500)
              }
            }
          })
        }
        if (that.payType === 2) {
          this.$store.dispatch('PtWXPay', {
            'orderId': Toucth.AcquireUrlParameter().orderId,
            'openid': that.loginData.openId,
            'WXappId': window.configure.WXappId,
            fn: function (data) {
              that.loadState = false
              if (data === '支付成功') {
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '支付成功！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                  that.$store.dispatch('unfilledOrderState', {type: 'paotui', state: false})
                  that.$router.push({path: '/paotui/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
                }, 1500)
              } else {
                that.hintBox = {
                  stateShow: true,
                  state: 'yes',
                  content: '支付失败！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
              }
            }
          })
        }
      }
    },
    mounted: function () {
      let that = this
      that.loadState = true
      that.$store.dispatch('checkLogin', (state) => {
        if (state && that.loginData && that.loginData.passenger) {
          that.$store.dispatch('updateLoginData', {passengerId: that.loginData.passenger.id})
          that.$store.dispatch('PtQueryOrder', {
            orderId: Toucth.AcquireUrlParameter().orderId
          })
          that.$store.dispatch('PtPayData', {
            orderId: Toucth.AcquireUrlParameter().orderId
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Settlement {
    .Settlementcont {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      padding: 1rem;
    }
    .title {
      height: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      position: relative;
      h2 {
        display: inline-block;
        color: #333;
        font-size: 1.6rem;
        padding: 0 0.8rem;
        background-color: #fff;
        z-index: 10;
        position: relative;
      }
      .line {
        width: 100%;
        height: 1px;
        background-color: #eee;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .feiyong {
      background-color: #fff;
      padding: 1rem;
      padding-top: 1.5rem;
      .content {
        text-align: center;
        padding-top: 1.8rem;
        .sum {
          color: #f06553;
          .xiaoye, .danwei, .xsd {
            font-size: 1.5rem;
          }
          .daxie {
            font-size: 3rem;
          }
        }
      }
    }
    .detailed {
      background-color: #fff;
      text-align: center;
      line-height: 1.5rem;
      height: 1.5rem;
      color: #999999;
      padding: 0.5rem 0 1.8rem 0;
      font-size: 1rem;
      span {
        display: inline-block;
        i {
          font-size: 1.3rem;
          padding-left: 0.3rem;
          color: #999999;
        }
      }

    }
    .payType {
      background-color: #fff;
      margin-top: 1rem;
      padding: 1rem;
      padding-top: 1.5rem;
      .payTypeContent {
        padding-top: 1rem;
        ul {
          width: 100%;
          li {
            box-sizing: border-box;
            line-height: 3.6666rem;
            height: 3.6666rem;
            font-size: 1.3rem;
            color: #434343;
            width: 100%;
            position: relative;
            padding: 0 1rem;
            > span {
              width: 100%;
            }
            i {
              font-size: 1.5rem;
            }
            .Icon {
              margin-right: 0.5rem;
            }
            .select {
              display: inline-block;
              width: 1.8rem;
              height: 1.8rem;
              box-sizing: border-box;
              overflow: hidden;
              border-radius: 50%;
              border: 1px solid #cccccc;
              position: absolute;
              right: 1.1666rem;
              top: 50%;
              transform: translate(0, -50%);
              transition: 0.3s all;
              i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                font-size: 0.3rem;
                opacity: 0;
                color: #fff;
                transition: 0.3s all;
              }
            }
            .selectAction {
              background-color: #f06553;
              border-color: #f06553;
              i {
                opacity: 1;
              }
            }
          }
          .icon-yue {
            color: #fe5459;
          }
          .icon-weixin {
            color: #00c801;
          }
          .icon-qiandan-copy {
            color: #ffb400;
          }
        }
      }
    }
    .submitBut {
      box-sizing: border-box;
      width: 100%;
      margin-top: 1rem;
      margin-bottom: 1rem;
      span {
        width: 100%;
        line-height: 3.666rem;
        height: 3.666rem;
        border-radius: 0.333rem;
        background-color: #f06553;
        text-align: center;
        color: #fff;
        font-size: 1.4rem;
      }
    }
    .costBox {
      ul {
        li {
          height: 2rem;
          line-height: 2rem;
          color: #999999;
          .name {
            float: left;
          }
          .time, .val {
            float: right;
          }
          .time {
            margin-right: 0.8rem;
            color: #333333;
          }
          .sum {
            color: #f06553;
          }
          .unit {
            margin-left: 0.3rem;
          }
        }
      }
    }
  }
</style>



