/**服务中**/
<template>
  <div class="Service">
    <div class="Content">
      <div class="describe">
        <div class="describeContent">
          <h2>{{title}}</h2>
          <h3 style="width: 60%;word-wrap: break-word;text-align: center;margin: 10px auto">{{titleCont}}</h3>
        </div>
      </div>
      <xk-waiter
        :src="v5FindZcOrderById?v5FindZcOrderById.employ_head_path:'../../../../assets/img/yonghu/touxiang.jpg'"
        :name="v5FindZcOrderById?v5FindZcOrderById.employ_name:''"
        :carNo="v5FindZcOrderById?v5FindZcOrderById.vehicle_no:''"
        :carName="v5FindZcOrderById?v5FindZcOrderById.vehicle_brand:''"
        :carColor="v5FindZcOrderById?v5FindZcOrderById.vehicle_color:''"
        :carType="v5FindZcOrderById?v5FindZcOrderById.vehicle_type:''"
        :headImg="v5FindZcOrderById?v5FindZcOrderById.employ_head_path:''"
        :phone="v5FindZcOrderById?v5FindZcOrderById.employ_phone:''"
      ></xk-waiter>
      <xk-Add :time="v5FindZcOrderById?v5FindZcOrderById.book_time:''"
              :origin="v5FindZcOrderById?v5FindZcOrderById.book_address:''"
              :endPoint="v5FindZcOrderById?v5FindZcOrderById.destination:''"></xk-Add>
    </div>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkAdd from '../../module/add/src/add.vue'
  import {mapGetters} from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkWaiter,
      XkAdd
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        // ZcQueryOrderInfo: 'ZcQueryOrderInfo',
        v5FindZcOrderById: 'v5FindZcOrderById'
      })
    },
    watch: {
      v5FindZcOrderById (val) {
        let that = this
        val.book_time = Toucth.TimestampDate(val.book_time)
        if (val.status === 5) { // 已派单
          that.title = '已为您派单'
          that.titleCont = '请耐心等待'
        }
        if (val.status === 5) { // 已接单
          that.title = '司机已接单'
          that.titleCont = '司机正在向您狂奔！'
        }
        if (val.status === 15) { // 前往预约地
          that.title = '司机正在前往预约地'
          that.titleCont = `前往【${val.book_address}】`
        }
        if (val.status === 20) { // 到达预约地
          that.title = '司机已到达预约地'
          that.titleCont = `到达【${val.book_address}】`
        }
        if (val.status === 25) { // 前往目的地
          that.title = '司机正在前往目的地'
          that.titleCont = `前往【${val.destination}】`
        }
        if (val.status === 28) { // 中途等待
          that.title = '中途等待'
          that.titleCont = '正在等候乘客，请尽快上车'
        }
        if (val.status === 30) { // 到达目的地
          that.title = ' 到达目的地'
          that.titleCont = `到达【${val.destination}】`
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/settlement', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          }, 1500)
        }
        if (val.status === 35) { // 已结算
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已结算！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/affirm'})
            return false
          }, 1500)
        }
        if (val.status === 40) { // 已结算
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已评价！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/checkOrder'})
            return false
          }, 1500)
        }
        if (val.status === 40) { // 已销单
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被取消！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche'})
            return false
          }, 1500)
        }
        setTimeout(function () {
          if (that.$route.fullPath.split('?')[0] === '/zhuanche/service') {
            that.getOrderInforByid()
          }
        }, 3000)
      }
    },
    data () {
      return {
        title: '司机已接单',
        titleCont: '司机正在向你狂奔...',
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    methods: {
      getOrderInforByid () {
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('findZcOrderById', {
            order_id: that.$route.query.orderId,
            fn (data) {
              if (data.code === 1) {
                resolve(data)
              } else {
                reject(data)
              }
            }
          })
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          // that.$store.dispatch('ZcQueryOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
          that.getOrderInforByid()
        }
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Service {
    .Content {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      .describe {
        height: 16.666rem;
        background-color: #fff;
        position: relative;
        .describeContent {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
        }
        h2 {
          display: inline-block;
          width: 100%;
          font-size: 2rem;
          color: #333;
          text-align: center;
          margin-top: 1rem;
        }
        h3 {
          width: 100%;
          color: #999999;
          font-size: 1.25rem;
          text-align: center;
          margin-top: 1.4rem;
        }
      }
      .Add {
        margin-top: 1rem;
      }
    }
  }
</style>



