/**登陆**/
<template>
  <div class="login" :class="!login?'showLogin':''">
    <div class="loginContent">
      <!--手机号-->
      <div class="row row1 clear">
        <div class="content">
          <span class="Region">
              <span class="Regionbutton" @click="RegionButFn">{{phoneRegion}}</span>
              <ul :class="phoneRegionUl?'showUl':''">
                <li @click="RegionButFn('中国 +86')">中国 +86</li>
              </ul>
            </span>
          <span class="phone">
              <input placeholder="手机号" v-model="phoneNo"
                     type="tel" :maxlength="phoneLength" @input="loginStateFn(1)"/>
            </span>
        </div>
      </div>
      <!--图形验证码-->
      <div class="row row3 clear" v-if="step===1">
        <div class="top clear">
                  <span class="imgCode">
                    <input placeholder="图形验证码" :maxlength="4" v-model="imgCode" @input="loginStateFn(2)"/>
                  </span>
          <span class="img" @click="getImgCodeFn">
                    <xk-img-code :code="imgCodeUrl"></xk-img-code>
                  </span>
        </div>
        <span class="bottom">看不清楚？点击图片刷新</span>
      </div>
      <!--短信验证码-->
      <div class="row row2 clear" v-if="step===2">
        <span class="numCode">
          <input placeholder="短信验证码" v-model="smsCode" type="tel" maxlength="4" @input="loginStateFn(3)"/>
        </span>
        <span class="numCodeBut" :class="phoneCodeText==='获取短信验证码'?'numCodeButShow':''" @click="getSmsCodeFn">
          {{phoneCodeText}}
        </span>
      </div>
      <!--忘记密码-->
      <div class="row row5 clear" v-if="step===4">
        <span class="password">
          <input placeholder="重置密码" v-model="password" type="password" maxlength="10"
                 @input="loginStateFn(4)"/>
        </span>
      </div>
      <!--设置密码-->
      <div class="row row5 clear" v-if="step===5">
        <span class="password">
          <input placeholder="设置6-10位密码" v-model="password" type="password" maxlength="10"
                 @input="loginStateFn(5)"/>
        </span>
      </div>
      <!--登陆密码-->
      <div class="row row5 clear" v-if="step===3" style="height:auto;">
        <span class="password">
          <input placeholder="登录密码" v-model="password" type="password"
                 @input="loginStateFn(4)" maxlength="10"/>
        </span>
        <div class="textBtn" style="width: 100%;display: flex;justify-content: space-between">
          <div class="forget" @click="forgetFn">
            忘记密码
          </div>
          <div class="nextStep" @click="nextStep">
            下一步
          </div>
        </div>
      </div>
      <!--登陆密码，老用户，有密码，关双因子-->
      <div class="row row5 clear" v-if="step===6" style="height:auto;">
        <span class="password">
          <input placeholder="登录密码" v-model="password" type="password"
                 @input="loginStateFn(6)" maxlength="10"/>
        </span>
        <div class="textBtn" style="width: 100%;display: flex;justify-content: space-between">
          <div class="forget" @click="forgetFn">
            忘记密码
          </div>
        </div>
      </div>
      <!--用户协议-->
      <div class="row row4" @click="userxieyiFn">
        <p>点击登录即表示已同意<span class="protocol">《用户协议》</span></p>
      </div>
      <!--登录按钮-->
      <div class="loginBut" :class="loginState?'loginButShow':''" @click="loginButFn">
        {{forgetState ? '确认' : '登录'}}
      </div>
    </div>
    <xk-alert-box :text="alertText" v-if="alertText"></xk-alert-box>
    <div class="Module" v-if="!login" @click="moduleFn"></div>
    <xk-hint-box :state="hintBox.state" :content="hintBox.content" v-if="hintBox.stateShow"></xk-hint-box>
  </div>
</template>

<script>
  import Toucth from '../../../../assets/js/method'
  import {mapGetters} from 'vuex'
  import XkAlertBox from '../../alertBox/src/alertBox'
  import XkHintBox from '../../hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkAlertBox
    },
    name: 'xk-login',
    data () {
      return {
        forgetState: false,
        phoneLength: '11', // 电话输入框长度
        phoneRegion: '中国 +86', // 区号
        phoneRegionUl: false, // 区号选择
        phoneNo: '', // 电话
        phoneAes: null, // 加密电话
        smsCode: '', // 数字验证码
        imgCode: '', // 图形验证码
        password: '', // 登录密码
        imgCodeUrl: null, // 图形验证码链接
        loginState: false, // 登陆按钮
        alertText: '', // 提示信息
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        time: '',
        country: '中国', // 国家
        phoneCodeText: '获取短信验证码', // 短信验证码 按钮 文本
        imgCodeState: false, // 图形验证码是否正确
        newUserState: false, // 是否是新号
        step: null, // 1 图片验证码  2 短信验证码
        factor: null, // 双因子
        hasPassword: true, // 老用户是否有密码
        showSmsInput: true
      }
    },
    computed: {
      ...mapGetters({
        positioning: 'positioning',
        companyInfo: 'companyInfo',
        infoData: 'infoData',
        login: 'login',
        loginData: 'loginData'
      }),
      imgUrlFn: function () {
        this.$store.dispatch('validateState', {state: true, text: '获取验证码'})
      },
      numCodeButStateFn: function () {
        if (Toucth.checkPhone(this.phoneNo)) {
          return true
        }
        return false
      }
    },
    watch: {},
    mounted () {
      if (this.$route.query.tel) {
        this.phoneNo = this.$route.query.tel
        this.loginStateFn(1)
      }
    },
    methods: {
      loginButFn: function () { // 登陆按钮
        if (!this.loginState) {
          return
        }
        if (this.newUserState || this.forgetState) {  // 新号 设置密码
          this.postRegisterFn()
        } else { // 老号 密码登录
          if (this.hasPassword) {
            this.postEmployLoginFn()
          } else {
            if (this.factor === 1) {
              this.postRegisterFn()
            } else {
              this.postEmployLoginFn()
            }
          }
        }
      },
      postEmployLoginFn () { // 密码登录
        let that = this
        let pw = null
        if (that.password) {
          pw = that.$utils.aesEncryptCode(that.password)
        }
        that.$store.dispatch('postLogin', {
          phone: that.phoneAes,
          country: that.country,
          open_id: window.configure.open_id,
          password: pw,
          fn (state, code) {
            console.log(state, code)
            that.loginState = false
            if (code === 20051 || code === 20048) {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '密码错误'
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
                return false
              }, 1500)
              return false
            }
            that.phoneNo = ''
            that.password = ''
            that.imgCode = ''
            that.smsCode = ''
            that.step = null
          }
        })
      },
      RegionButFn: function (val) { // 切换区号
        let that = this
        if (typeof val === 'string') {
          that.phoneRegion = val
        }
        that.phoneRegionUl = !that.phoneRegionUl
      },
      getSmsCodeFn: function () { // 获取 短信验证码
        let that = this
        if (that.phoneCodeText !== '获取短信验证码') {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您登陆过于频繁，请两分钟后再登陆'
          }
          setTimeout(function () {
            that.hintBox.stateShow = false
          }, 10000)
          that.showSmsInput = false
          return
        }
        that.showSmsInput = true
        that.$store.dispatch('getSmsCode', {
          country: that.country,
          phone: that.phoneAes,
          fn (data) {
            if (data.code === 1) {
              that.phoneCodeTextFn(120)
            }
          }
        })
      },
      phoneCodeTextFn (num) { // 手机验证码 按钮文字 num 剩余时间
        let that = this
        let countFn = () => { // 计算倒计时
          num--
          if (num <= 0) {
            that.phoneCodeText = '获取短信验证码'
          } else {
            that.phoneCodeText = num + 's'
            setTimeout(() => {
              countFn(num)
            }, 1000)
          }
        }
        countFn(num)
      },
      postCheckCodeFn (code, codeType) { // 检验验证码是否正确 code 码  codeType 码的类型  sms 短信 pic 图形
        let that = this
        that.$store.dispatch('postCheckCode', {
          country: that.country,
          phone: that.phoneAes,
          code: code,
          code_type: codeType,
          fn (data) {
            if (data.code === 1) {
              // if (codeType === 'pic' && that.newUserState) { // 判断是不是  图形验证码 如果是新号  验证手机验证码
              //   that.imgCodeState = true
              //   that.step = 2
              //   that.getSmsCodeFn()
              // } else if (that.forgetState) {
              //   that.password = null
              //   that.step = 4
              // } else { // 不是新号 则输密码
              //   that.step = 3
              // }
              if (codeType === 'pic') { // 图形验证码
                that.imgCodeState = true
                that.getSmsCodeFn()
                if (that.showSmsInput) {
                  that.step = 2
                }
              }
              if (codeType === 'sms') { // 短信验证
                if (that.newUserState) { // 新用户
                  // if (that.factor === 1 || that.forgetState) { // 开启双因子
                  //   that.step = 5
                  // } else {
                  //   that.loginState = true
                  // }
                  that.loginState = true
                }
                if (!that.newUserState) { // 老用户
                  if (that.hasPassword) {
                    that.loginState = true
                  }
                  if (!that.hasPassword) {
                    if (that.factor === 1 || that.forgetState) {
                      that.step = 5
                    } else {
                      that.loginState = true
                    }
                  }
                }
              }
            } else {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '验证码错误'
              }
              setTimeout(function () {
                that.hintBox.stateShow = false
              }, 1000)
            }
          }
        })
      },
      getCheckUserFn () { // 检查 手机 是否存在
        let that = this
        that.$store.dispatch('getCheckUser', { // step:1 图形验证码 2 短信验证码 3 忘记密码/登陆密码 4 设置密码
          phone: that.phoneAes,
          country: that.country,
          fn (data) {
            if (data.code === 50013 || data.code === 50014) { // 老用户
              that.newUserState = false
              if (data.msg.indexOf('has password') !== -1) { // 有密码
                that.hasPassword = true
                if (data.factor === 1) {
                  that.step = 3
                } else {
                  that.step = 6
                }
              } else {
                that.hasPassword = false
                that.step = 1
                that.getImgCodeFn()
              }
            } else if (data.code === 30044) { // 新用户
              that.step = 1
              that.getImgCodeFn()
              that.newUserState = true
            }
            that.factor = data.factor
          }
        })
      },
      postRegisterFn () { // 注册
        let that = this
        let pw = null
        if (that.password && that.password !== '') {
          pw = that.$utils.aesEncryptCode(that.password)
        }
        that.$store.dispatch('postRegister', {
          password: pw, // 密码
          phone: that.phoneAes, // 电话
          channel: 'wechat', // 渠道
          country: that.country, // 国家
          open_id: window.configure.open_id,
          fn (data) {
            if (data.code === 1) {
              that.forgetState = false
              that.loginState = false
              that.step = null
              that.phoneNo = ''
              that.password = ''
              that.imgCode = ''
              that.smsCode = ''
            }
          }
        })
      },
      forgetFn () {
        this.forgetState = true
        this.step = 1
        this.getImgCodeFn()
        this.newUserState = true
      },
      loginStateFn: function (num) {  // 登录检测 1 电话 2 图形验证码 3 短信验证码 4 设置/重置/输入密码 5 设置密码 6 登陆，关双因子
        let that = this
        console.log(num)
        switch (num) {
          case 1:
            if (that.$utils.checkPhone(that.phoneNo)) { // 电话号码是否正确
              that.phoneAes = that.$utils.aesEncryptCode(that.phoneNo)
              that.getCheckUserFn()
            } else {
              that.step = null
            }
            break
          case 2:
            if (that.imgCodeUrl === that.imgCode) { // 图形验证码是否正确
              that.postCheckCodeFn(that.$utils.aesEncryptCode(that.imgCode), 'pic')
            } else {
              that.imgCodeState = false
            }
            break
          case 3:
            if (that.smsCode.length === 4) { // 短信验证码是否正确
              that.postCheckCodeFn(that.$utils.aesEncryptCode(that.smsCode), 'sms')
            } else {
              that.imgCodeState = false
            }
            break
          case 4:
            // if (that.password.length >= 6) { // 密码是否正确
            //   if (that.newUserState) {
            //     that.loginState = true
            //   }
            //   if (!that.newUserState) {
            //     if (that.hasPassword) {
            //       if (that.factor === 1) {
            //         that.step = 1
            //         // 需要发送图形验证码
            //         that.getImgCodeFn()
            //       } else {
            //         that.loginState = true
            //       }
            //     }
            //     if (!that.hasPassword) {
            //       that.loginState = true
            //     }
            //   }
            // } else {
            //   that.loginState = false
            // }
            // if (that.password.length >= 6) {
            //   if (!that.newUserState && that.hasPassword && that.factor === 1) {
            //     that.loginState = false
            //   } else {
            //     that.loginState = true
            //   }
            // } else {
            //   that.loginState = false
            // }
            break
          case 5:
            if (that.password.length >= 6) {
              that.loginState = true
            } else {
              that.loginState = false
            }
            break
          case 6:
            if (that.password.length >= 6) {
              that.loginState = true
            } else {
              that.loginState = false
            }
            break
        }
      },
      nextStep () {
        let that = this
        if (that.password.length >= 6) { // 密码是否正确
          if (that.newUserState) {
            that.loginState = true
          }
          if (!that.newUserState) {
            if (that.hasPassword) {
              if (that.factor === 1) {
                that.step = 1
                // 需要发送图形验证码
                that.getImgCodeFn()
              } else {
                that.loginState = true
              }
            }
            if (!that.hasPassword) {
              that.loginState = true
            }
          }
        } else {
          that.loginState = false
        }
      },
      moduleFn: function () { // 模态框 点击事件 关闭 弹窗
        this.$store.dispatch('loginAction', {state: true})
      },
      userxieyiFn: function () { // 跳转 用户协议
        this.$store.dispatch('loginAction', {state: true})
        this.$router.push('/serviceAgreement')
      },
      getImgCodeFn () { // 获取 切换 图片
        let that = this
        that.imgCode = null
        this.$store.dispatch('getImgCode', {
          phone: that.$utils.aesEncryptCode(that.phoneNo),
          country: that.country,
          fn (data) {
            if (data.code === 1) {
              that.imgCodeUrl = that.$utils.aesDecryptCode(data.data)
            }
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .login {
    position: fixed;
    left: 0;
    top: 0;
    z-index: -100;
    opacity: 0;
    transition: 0.3s all;
    .forget {
      color: #f06553;
      font-size: 0.8333rem;
      display: inline-block;
    }
    .nextStep {
      font-size: 0.8333rem;
      display: inline-block;
    }
    .loginContent {
      padding-top: 1rem;
      width: 19.75rem;
      height: auto;
      background-color: #fff;
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border-radius: 0.333rem;
      transition: 0.3s all;
      z-index: 777;
      .row {
        width: 100%;
        box-sizing: border-box;
        height: 2.9166rem;
        line-height: 2.9166rem;
        padding: 0 1rem;
        margin: 1rem 0;
      }
      .row1 {
        .content {
          width: 100%;
          border: 1px solid #eee;
          border-radius: 0.333rem;
        }
        .Region {
          float: left;
          height: 100%;
          position: relative;
          width: 5.8333rem;
          text-align: center;
          ul {
            position: absolute;
            left: 0;
            top: 3rem;
            box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
            border-radius: 0.333rem;
            background-color: #fff;
            width: 100%;
            z-index: -100;
            font-size: 1rem;
            color: #2c2c2c;
            transform-origin: 0 0;
            opacity: 0;
            li {
              width: 100%;
            }
          }
          .showUl {
            z-index: 10;
            opacity: 1;
          }
        }
        .Region:after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate(0, -50%);
          width: 1px;
          height: 50%;
          background-color: #eee;
        }
        .phone {
          box-sizing: border-box;
          height: 100%;
          width: calc(~'100% - 5.8333rem');
          input {
            box-sizing: border-box;
            width: 100%;
            border: none;
            padding: 0 0.5rem;
            height: 100%;
            font-size: 1rem;
            color: #2c2c2c;
          }
        }

      }
      .row2 {
        .numCode {
          height: 100%;
          width: calc(~'100% - 7.25rem');
          float: left;
          box-sizing: border-box;
          border: 1px solid #eee;
          border-radius: 0.333rem;
          overflow: hidden;
          input {
            font-size: 1rem;
            box-sizing: border-box;
            height: 100%;
            width: 100%;
            border: none;
            color: #2c2c2c;
            padding: 0 0.5rem;
          }
        }
        .numCodeBut {
          box-sizing: border-box;
          width: 6.25rem;
          font-size: 0.8333rem;
          float: left;
          height: 100%;
          background-color: #f4f4f4;
          border-radius: 0.333rem;
          overflow: hidden;
          margin-left: 1rem;
          color: #999;
          transition: 0.3s all;
          text-align: center;
          border: 1px solid #eee;
        }
        .numCodeButShow {
          color: #fff;
          background: #f06553;
        }
      }
      .row3 {
        height: auto;
        line-height: inherit;
        .top {
          height: 2.9166rem;
          .imgCode {
            float: left;
            height: 100%;
            width: calc(~'100% - 7.25rem');
            input {
              box-sizing: border-box;
              width: 100%;
              padding: 0 0.5rem;
              height: 100%;
              border: 1px solid #eee;
              font-size: 1rem;
              border-radius: 0.333rem;
            }
          }
          .img {
            float: left;
            box-sizing: border-box;
            width: 6.25rem;
            font-size: 0.8333rem;
            height: 100%;
            background-color: #f4f4f4;
            border-radius: 0.333rem;
            overflow: hidden;
            margin-left: 1rem;
            color: #333333;
            text-align: center;
            border: 1px solid #eee;
            img {
              width: 100%;
              height: 100%;
            }
          }

        }
        .bottom {
          width: 100%;
          text-align: center;
          font-size: 0.8333rem;
          margin-top: 0.3rem;
          color: #999999;
        }
      }
      .row5 {
        .password {
          height: 100%;
          width: 100%;
          float: left;
          box-sizing: border-box;
          border: 1px solid #eee;
          border-radius: 0.333rem;
          overflow: hidden;
          input {
            font-size: 1rem;
            box-sizing: border-box;
            height: 100%;
            width: 100%;
            border: none;
            color: #2c2c2c;
            padding: 0 0.5rem;
          }
        }
      }
      .row4 {
        line-height: inherit;
        height: auto;
        margin-top: 1.6rem;
        font-size: 0.8333rem;
        color: #a5a5a5;
        .protocol {
          color: #f06553;
        }
      }
      .loginBut {
        height: 3.4rem;
        line-height: 3.4rem;
        background-color: #f4f4f4;
        color: #bcbcbc;
        text-align: center;
        font-size: 1.25rem;
        border-radius: 0 0 0.333rem 0.333rem;
        transition: 0.3s all;
      }
      .loginButShow {
        background-color: #f06553;
        color: #fff;
      }
    }
    .Module {
      width: 100vw;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 8888;
    }
  }

  .showLogin {
    z-index: 555;
    opacity: 1;
  }
</style>



