/**服务中**/
<template>
  <div class="Service">
    <div class="Content">
      <div class="describe">
        <div class="describeContent">
          <h2>{{title}}</h2>
          <h3>{{titleCont}}</h3>
        </div>
      </div>
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="ZxFindOrder?ZxFindOrder.employName:''"
                 :carNo="ZxFindOrder?ZxFindOrder.carNumber:''"
                 :carName="ZxFindOrder?(ZxFindOrder.carBodyColor?ZxFindOrder.carBodyColor+'`':'')+ZxFindOrder.carName:''"
                 :headImg="ZxFindOrder?ZxFindOrder.employPhoto:''"
                 :phone="ZxFindOrder?ZxFindOrder.employPhone:''"
      ></xk-waiter>
      <xk-Add :time="ZxFindOrder?serverTimeFn:''"
              :origin="ZxFindOrder?ZxFindOrder.startAddress:''"
              :endPoint="ZxFindOrder?ZxFindOrder.endAddress:''"
      ></xk-Add>
    </div>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkAdd from '../../module/add/src/add.vue'
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkWaiter,
      XkAdd
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        ZxFindOrder: 'ZxFindOrder'
      }),
      serverTimeFn: function () {
        return Toucth.TimestampDate(this.ZxFindOrder.serverTime)
      }
    },
    watch: {
      ZxFindOrder (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/zhuanxian')
          return
        }
        if (val.status === 2 || val.status === 3) {
          if (val.status === 3) {
            if (val.zxOrderType === 1 || val.zxOrderType === 0) {
              that.title = '司机已到达预约地'
              that.titleCont = '请您赶快上车...'
            }
            if (val.zxOrderType === 2) {
              that.title = '司机已到达预约地'
              that.titleCont = '请准备好装货上车！'
            }
          }
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/zhuanxian/service') {
              that.$store.dispatch('ZxFindOrder', {orderId: Toucth.AcquireUrlParameter().orderId})
            }
          }, 3000)
        }
        if (val.status === 4 && val.treatment) {
          that.$router.push({path: '/zhuanxian/settlement', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
        if (val.status === 4) {
          if (val.zxOrderType === 1 || val.zxOrderType === 0) {
            that.title = '正在前往目的地...'
            that.titleCont = '请系上您的安全带，祝您旅途愉快！'
          }
          if (val.zxOrderType === 2) {
            that.title = '正在前往目的地...'
            that.titleCont = '正在为您的货物保驾护航！'
          }
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/zhuanxian/service') {
              that.$store.dispatch('ZxFindOrder', {orderId: Toucth.AcquireUrlParameter().orderId})
            }
          }, 3000)
        }
        if (val.status === 5) {
          this.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            this.hintBox = false
            that.$router.push({path: '/zhuanxian/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
          }, 1500)
        }
        if (val.status === 6) {
          this.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            this.hintBox = false
            that.$router.push('/zhuanxian')
          }, 1500)
        }
      }
    },
    data () {
      return {
        title: '司机已接单',
        titleCont: '司机正在向你狂奔...',
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('ZxFindOrder', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .Service {
    .Content {
      width: 100vw;
      height: 100vh;
      overflow: hidden;
      overflow-y: auto;
      box-sizing: border-box;
      background-color: #f3f4f5;
      .describe {
        height: 16.666rem;
        background-color: #fff;
        position: relative;
        .describeContent {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
        }
        h2 {
          display: inline-block;
          width: 100%;
          font-size: 2rem;
          color: #333;
          text-align: center;
          margin-top: 1rem;
        }
        h3 {
          width: 100%;
          color: #999999;
          font-size: 1.25rem;
          text-align: center;
          margin-top: 1.4rem;
        }
      }
      .Add {
        margin-top: 1rem;
      }
    }
  }
</style>



