<template>
  <div class="DaiJia ">
    <div class="content">
      <div class="banner" v-if="daijiaBanner && daijiaBanner.length>0">
        <xk-roll-banner :data="daijiaBanner"></xk-roll-banner>
      </div>
      <div class="setSite">
        <div class="typeNav">
          <xk-type-tabs v-if="typeAll.data.length>0" :cardata="typeAll" @click="typeTabFn"
                        :defaultmum="0"></xk-type-tabs>
        </div>
        <div class="title">
          “附近有 <span style="color: #f06553">{{getNearDrivers.length || 0}}</span> 位司机候驾”
        </div>
        <div class="row row1 clear">
          <i class="iconfont icon-yuan"></i>
          <div class="val">
            <xk-site-input placeholder="请输入起点位置" @onClick="startCityFn"
                           :name="positioning?positioning.name:''"
                           :state="true"
            ></xk-site-input>
          </div>
        </div>
        <div class="row row2 clear">
          <i class="iconfont icon-yuan"></i>
          <div class="val">
            <xk-site-input placeholder="请输入终点位置"
                           @onClick="endCityFn"
                           checkedlogin
                           :name="adds.endAdd.endAddress"
                           :addressrecord="addressRecordFn"
                           :state="true"
            ></xk-site-input>
          </div>
        </div>
        <div class="row row3 clear">
          <div class="top">
            <span class="list" :class="others.othersPhone!==''?'listAc':''" style="display: inline-flex;vertical-align: top;justify-content: center;">
              <i class="iconfont icon-mine"></i>
              <div class="listVal" @click="othersPhoneFn('show')">
                <p v-if="others.state">为别人叫</p>
                <p v-else>{{others.recordPhone}}</p>
              </div>
            </span>
            <span class="list listAc" @click="selectTimeFn" style="display: inline-flex;vertical-align: top;justify-content: center;">
              <i class="iconfont icon-shijian1"></i>
              <p>{{startTime.name}}</p>
            </span>
          </div>
        </div>
        <div class="totalNum clear">
          <div class="row" @click="chooseDriverFn">
            <div style="width: 100%">
              <span>选择</span>
              <span class="red">{{driverNumber}}</span>
              <span>位司机</span>
            </div>
          </div>
          <div class="line"></div>
          <div class="row">
            <xk-coupon-select
              @click="couponSlelectFn"
              :type="'daijia'"
              :data="djCoupnsGetter"></xk-coupon-select>
          </div>
        </div>
        <div class="totalNum">
          <div class="row" @click="detailsBoxFn" style="width: 100%;line-height: 4.5rem;">
            <div style="width: 100%">
              <span v-if="budgetFee&&budgetFee.startPrice">{{loginData&&loginData.passenger?'约':'起步价'}}</span><span
              class="red">{{DjBudgetCostFn}}</span><span
              v-if="budgetFee&&budgetFee.startPrice">元</span>
              <i class="iconfont icon-iconfontyiwen" v-if="budgetFee&&budgetFee.startPrice"></i>
            </div>
          </div>
        </div>
      </div>
      <div class="callOutBut">
        <button @click="callCarFn" :disabled="loadState" :class="{disable: loadState}">呼叫代驾</button>
      </div>
      <xk-input-box ref="othersPhone" title="乘车人电话" v-if="others.showState" @click="othersPhoneFn">
        <div slot="content">
          <input class="othersPhoneInput" placeholder="输入乘车人电话" v-model="others.othersPhone"
                 @input="othersPhoneCheck"/>
        </div>
      </xk-input-box>
      <xk-date-picker-mod @click="DatePickerFn" ref="DatePicker" v-if="datePickerState"></xk-date-picker-mod>
    </div>
    <xk-unfilled-order-mod
      v-if="unfilledOrderState && unfilledOrderState.daijia && unfilledOrderFn.length>0"
      :data="unfilledOrderFn?unfilledOrderFn:null"
      :title="'代驾'"
      @close="unfilledOrdercCloseFn"></xk-unfilled-order-mod>
    <!--<xk-upload-mod :state="loadState"></xk-upload-mod>-->
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
    <Xk-detailsBox v-if="budgetFee" :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <div class="List">
          <ul>
            <li class="clear">
              <span class="name">总价</span>
              <span class="val"><span class="sum">{{budgetFee&&budgetFee.total}}</span><span
                class="unit">元</span></span>
            </li>
            <li class="clear" v-if="budgetFee&&budgetFee.distancePrice">
              <span class="name">里程费</span>
              <span class="val"><span class="sum">{{budgetFee&&budgetFee.distancePrice}}</span><span
                class="unit">元</span></span>
            </li>
            <li class="clear" v-if="budgetFee&&budgetFee.timePrice">
              <span class="name">时间费</span>
              <span class="val"><span class="sum">{{budgetFee&&budgetFee.timePrice}}</span><span
                class="unit">元</span></span>
            </li>
            <li class="clear">
              <span class="name">起步价</span>
              <span class="val"><span class="sum">{{budgetFee&&budgetFee.startPrice}}</span><span
                class="unit">元</span></span>
            </li>
          </ul>
        </div>
        <div class="introduce" style="max-height: 40vh;overflow-y: auto" v-html="budgetFeeRemark">
        </div>
      </div>
    </Xk-detailsBox>
    <!--选择司机-->
    <xk-my-picker
      :showMyPicker="visible"
      :pickItem="slots"
      title="选择司机人数"
      @sureAction="sureSelectCar"
      @cancelAction="cancleSelectCar">
    </xk-my-picker>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Toucth from '../../../assets/js/method'
  import { mapGetters } from 'vuex'
  import XkRollBanner from '../../module/rollBanner/src/rollBanner'
  import XkSiteInput from '../../module/siteInput/src/siteInput'
  import XkInput from '../../module/input/src/input'
  import XkDetailsBox from '../../module/detailsBox/src/detailsBox'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import XkDatePickerMod from '../../module/datePickerMod/src/datePickerMod'
  import XkCouponSelect from '../../module/couponSelect/src/couponSelect'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import xkMyPicker from '../../module/myPicker/myPicker'
  import { Picker } from 'mint-ui'
  import Cookie from 'js-cookie'

  export default {
    components: {
      XkHintBox,
      XkUnfilledOrderMod,
      XkUploadMod,
      XkCouponSelect,
      XkDatePickerMod,
      XkInputBox,
      XkDetailsBox,
      XkInput,
      XkSiteInput,
      XkRollBanner,
      xkMyPicker,
      Picker
    },
    name: 'DaiJia',
    computed: {
      ...mapGetters({
        daijiaBanner: 'daijiaBanner',
        DjBudgetCost: 'DjBudgetCost',
        DJBusiness: 'DJBusiness',
        addressRecord: 'addressRecord',
        loginData: 'loginData',
        positioning: 'positioning',
        companyInfo: 'companyInfo',
        getNearDrivers: 'getNearDrivers',
        unfilledOrderState: 'unfilledOrderState',
        unfilledOrder: 'unfilledOrder',
        djCoupnsGetter: 'djCoupnsGetter',
        login: 'login'
      }),
      unfilledOrderFn: function () { // 准备未完成订单数据
        let unfilledOrder = []
        let daijiaData = []
        if (this.unfilledOrder && this.unfilledOrder.length > 0) {
          this.unfilledOrder.forEach((row) => {
            if (row.business === 'daijia') {
              daijiaData.push(row)
            }
          })
          if (daijiaData && daijiaData.length > 0) {
            let obj = {}
            for (let i = 0; i < daijiaData.length; i++) {
              obj = {
                startAddress: daijiaData[i].book_address,
                endAddress: daijiaData[i].destination,
                serverTime: Toucth.TimestampDate(daijiaData[i].book_time),
                orderId: daijiaData[i].id,
                status: ''
              }
              switch (daijiaData[i].status) {
                case 1:
                  obj.status = {
                    name: '新单',
                    router: {path: '/daijia/await', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 5:
                  obj.status = {
                    name: '等待中',
                    router: {path: '/daijia/await', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 10:
                  obj.status = {
                    name: '已接单',
                    router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 15:
                  obj.status = {
                    name: '前往达预约地',
                    router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 20:
                  obj.status = {
                    name: '到达预约地',
                    router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 25:
                  obj.status = {
                    name: '前往目的地',
                    router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 28:
                  obj.status = {
                    name: '等待',
                    router: {path: '/daijia/service', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 30:
                  obj.status = {
                    name: '待结算',
                    router: {path: '/daijia/settlement', query: {order_id: daijiaData[i].id, app_key: this.app_key}}
                  }
                  this.$router.push({path: '/daijia/settlement', query: {order_id: daijiaData[i].id, app_key: this.app_key}})
                  break
                case 35:
                  obj.status = {
                    name: '待评价',
                    router: {path: '/daijia/affirm', query: {order_id: daijiaData[i].id}}
                  }
                  break
                case 45:
                  obj.status = {
                    name: '已销单',
                    router: {path: '/daijia/checkOrder', query: {order_id: daijiaData[i].id}}
                  }
                  break
              }
              unfilledOrder.push(obj)
            }
          }
        }
        return unfilledOrder
      },
      DjBudgetCostFn: function () { // 预算费用计算函数
        let money = 0
        let that = this
        let val = that.budgetFee
        if (val && val.total) {
          money = val.total
          if (that.couponMoney.type === 'dikou') {
            money = Number(money - that.couponMoney.val) > 0 ? Number(money - that.couponMoney.val) : 0
          }
          if (that.couponMoney.type === 'dazhe') {
            money = money * (that.couponMoney.val / 100)
          }
        }
        if (!money) {
          money = 0
        } else {
          money = money.toFixed(1)
        }
        return money
      },
      addressRecordFn: function () {
        if (this.addressRecord.daijia.length > 0) {
          return this.addressRecord.daijia
        }
      }
    },
    data () {
      return {
        adds: {
          startAdd: {
            beginAddress: '',
            beginAddressDetail: '',
            beginLat: '',
            beginLng: ''
          },
          endAdd: {
            endAddress: '',
            endAddressDetail: '',
            endLat: '',
            endLng: ''
          }
        },
        others: { // 他人
          showState: false,
          state: true,
          othersPhone: '',
          recordPhone: ''
        },
        startTime: {
          val: '',
          name: '现在出发'
        },
        loadState: false,
        couponMoney: {
          type: '',
          id: '',
          num: ''
        },
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        datePickerState: false,
        couponData: [], // 优惠券 列表
        typeAll: { // 子业务
          action: 0,
          data: []
        },
        typeAcId: null, // 选中的子业务id
        budgetFee: null, // 预算费用
        budgetFeeRemark: '', // 预算备注
        visible: false,
        slots: [1],
        driverNumber: 1,
        app_key: ''
      }
    },
    watch: {
      loginData (val) {
        let that = this
        if (that.loginData && that.loginData.passenger && that.others.othersPhone === '' && that.others.recordPhone === '') {
          that.unfilledOrderShowState = true
        }
        if (that.loginData && that.loginData.passenger) {
          this.getPassengerDaijiaFn()
          this.getCouponTypeFn()
        }
      },
      unfilledOrder (val) { // 如果有未结算的代驾订单，路由跳转到结算页面
        let that = this
        if (val && val.length > 0) {
          val.forEach(function (row) {
            if (Number(row.status) === 30) { // 代驾订单待结算
              if (row.business === 'zhuanche') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '您有未支付的专车订单'
                }
                setTimeout(function () {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/zhuanche/settlement', query: {orderId: row.id}})
                }, 1500)
              }
              if (row.business === 'daijia') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '您有未支付的代驾订单'
                }
                console.log('daijia', this.$route)
                setTimeout(function () {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/daijia/settlement', query: {order_id: row.id, app_key: that.app_key}})
                }, 1500)
              }
            }
          })
        }
      }
      // login (val) { // 监听登录状态
      //   if (val) {
      //     this.getCouponTypeFn()
      //   }
      // }
    },
    methods: {
      getdjBudgetCost () {
        let that = this
        that.$store.dispatch('getBudgetPrice', {
          'distance': 0,
          'time': 0,
          'company_id': that.companyInfo,
          'order_time': that.startTime.val,
          'typeId': that.typeAcId,
          'channel': 'wechat',
          fn (data) {
            if (data.code === 1) {
              that.budgetFee = data.budgetFee
              that.budgetFee['time'] = 0
              that.budgetFee['distance'] = 0
            }
          }
        })
        that.getArticlebyaliasFn()
      },
      async getArticlebyaliasFn () { // 获取 预算备注
        let companyInfo = null
        if (Cookie.get('companyinfo')) {
          companyInfo = JSON.parse(Cookie.get('companyinfo'))
        }
        let params = this.$utils.objKeySort('/passenger/api/v1/articlebyalias', {
          companyId: companyInfo.id || 0,
          alias: 'CutomerPricingRules'
        })
        let data = (await this.$http.get('/v1/articlebyalias', {params})).body
        if (data && data.code === 1) {
          this.budgetFeeRemark = data.article.contents
        }
      },
      startCityFn: function (obj, val) { // 起点位置
        this.adds.startAdd.beginAddress = obj.name
        this.adds.startAdd.beginAddressDetail = obj.detailedAddress
        this.adds.startAdd.beginLng = obj.lng
        this.adds.startAdd.beginLat = obj.lat
        this.$store.dispatch('getNearDrivers', {
          lng: this.adds.startAdd.beginLng,
          lat: this.adds.startAdd.beginLat,
          distance: 20,
          business: 'daijia'
        })
        this.affirmFn()
      },
      endCityFn: function (obj) { // 终点位置
        this.adds.endAdd.endAddress = obj.name
        this.adds.endAdd.endAddressDetail = obj.detailedAddress
        this.adds.endAdd.endLng = obj.lng
        this.adds.endAdd.endLat = obj.lat
        this.affirmFn()
      },
      typeTabFn: function (obj) { // 业务类型菜单
        this.typeAll.action = obj.index
        this.typeAcId = obj.data.id
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          if (state && that.loginData && this.loginData.passenger) {
            that.getCouponTypeFn()
            if (that.adds.endAdd.endLng && that.adds.endAdd.endLng !== '') {
              that.affirmFn()
            } else {
              that.getstartPriceFn()
            }
          } else {
            that.getstartPriceFn()
          }
        })
      },
      affirmFn: function () { // 预算费用
        let that = this
        if (that.adds.startAdd.beginLng && that.adds.startAdd.beginLat) {
          that.$store.dispatch('routePlanning', {
            originLng: that.adds.startAdd.beginLng,
            originLat: that.adds.startAdd.beginLat,
            destinationLng: that.adds.endAdd.endLng || '',
            destinationLat: that.adds.endAdd.endLat || '',
            fn (data) {
              if (data.status === '1') {
                let Data = data.route.paths[0]
                that.getBudgetPriceFn(Data)
              }
            }
          })
        }
      },
      getBudgetPriceFn (Data) {
        let that = this
        that.$store.dispatch('getBudgetPrice', {
          'distance': (Data.distance / 1000).toFixed(1),
          'time': (Data.duration / 60).toFixed(0),
          'order_time': that.startTime.val,
          'typeId': that.typeAcId,
          'channel': 'wechat',
          fn (data) {
            if (data.code === 1) {
              that.budgetFee = data.budgetFee
              that.budgetFee['time'] = (Data.duration / 60).toFixed(0)
              that.budgetFee['distance'] = (Data.distance / 1000).toFixed(1)
            }
          }
        })
      },
      getstartPriceFn () {
        let that = this
        that.$store.dispatch('getBudgetPrice', {
          'order_time': that.startTime.val,
          'typeId': that.typeAcId,
          'channel': 'wechat',
          fn (data) {
            if (data.code === 1) {
              that.budgetFee = data.budgetFee
            }
          }
        })
      },
      othersPhoneFn: function (obj) { // 他人电话
        let that = this
        switch (obj) {
          case 'affirm':
            that.others.showState = false
            if (that.others.othersPhone === '') {
              that.others.state = true
              that.others.othersPhone = ''
              that.others.recordPhone = that.$store.state.mc.phoneNo
              return false
            }
            if (that.others.othersPhone !== that.$store.state.mc.phoneNo) {
              that.others.state = false
              that.others.recordPhone = that.others.othersPhone
            } else {
              that.others.state = true
              that.others.recordPhone = that.others.othersPhone
              that.others.othersPhone = ''
            }
            break
          case 'cancel':
            that.others.showState = false
            if (Toucth.checkPhone(that.others.othersPhone) && that.others.othersPhone === that.$store.state.mc.phoneNo) {
              that.others.state = true
              that.others.othersPhone = ''
            }
            if (Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone) {
              that.others.state = false
              that.others.othersPhone = that.others.recordPhone
            }
            if (!Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone && that.others.recordPhone !== that.$store.state.mc.phoneNo) {
              that.others.state = false
              that.others.othersPhone = that.others.recordPhone
            }
            if (!Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone && that.others.recordPhone === that.$store.state.mc.phoneNo) {
              that.others.state = true
              that.others.othersPhone = ''
            }
            break
          case 'show':
            that.others.showState = true
            setTimeout(() => {
              if (Toucth.checkPhone(that.others.othersPhone) || that.others.othersPhone === '') {
                that.$refs.othersPhone.affStateFn(true)
              } else {
                that.$refs.othersPhone.affStateFn(false)
              }
            }, 0)
            break
        }
      },
      othersStateFn: function () { // 他人是否显示
        if (this.others.othersPhone !== '' && this.others.state) {
          this.$refs.othersPhone.stateFn()
        } else {
          this.others.state = true
        }
      },
      othersPhoneCheck: function () { // 他人电话
        if (Toucth.checkPhone(this.others.othersPhone) || this.others.othersPhone === '') {
          this.$refs.othersPhone.affStateFn(true)
        } else {
          this.$refs.othersPhone.affStateFn(false)
        }
      },
      callCarFn: function () { // 呼叫
        let that = this
        that.loadState = true
        that.$store.dispatch('checkLogin', (state) => {
          if (state) {
            console.log(that.adds.startAdd)
            // 初始化起点和预算费用
            if (!that.adds.startAdd.beginLat || !that.adds.startAdd.beginLng) { // 防止初始化地址的时候没拿到
              if (this.positioning && this.positioning.lng && this.positioning.lat) {
                this.adds.startAdd = {
                  beginAddress: this.positioning.name,
                  beginAddressDetail: this.positioning.detailedAddress,
                  beginLat: this.positioning.lat,
                  beginLng: this.positioning.lng
                }
                console.log('初始化地址1', this.adds.startAdd)
              }
            }
            if (!that.adds.startAdd.beginLat || !that.adds.startAdd.beginLng || !that.adds.startAdd.beginAddress) {
              // that.loadState = false
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '请选择起点'
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1500)
              return false
            }
            let bookTime = 0
            if (that.startTime.name === '现在出发') {
              let timetamp = Date.parse(new Date()) / 1000
              bookTime = that.startTime.val ? that.startTime.val : timetamp // 时间戳为NaN时获取当前时间
            } else {
              bookTime = (that.startTime.val) / 1000
            }
            if (that.loginData && that.loginData.passenger && that.adds.startAdd.beginLat && that.adds.startAdd.beginLng) {
              let obj = {
                'order_person': that.loginData.passenger.name,
                'order_person_id': that.loginData.passenger.id,
                'number': that.driverNumber,
                'cid': that.typeAcId,
                'passenger_phone': that.loginData.passenger.phone,
                'channelName': 'wechat',
                'book_address': that.adds.startAdd.beginAddress,
                'startAddressDetail': that.adds.startAdd.beginAddressDetail,
                'book_address_lat': that.adds.startAdd.beginLat,
                'book_address_lng': that.adds.startAdd.beginLng,
                'user_phone': that.others.recordPhone,
                'destination': that.adds.endAdd.endAddress,
                'endAddressDetail': that.adds.endAdd.endAddressDetail,
                'destination_lat': that.adds.endAdd.endLat,
                'destination_lng': that.adds.endAdd.endLng,
                'book_time': bookTime,
                'budget_fee': that.DjBudgetCostFn,
                'coupon_id': that.couponMoney.id,
                fn (data) {
                  if (data.code === 1) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'yes',
                      content: '下单成功！'
                    }
                    setTimeout(() => {
                      that.loadState = false
                      that.hintBox.stateShow = false
                      that.$router.push({
                        path: '/daijia/await',
                        query: {order_id: data.ids[0]}
                      })
                    }, 1300)
                  } else if (data.code === 31012) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'no',
                      content: '选择多位司机时，不能使用优惠券！'
                    }
                    setTimeout(() => {
                      that.loadState = false
                      that.hintBox.stateShow = false
                    }, 1300)
                  } else if (data.code === 30014) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'no',
                      content: '你已被加入黑名单！'
                    }
                    setTimeout(() => {
                      that.loadState = false
                      that.hintBox.stateShow = false
                    }, 1300)
                  } else if (data.code === 20091) {
                    that.hintBox = {
                      stateShow: true,
                      state: 'no',
                      content: '你的并行订单量已经达到上限！'
                    }
                    setTimeout(() => {
                      that.loadState = false
                      that.hintBox.stateShow = false
                    }, 1300)
                  } else {
                    that.hintBox = {
                      stateShow: true,
                      state: 'no',
                      content: '下单失败！'
                    }
                    setTimeout(() => {
                      that.loadState = false
                      that.hintBox.stateShow = false
                    }, 1300)
                  }
                }
              }
              that.$store.dispatch('postCreateOrder', obj)
            }
          } else {
            that.$store.dispatch('loginAction', {state: false})
            that.loadState = false
          }
        })
      },
      DatePickerFn: function (time) { // 时间选择
        if (time) {
          console.log('时间选择', time)
          this.startTime.val = time.val
          this.startTime.name = time.name
        }
        this.datePickerState = false
      },
      selectTimeFn: function () { // 时间选择
        this.datePickerState = true
      },
      couponSlelectFn: function (obj) {
        if (obj.type === 'nonuse') {
          this.couponMoney = {
            type: '',
            id: '',
            num: ''
          }
          return true
        }
        if (obj.type === 'action' && obj.obj && obj !== '') {
          if (obj.obj.discount && obj.obj.discount !== '') {
            this.couponMoney = {
              type: 'dazhe',
              val: obj.obj.discount,
              id: obj.obj.id
            }
          } else if (obj.obj.deductible && obj.obj.deductible !== '') {
            this.couponMoney = {
              type: 'dikou',
              val: obj.obj.deductible,
              id: obj.obj.id
            }
          }
          return true
        }
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'daijia', state: false})
      },
      detailsBoxFn: function () { // 详情事件
        if (this.$refs.detailsBox) {
          this.$refs.detailsBox.switchFn()
        }
      },
      getCouponTypeFn () { // 获取 优惠券
        let that = this
        that.$store.dispatch('v5getCoupons', {limit: 80, page: 1, service_type: 1, channel: true})
      },
      chooseDriverFn () { // 选择司机个数
        let that = this
        that.$store.dispatch('checkLogin', (state) => {
          console.log('选择司机个数', state, 'that.loginData', that.loginData)
          if (state && that.loginData && that.loginData.passenger) {
            that.visible = true
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      sureSelectCar (val) { // 司机选择确认
        this.visible = false
        this.driverNumber = val[0]
      },
      cancleSelectCar (val) { // 司机选择取消
        this.visible = false
      },
      getPassengerDaijiaFn () { // 获取个人信息
        let appkey = this.$route.query.app_key || window.configure.appKey
        let passengerId = window.configure.passengerInfo ? window.configure.passengerInfo.id : null
        let url = '/v1/passenger'
        let params = {
          id: passengerId,
          app_key: appkey
        }
        Vue.http.get(url, {params}, {emulateJSON: true}).then((data) => {
          if (data.body.code === 1) {
            let singleOrder = Number(data.body.grade.single_order)
            if (singleOrder > 1) {
              this.slots = []
              for (let i = 1; i <= singleOrder; i++) {
                this.slots.push(i)
              }
            }
          }
        })
      }
    },
    created: function () {
      this.app_key = this.$route.query.app_key
    },
    mounted: function () {
      let that = this
      if (this.loginData && this.loginData.passenger) {
        console.log('1232123', this.loginData)
        this.getPassengerDaijiaFn()
      }
      if (this.$route.query.tel) {
        this.$store.dispatch('checkLogin', (state) => {
          if (state) {
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      }
      that.$store.dispatch('getBusiness', {
        business: 'daijia',
        company_id: 1,
        fn (data) {
          if (data.code === 1) {
            let business = data.categories // 子业务
            if (business && business.length > 0) {
              that.typeAcId = business[0].id
              that.typeAll.data = [...business]
            }
            that.getdjBudgetCost()
            that.getCouponTypeFn()
          }
        }
      })

      // 初始化起点和预算费用
      if (this.positioning && this.positioning.lng && this.positioning.lat) {
        this.adds.startAdd = {
          beginAddress: this.positioning.name,
          beginAddressDetail: this.positioning.detailedAddress,
          beginLat: this.positioning.lat,
          beginLng: this.positioning.lng
        }
      }
      let date = new Date()
      that.startTime.name = '现在出发'
      that.startTime.val = Date.parse(date) / 1000
      that.$store.dispatch('checkLogin', (state) => {
        if (state && that.loginData && this.loginData.passenger) {
          that.$store.dispatch('unfilledOrder', {page: 1, limit: 100}) // 为完成订单
          console.log('daijia-mounted未完成订单', this.unfilledOrder)
          that.unfilledOrderShowState = true
        }
      })
      setTimeout(() => {
        if (window.configure.location) {
          that.$store.dispatch('getNearDrivers', {
            lng: window.configure.location.lng || null,
            lat: window.configure.location.lat || null,
            distance: 10,
            business: 'daijia'
          })
          that.getCouponTypeFn()
        }
        this.getstartPriceFn()
      }, 1000)
    }
  }
</script>

