import axios from 'axios'
import api from '../api'
import Vue from 'vue'
import QS from 'qs'

Vue.prototype.$axios = {
  get: api.getAxios,
  put: api.putAxios,
  post: api.postAxios,
  delete: api.deleteAxios
}
axios.defaults.timeout = 30000 // 30秒超时

let pending = [] // 声明一个数组用于存储每个ajax请求的取消函数和ajax标识
let CancelToken = axios.CancelToken
let removePending = (config) => {
  for (let p in pending) {
    if (pending[p].u === config.url + '&' + config.method) { // 当当前请求在数组中存在时执行函数体
      pending[p].f() // 执行取消操作
      pending.splice(p, 1) // 把这条记录从数组中移除
    }
  }
}

// Add a request interceptor 请求
axios.interceptors.request.use(function (config) {
  console.log(config.url)
  console.log(config.params, config.data)
  // -----取消重复请求--start-----
  if (config.url !== '/v1/safe/back/police_records') { // 该接口必须请求两次，但参数不一致
    removePending(config) // 在一个ajax发送前执行一下取消操作
    config.cancelToken = new CancelToken((c) => {
      pending.push({u: config.url + '&' + config.method, f: c})
    })
  }
  // -----取消重复请求--end-----
  // -----post/put请求--start-----
  if (config.method === 'post' || config.method === 'put') {
    if (config.url === '/upImg') {
      config.headers['Content-Type'] = 'application/json'
    } else {
      config.data = QS.stringify(config.data)
    }
  }
  // -----post/put请求--end-----
  return config
}, function (error) {
  return Promise.reject(error)
})

// Add a response interceptor 响应
axios.interceptors.response.use(function (response) {
  return response.data
}, function (error) {
  return Promise.reject(error)
})



