/**完成订单 评价订单**/
<template>
  <div class="affirm">
    <div class="content">
      <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
                 :name="DjOrderInfo?DjOrderInfo.employ_name:''"
                 :headImg="DjOrderInfo?DjOrderInfo.employ_head_path:''"
      >
      </xk-waiter>
      <div class="grade">
        <xk-star @clickChange="starFn" :defScore="5"></xk-star>
        <div class="evaluate">
          <div class="evaluateTag clear">
            <div class="TagList clear">
              <xk-tag-item v-for="item in tags" :name="item.name" :value="item.val" @clickChange="tagsFn"
                           :state="false" :key="item.name"></xk-tag-item>
            </div>
          </div>
          <div class="remark">
            <xk-textarea @input="remarkFn" defaulttext="请输入其它意见和建议"></xk-textarea>
          </div>
        </div>
      </div>
      <div class="submit">
        <span @click="submitFn">确定</span>
      </div>
    </div>
    <xk-hint-Box :time="1500" v-if="hintBox.state" :state="hintBox.showState" :content="hintBox.content">
    </xk-hint-Box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
  </div>
</template>

<script>
  import XkTagItem from '../../module/tagitem/src/tagitem'
  import XkStar from '../../module/star/src/star'
  import XkTextarea from '../../module/textarea/src/textarea'
  import XkWaiter from '../../module/waiter/src/waiter'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'

  export default {
    components: {
      XkUploadMod,
      XkStar,
      XkTagItem,
      XkTextarea,
      XkWaiter,
      XkHintBox
    },
    name: 'affirm',
    computed: {
      ...mapGetters({
        DjOrderInfo: 'DjOrderInfo',
        loginData: 'loginData'
      })
    },
    data () {
      return {
        score: '',
        tagsVal: [], // 选择的标签
        tags: [], // 标签
        remark: '', // 备注
        loadState: false,
        hintBox: {
          state: false,
          showState: 'yes',
          content: ''
        } // 模态状态,
      }
    },
    methods: {
      starFn: function (msg) { // 星星的回掉函数
        let that = this
        that.score = msg
        that.tagsVal = []
        that.getTagsFn()
      },
      tagsFn: function (msg) { // 标签的回掉函数
        if (msg.state) {
          this.tagsVal.push(msg.val)
        } else {
          for (let i = 0; i < this.tags.length; i++) {
            if (msg.val === this.tagsVal[i]) {
              this.tagsVal.splice(i)
              return
            }
          }
        }
      },
      remarkFn: function (msg) { // 文本事件
        this.remark = msg
      },
      submitFn: function () { // 保存事件
        let that = this
        let content = that.tagsVal.join('&|&') + '&*&' + that.remark
        if (that.score !== '' && that.score) {
          that.loadState = true
          that.$store.dispatch('DjEvaluateOrder', {
            order_id: Toucth.AcquireUrlParameter().order_id,
            score: that.score,
            remark: content,
            fn: (data) => {
              that.loadState = false
              if (data.code === 1) {
                that.hintBox = {
                  state: true,
                  showState: 'yes',
                  content: '评价成功！'
                }
                setTimeout(() => {
                  that.hintBox = false
                  that.$store.dispatch('unfilledOrderState', {type: 'daijia', state: false})
                  that.$router.push({path: '/daijia?app_key=' + window.configure.appKey})
                }, 1500)
              }
            }
          })
        } else {
          that.hintBox = {
            state: true,
            showState: 'no',
            content: '请选择星级后评价！'
          }
          setTimeout(() => {
            that.hintBox = false
          }, 1500)
        }
      },
      getTagsFn () {
        let that = this
        that.$store.dispatch('getTags', {
          type: 'daijiaOrderRate',
          rate: that.score,
          fn (data) {
            if (data.code === 1) {
              let Data = data.tags
              that.tags = []
              if (Data) {
                if (Data.length < 6) {
                  Data.forEach((row) => {
                    that.tags.push({
                      name: row.tag,
                      val: row.tag
                    })
                  })
                } else {
                  for (let i = 0; i < 6; i++) {
                    let num = that.$utils.RandomNumBoth(0, (Data.length - 1))
                    let tag = Data[num].tag
                    Data.splice(num, 1)
                    that.tags.push({
                      name: tag,
                      val: tag
                    })
                  }
                }
              }
            }
          }
        })
      }
    },
    watch: {
      DjQueryOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.clientId) {
          that.$router.push('/daijia')
          return
        }
        if (val.status === 5 && val.review) {
          setTimeout(() => {
            that.$store.dispatch('unfilledOrderState', {type: 'daijia', state: false})
            that.$router.push('/daijia')
          }, 1500)
        }
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('DjOrderInfo', {order_id: Toucth.AcquireUrlParameter().order_id})
          setTimeout(() => {
            that.getTagsFn()
          }, 1000)
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .affirm {
    min-height: 100%;
    .content {
      box-sizing: border-box;
      width: 100vw;
      background-color: #f3f4f5;
      .grade {
        width: 100%;
        padding: 0 3.25rem;
        box-sizing: border-box;
        text-align: center;
        .evaluate {
          width: 100%;
          padding-top: 1.6rem;
          .evaluateTag {
            width: 100%;
            .TagList {
              width: 100%;

            }
          }
          .remark {
            box-sizing: border-box;
            margin-top: 1rem;
            height: 8.83rem;
            width: 100%;
          }
        }

      }
      .submit {
        box-sizing: border-box;
        width: 100%;
        padding: 0 3.25rem;
        margin-top: 1.5rem;
        padding-bottom: 1rem;
        span {
          width: 100%;
          height: 3.75rem;
          line-height: 3.75rem;
          background-color: #f06553;
          color: #fff;
          text-align: center;
          border-radius: 0.333rem;
          overflow: hidden;
          font-size: 1rem;
        }
      }
    }
  }
</style>



