import Vue from 'vue'
import InitData from '../../store/configure'
import WX from 'weixin-js-sdk'

export default {
  queryCarType (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'companyId': obj.companyId
      // 'acKey': InitData.acKey,
      // 'lng': obj.lng,
      // 'lat': obj.lat
    }
    var url = InitData.weixinUrl + '/api/freight/queryTruckType'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  budgetCost (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'companyId': obj.companyId,
      'areaId': obj.areaId,
      'time': obj.time,
      'mileage': obj.mileage
    }
    var url = InitData.weixinUrl + '/api/freight/getPrice'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  placeAnOrder (obj, callback) { // 下单
    var params = {
      'app_key': InitData.appKey,
      'passengerName': obj.passengerName, // 客户名称
      'passengerPhone': obj.passengerPhone, // 手机s
      'truckTypeId': obj.truckTypeId, // 货车类型Id
      'truckTypeName': obj.truckTypeName, // 货车类型名称
      'startTime': obj.startTime, // 预约时间
      'waypoint': obj.waypoint ? obj.waypoint : null, // 途径点
      'preStartPrice': obj.preStartPrice, // 预算起步价
      'preMileage': obj.preMileage, // 预算公里数
      'preRunTime': obj.preRunTime, // 预算行驶时间
      'preRunTimePrice': obj.preRunTimePrice, // 预算时间价格
      'budgetPay': obj.budgetPay, // 预算价格
      'passengerId': obj.passengerId,
      'companyId': obj.companyId,
      'companyName': obj.companyName,
      'memo': obj.memo,
      'flitting': obj.flitting, // 搬运
      'receipt': obj.receipt, // 收据
      'receivedPay': obj.receivedPay, // 收钱
      'payMount': obj.payMount, // 汇款金额
      'couponId': obj.couponId ? obj.couponId : null,
      'preMileagePrice': obj.preMileagePrice, // 预算回来的公里费用
      'payDetail': obj.payDetail, // 预算 中文
      'channelName': '微信预约', // 来源
      'tip': obj.tip // 小费
    }
    var url = InitData.weixinUrl + '/api/freight/create'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  coupon (obj, callback) { // 获取 优惠券
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/getCoupons'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/api/freight/findByOrderId'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  cancelOrder (obj, callback) { // 取消优惠券
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'cause': obj.cause
    }
    var url = InitData.weixinUrl + '/api/freight/cancel'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  payInfo (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/api/freight/clientPayData'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body, obj.type)
      }
    )
  },
  weixinPay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'openId': obj.openid,
      'appId': obj.WXappId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/doPayForWx'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        if (data.body.code === 0) {
          var content = data.body.data

          WX.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: window.configure.WXappId, // 必填，公众号的唯一标识
            timeStamp: content.timestamp, // 必填，生成签名的时间戳
            nonceStr: content.nonceStr, // 必填，生成签名的随机串
            signature: content.signature, // 必填，签名，见附录1
            jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
          })

          WX.ready(function () {
            // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
            WX.chooseWXPay({
              timestamp: content.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              nonceStr: content.nonceStr, // 支付签名随机串，不长于 32 位
              package: content.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
              signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              paySign: content.paySign, // 支付签名
              success: function (res) {
                // 支付成功后的回调函数
                callback('支付成功')
              }
            })
          })
          WX.error(function (res) {
            callback('支付失败')
            // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
          })
        }
      }
    )
  },
  balancePay (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'who': obj.who,
      'payType': obj.payType
    }
    var url = InitData.weixinUrl + '/api/freight/finishTask'
    Vue.http.get(url, {params}).then(
      data => {
        callback(data.body)
      }
    )
  },
  evaluateOrder (obj, callback) { // 评价订单
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId,
      'content': obj.content,
      'score': obj.score
    }
    var url = InitData.weixinUrl + '/api/freight/review'
    Vue.http.post(url, params, {emulateJSON: true}).then(
      data => {
        callback(data.body)
      }
    )
  },
  queryReview (obj, callback) { // 获取评论
    var params = {
      'app_key': InitData.appKey,
      'orderId': obj.orderId
    }
    var url = InitData.weixinUrl + '/v5/huoyun/getReview'
    Vue.http.get(url, {params, _timeout: 5000}).then(
      data => {
        callback(data.body)
      }
    )
  },
  allOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId,
      'page': obj.page,
      'rows': obj.rows
    }
    Vue.http.get(InitData.weixinUrl + '/api/freight/findOrders', {params}).then((data) => {
      callback(data.body)
    })
  },
  unfilledOrder (obj, callback) {
    var params = {
      'app_key': InitData.appKey,
      'passengerId': obj.passengerId
    }
    Vue.http.get(InitData.weixinUrl + '/v5/huoyun/getNotSuccessOrders', {params}).then((data) => {
      callback(data.body)
    })
  }
}



