<template>
  <div id="datePicker" v-if="showTimePicker">
    <div class="mode"></div>
    <div class="timePick">
      <div class="bottons">
        <span @click="cancel">取消</span>
        <span>{{title}}</span>
        <span @click="timeSureFn">确定</span>
      </div>
      <div class="lines"></div>
      <div class="backColor"></div>
      <mt-picker
        :slots="timeValue"
        @change="onValuesChange"
        valueKey="name">
      </mt-picker>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'datePicker',
    // datePickerState控制显示隐藏,title:标题
    props: {
      datePickerState: { // 控制时间选择器显示隐藏
        type: Boolean,
        default () {
          return false
        }
      },
      title: { // 标题
        type: String,
        default () {
          return '请选择时间'
        }
      },
      afterHour: { // 可以设置初始值为当前小时数几小时以后
        type: Number,
        default () {
          return 0
        }
      },
      afterMinutes: {
        type: Number,
        default () {
          return 0
        }
      },
      days: { // 可设置可选天数（未限制所传的值，注意父组件控制不要超出当月时间）
        type: Array,
        default () {
          let arr = []
          let date = new Date()
          let d1 = date.getDate()
          arr = [
            {name: '今天', value: d1},
            {name: '明天', value: d1 + 1},
            {name: '后天', value: d1 + 2}
          ]
          return arr
        }
      },
      intervalMinute: { // 间隔多少秒显示
        type: Number,
        default () {
          return 10
        }
      }
    },
    data () {
      return {
        timeValue: [
          {
            flex: 1,
            values: [],
            className: 'slot1',
            textAlign: 'center'
          }, {
            flex: 1,
            values: [],
            className: 'slot2',
            textAlign: 'center'
          },
          {
            flex: 1,
            values: [],
            className: 'slot3',
            textAlign: 'center'
          }
        ],
        showTimePicker: this.datePickerState, // 本组件内控制显示
        daysValue: this.days, // 可选的天数
        hoursValue: [], // 可选的小时数
        minutesValue: [], // 可选的分钟数
        startDay: '', // 开始计时的天
        startHour: '', // 开始计算的小时数
        startMinute: '', // 开始计时的分钟数
        day: '', // 选择的天
        hour: '', // 选择的时
        minute: '', // 选择的分
        monthDays: 0 // 本月有多少天
      }
    },
    watch: {
      datePickerState (val) {
        this.showTimePicker = val
      },
      showTimePicker (val) {
        this.$emit('update:datePickerState', val)
        this.initTime()
      },
      afterMinutes (val) {
        if (val) {
          this.initTime()
        }
      }
      // daysValue (val) {
      //   this.$emit('update:days', val)
      // }
    },
    computed: {
      // daysValues: function () {
      //   alert(JSON.stringify(this.days))
      //   return this.days
      // }
    },
    methods: {
      cancel () {
        this.showTimePicker = false
      },
      timeSureFn () {
        let date = new Date()
        let Y = date.getFullYear()
        let M = date.getMonth() + 1
        if (this.hour < 10) {
          this.hour = '0' + this.hour
        }
        if (this.minute < 10) {
          this.minute = '0' + this.minute
        }
        let selectDay = this.day
        if (selectDay > this.monthDays) {
          M += 1
          selectDay = this.day - this.monthDays
        }
        let chooseDate = Y + '-' + M + '-' + selectDay + ' ' + this.hour + ':' + this.minute
        let date2 = new Date(chooseDate)
        let timeStamp = date2.getTime()
        let time = {name: chooseDate, value: timeStamp}
        this.$emit('timeSureFn', time)
        this.showTimePicker = false
      },
      // 时间改变事件
      onValuesChange (picker, values) {
        let that = this
        let date = new Date()
        // let nowD = date.getDate()
        // let nowM = this.startMinute
        // let nowDay = date.getDate()
        that.startHour = date.getHours()
        that.startMinute = date.getMinutes()
        var startM = Math.ceil((that.startMinute + that.afterMinutes) / this.intervalMinute)
        startM = startM * this.intervalMinute
        if (startM > 50) {
          that.startHour += 1
          startM = startM - 60
        }
        let startH = that.startHour
        // let nowH = this.startHour
        var hour = []
        let minuts = []
        let firstSelectDay = this.daysValue
        if (values[0].value > firstSelectDay[0].value) { // 选择日期大于第一天
          let hourName = ''
          for (let k = 0; k <= 23; k++) { // 时
            if (k < 10) {
              hourName = '0' + k
            } else {
              hourName = k
            }
            hour.push({name: `${hourName}`, value: k})
          }
          let minuteName = ''
          for (let l = 0; l < 60; l += this.intervalMinute) { // 分
            if (l < 10) {
              minuteName = '0' + l
            } else {
              minuteName = l
            }
            minuts.push({name: `${minuteName}`, value: l})
          }
        } else if (values[0].value === firstSelectDay[0].value) {  // 选择日期等于今天
          let hourName = ''
          let aa = startH + this.afterHour
          if (aa >= 24) {
            aa = aa - 24
          }
          for (let k = aa; k <= 23; k++) { // 时
            if (k < 10) {
              hourName = '0' + k
            } else {
              hourName = k
            }
            hour.push({name: `${hourName}`, value: k})
            // console.log('可选时', hour)
          }
          let hname = ''
          if (values[1].value > aa) { // 选择时大于当前时
            console.log(1)
            for (let l = 0; l < 60; l += this.intervalMinute) {
              if (l < 10) {
                hname = '0' + l
              } else {
                hname = l
              }
              minuts.push({name: `${hname}`, value: l})
            }
          } else if (values[1].value === aa) { // 选择时等于当前时
            // let startM = Math.ceil(nowM / this.intervalMinute)
            // startM = startM * this.intervalMinute
            let hhname = ''
            for (let l = startM; l < 60; l += this.intervalMinute) {
              if (l < 10) {
                hhname = '0' + l
              } else {
                hhname = l
              }
              minuts.push({name: `${hhname}`, value: l})
            }
          }
        }
        picker.setSlotValues(1, hour)
        picker.setSlotValues(2, minuts)
        if (picker.getSlotValue(0)) {
          this.day = picker.getSlotValue(0).value
        } else {
          this.day = new Date().getDate()
        }
        if (picker.getSlotValue(1)) {
          this.hour = picker.getSlotValue(1).value
        } else {
          this.hour = that.startHour
        }
        if (picker.getSlotValue(2)) {
          this.minute = picker.getSlotValue(2).value
        } else {
          this.minute = startM
        }
        // 设置选中样式-时
        if (picker.getSlotValue(1)) {
          var pickedHour = picker.getSlotValue(1).value
        }
        let hourSlots = picker.getSlotValues(1)
        var index = 0
        if (hourSlots) {
          for (let j = 0; j < hourSlots.length; j++) {
            if (hourSlots[j].value === pickedHour) {
              index = j
            }
          }
          var hFdiv1 = document.getElementsByClassName('slot2')[0]
          var hFdiv2 = hFdiv1.getElementsByClassName('picker-slot-wrapper')[0]
          var divs = hFdiv2.getElementsByClassName('picker-item')
        }
        if (divs) {
          for (let w = 0; w <= divs.length; w++) {
            if (w !== index) {
              if (divs[w]) {
                divs[w].className = 'picker-item'
              }
            } else {
              if (divs[w]) {
                divs[w].className = 'picker-item picker-selected'
                let translateH = 36 * w
                console.log('translateH', translateH)
                // hFdiv2.style.translate = `(0px, -${translateH}px)`
              }
            }
          }
        }
        // 分
        if (picker.getSlotValue(2)) {
          var pickedMinute = picker.getSlotValue(2).value
        }
        let MinuteSlots = picker.getSlotValues(2)
        var mIndex = 0
        if (MinuteSlots) {
          for (let j = 0; j < MinuteSlots.length; j++) {
            if (MinuteSlots[j].value === pickedMinute) {
              mIndex = j
            }
          }
          var mFdiv1 = document.getElementsByClassName('slot3')[0]
          var mFdiv2 = mFdiv1.getElementsByClassName('picker-slot-wrapper')[0]
          var mdivs = mFdiv2.getElementsByClassName('picker-item')
        }
        if (mdivs) {
          for (let w = 0; w <= mdivs.length; w++) {
            if (w !== mIndex) {
              if (mdivs[w]) {
                mdivs[w].className = 'picker-item'
              }
            } else {
              if (mdivs[w]) {
                mdivs[w].className = 'picker-item picker-selected'
              }
            }
          }
        }
      },
      getDates () {
        var curDate = new Date()
        /* 获取当前月份 */
        var curMonth = curDate.getMonth()
        /*  生成实际的月份: 由于curMonth会比实际月份小1, 故需加1 */
        curDate.setMonth(curMonth + 1)
        /* 将日期设置为0, 这里为什么要这样设置, 我不知道原因, 这是从网上学来的 */
        curDate.setDate(0)
        /* 返回当月的天数 */
        return curDate.getDate()
      },
      // 初始化时间
      async initTime () {
        // ===初始化时间选择器===
        let that = this
        let bb = this.getDates()
        this.monthDays = bb
        console.log('本月有多少天', this.monthDays)
        let date = new Date()
        // let nowDay = date.getDate()
        that.startHour = date.getHours()
        that.startMinute = date.getMinutes()
        // 分
        var startM = Math.ceil((that.startMinute + that.afterMinutes) / this.intervalMinute)
        startM = startM * this.intervalMinute
        let minutName = ''
        let minutes = []
        if (startM > 50) {
          let disM = startM - 60
          that.startHour += 1
          this.minute = disM
          for (let u = disM; u < 60; u += this.intervalMinute) {
            console.log(u)
            if (u < 10) {
              minutName = '0' + u
            } else {
              minutName = u
            }
            minutes.push({name: `${minutName}`, value: u})
          }
        } else {
          this.minute = startM
          for (let l = startM; l < 60; l += this.intervalMinute) {
            console.log(l)
            if (l < 10) {
              minutName = '0' + l
            } else {
              minutName = l
            }
            minutes.push({name: `${minutName}`, value: l})
          }
        }
        this.hour = that.startHour
        this.timeValue[2].values = minutes
        // 时
        let hour = []
        var hourName = ''
        let finalStartH = that.startHour + this.afterHour
        let days = await
          this.daysValue
        if (days[0]) {
          if (finalStartH > 23 && days[0].value === new Date().getDate()) {
            days.shift()
          }
        }
        if (finalStartH >= 24) {
          finalStartH = finalStartH - 24
        }
        for (let i = finalStartH; i <= 23; i++) {
          if (i < 10) {
            hourName = '0' + i
          } else {
            hourName = i
          }
          hour.push({name: `${hourName}`, value: i})
        }
        this.timeValue[1].values = hour
        // 天
        // this.timeValue[0].values = this.days
        this.timeValue[0].values = days
        // 样式
        // var hFdiv1 = document.getElementsByClassName('slot2')[0]
        // var hFdiv2 = hFdiv1.getElementsByClassName('picker-slot-wrapper')[0]
        // var divs = hFdiv2.getElementsByClassName('picker-item')
        // for (let tt = 0; tt < divs.length; tt++) {
        //   if (divs[tt].innerHTML === this.timeValue[1].values[0].value) {
        //     divs[tt].className = 'picker-item picker-selected'
        //   }
        // }
        // console.log(divs[0].innerHTML)
      }
    },
    created: function () {
    },
    mounted: function () {
      this.initTime()
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  #datePicker {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    .mode {
      width: 100vw;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.5);
    }
    .lines {
      width: 100%;
      height: 30px;
      border-top: 1px solid #efefef;
      border-bottom: 1px solid #efefef;
      position: absolute;
      top: 8.2rem;
      left: 0;
    }
    /*.backColor {*/
    /*width: 100%;*/
    /*height: 100%;*/
    /*background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2), rgba(0, 0, 0, 0.1));*/
    /*}*/
    .timePick {
      width: 100%;
      /*padding-bottom: 20px;*/
      position: fixed;
      bottom: 0;
      left: 0;
      background-color: #fff;
      height: 200px;
      .bottons {
        display: flex;
        justify-content: space-around;
        color: #000;
        font-size: 1.2rem;
        height: 2rem;
        align-items: center;
        padding: 0.5rem 0;
        /*margin-top: 1rem;*/
        span {
          display: inline-block;
        }
        span:first-child {
          color: #999999;
        }
        span:last-child {
          color: #f06553;
        }
      }
      .picker {
        display: block;
        /*width: 100%;*/
        /*height: 90%;*/
        position: absolute;
        bottom: 0;
        margin-top: 35px;
        .picker-items {
          display: flex;
          font-size: 1.4rem;
          color: #999999;
          .picker-selected {
            color: #000000;
          }
        }
      }
    }
  }
</style>



