/**等待中**/
<template>
  <div class="PTawait">
    <div class="PTawaitContent" v-if="!loadState">
      <div class="loads">
        <span class="Type">{{PtQueryOrder ? PtQueryOrder.orderType : ''}}</span>
        <div class="animation">
          <span class="animationContent">
            <img src="../../../assets/img/paotui/jiazai.png"
                 :style="{transform:'translate(-50%, -50%) rotate('+jiazaiNum+'deg)'}">
            <p>等待中</p>
          </span>
        </div>
        <h2>请稍后正在为您联络服务人员</h2>
        <span class="cancelOrder" @click="cancelOrderFn">取消订单</span>
      </div>
      <div class="orderInform">
        <div class="row time" v-if="PtQueryOrder&&statrTimeFn">
          <i class="iconfont icon-shijian"></i>
          <span class="val"><p>{{PtQueryOrder ? statrTimeFn : ''}}</p></span>
        </div>
        <div class="row startAdd"
             v-if="PtQueryOrder && (PtQueryOrder.startAddress!=='' || PtQueryOrder.startDetailed!=='')">
          <div class="top" v-if="PtQueryOrder&&PtQueryOrder.startAddress!==''">
            <i class="iconfont icon-yuan"></i>
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.startAddress : ''}}</p></span>
          </div>
          <div class="bottom" v-if="PtQueryOrder&&PtQueryOrder.startDetailed!==''">
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.startDetailed : ''}}</p></span>
          </div>
        </div>
        <div class="row endAdd" v-if="PtQueryOrder&& (PtQueryOrder.endAddress!==''||PtQueryOrder.endDetailed!=='')">
          <div class="top" v-if="PtQueryOrder&&PtQueryOrder.endAddress!==''">
            <i class="iconfont icon-yuan"></i>
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.endAddress : ''}}</p></span>
          </div>
          <div class="bottom" v-if="PtQueryOrder&&PtQueryOrder.endDetailed!==''">
            <span class="val"><p>{{PtQueryOrder ? PtQueryOrder.endDetailed : ''}}</p></span>
          </div>
        </div>
        <div class="row more" :class="moreState?'moreShow':''" v-if="PtQueryOrder&&PtQueryOrder.buyNeeds!==''">
          <div class="moreBut" @click="moreFn"></div>
          <div class="moreContent">
            <div class="list" v-if="PtQueryOrder&&PtQueryOrder.buyNeeds!==''">
              <h3>购买需求</h3>
              <p>{{PtQueryOrder ? PtQueryOrder.buyNeeds : ''}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <xk-input-box v-if="reson.state" title="取消订单理由" @click="cancelOrderFn" :closeState="true">
      <div slot="content" class="DjCancelOrder">
        <textarea v-model="reson.val" placeholder="请输入取消订单理由">
        </textarea>
      </div>
    </xk-input-box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod,
      XkInputBox
    },
    name: '',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        PtQueryOrder: 'PtQueryOrder'
      }),
      statrTimeFn: function () {
        if (this.PtQueryOrder.startTime) {
          return Toucth.TimestampDate(this.PtQueryOrder.startTime)
        }
      }
    },
    data () {
      return {
        reson: {
          state: false,
          val: ''
        },
        jiazaiNum: 0,
        moreState: false,
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    watch: {
      PtQueryOrder (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/paotui')
          return
        }
        that.loadState = false
        if (val.status === 0 || val.status === 1 && Toucth.AcquireUrlParameter().orderId) {
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/paotui/await') {
              that.$store.dispatch('PtQueryOrder', {
                orderId: Toucth.AcquireUrlParameter().orderId
              })
            }
          }, 3000)
        }
        if (val.status === 2 || val.status === 3 && Toucth.AcquireUrlParameter().orderId) {
          that.$router.push({path: '/paotui/server', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
        if (val.status === 4) {
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/paotui/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          }, 1500)
        }
        if (val.status === 5) {
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/paotui'})
            return false
          }, 1500)
        }
      }
    },
    methods: {
      moreFn: function () {
        this.moreState = !this.moreState
      },
      cancelOrderFn: function (val) {
        let that = this
        this.reson.state = true
        if (val === 'affirm') {
          if (this.reson.val !== '') {
            that.loadState = true
            this.$store.dispatch('PtCancelOrder', {
              orderId: Toucth.AcquireUrlParameter().orderId,
              reson: this.reson.val,
              fn: (state) => {
                that.loadState = false
                if (state && state.success) {
                  this.reson.state = false
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单成功！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/paotui'})
                    return false
                  }, 1500)
                }
              }
            })
          } else {
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请输入销单理由！'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        }
        if (val === 'cancel') {
          this.reson.state = false
        }
      }
    },
    mounted: function () {
      let that = this
      that.loadState = true

      let num = 0

      function jiazai () {
        setTimeout(function () {
          num += 10
          that.jiazaiNum = num
          jiazai()
        }, 29)
      }

      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('PtQueryOrder', {
            orderId: Toucth.AcquireUrlParameter().orderId
          })
        }
      })
      jiazai()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .PTawait {
    width: 100%;
    .PTawaitContent {
      width: 100%;
      .loads {
        text-align: center;
        background-color: #fff;
        padding-bottom: 3rem;
        .Type {
          margin-top: 3.75rem;
          height: 1.8rem;
          line-height: 1.9rem;
          padding: 0 0.8333rem;
          background-color: #f06553;
          color: #fff;
          border-radius: 1.8/2rem;
          font-size: 0.8333rem;
        }
        .animation {
          margin: 3rem 0;
          .animationContent {
            width: 5.7rem;
            height: 5.7rem;
            position: relative;
            img {
              position: absolute;
              left: 50%;
              top: 50%;
              width: 100%;
              transform: translate(-50%, -50%);
              transform-origin: 50% 50%;
            }
            p {
              position: absolute;
              width: 5rem;
              font-size: 0.8333rem;
              color: #999999;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
            }
          }
        }
        h2 {
          font-size: 1.25rem;
          color: #333333;
          margin-bottom: 1rem;
        }
        .cancelOrder {
          color: #f06553;
          font-size: 0.833rem;
        }
      }
      .orderInform {
        margin-top: 1rem;
        padding: 0 1rem;
        padding-bottom: 3rem;
        background-color: #fff;
        .row {
          line-height: 5rem;
          border-bottom: 1px solid #eee;
          padding: 0 1rem;
          .iconfont {
            margin-right: 0.1rem;
          }
          .val {
            font-size: 1.3rem;
            color: #333333;
          }
        }
        .time {
          .iconfont {
            color: #fab10a;
          }
        }
        .startAdd, .endAdd {
          padding: 0 1rem;
          line-height: 2rem;
          .bottom {
            .val {
              font-size: 1rem;
              padding-left: 1.6rem;
              color: #999999;
            }
          }
        }
        .startAdd {
          padding-top: 1.2rem;
          padding-bottom: 0.3rem;
          border-bottom: none;
          .iconfont {
            color: #3cafb9;
          }

        }
        .endAdd {
          padding-bottom: 1.8rem;
          padding-top: 0.3rem;
          border: none;
          .iconfont {
            color: #f06553;
          }
        }
        .more {
          position: relative;
          height: auto;
          margin-top: 0.5rem;
          transition: 0.3s all;
          border-top: 1px solid #eee;
          border-bottom: none;
          .moreBut {
            display: inline-block;
            position: absolute;
            left: 50%;
            top: -1.7rem;
            transform-origin: 50% 50%;
            transform: translate(-50%, 0) scaleY(1);
            width: 3.5rem;
            height: 3.5rem;
            background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
            background-size: 2rem 2rem;
            transition: 0.3s all;
            border-radius: 50%;
            overflow: hidden;
          }
          .moreContent {
            height: 0;
            overflow: hidden;
          }
        }
        .moreShow {
          .moreBut {
            transform: translate(-50%, 0) scaleY(-1);
          }
          .moreContent {
            height: auto;
            line-height: 2.5rem;
            padding: 1rem 0;
            padding-top: 1.8rem;
            text-align: center;
            .list {
              h3 {
                font-size: 1.25rem;
                color: #333;
              }
              p {
                font-size: 1rem;
                line-height: 1.3rem;
                color: #999999;
              }
            }
          }
        }
      }
    }
    .DjCancelOrder {
      border: 1px solid #eee;
      height: 10rem;
      textarea {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        border: none;
        resize: none;
        font-size: 1rem;
        padding: 0.5rem;
      }
    }
  }
</style>



