/**分享**/
<template>
  <div class="share">
    <div class="QrCode">
      <xk-qr-code :val="url"></xk-qr-code>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import XkQrCode from '../module/qrCode/src/qrCode'

  export default {
    name: 'qrCode',
    components: {XkQrCode},
    computed: {
      ...mapGetters({
        loginData: 'loginData'
      })
    },
    data () {
      return {
        url: ''
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('queryQrCode', {
            company_id: that.loginData.passenger.company_id,
            id: that.loginData.passenger.id,
            fn (data) {
              if (data.code === 1) {
                that.url = data.url
              }
            }
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .share {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background: url(../../assets/img/erweima.png) no-repeat center #fe4e4e;
    background-size: 100%;
    position: relative;
    .QrCode {
      position: absolute;
      left: 50%;
      top: 52%;
      transform: translate(-50%, -50%);
      width: 50%;
      border-radius: 0.333rem;
      overflow: hidden;
      img {
        width: 100%;
        border-radius: 0.333rem;
      }
    }
  }
</style>



