<script>
  import Toucth from '../../../assets/js/method'
  import XkRollBanner from '../../module/rollBanner/src/rollBanner'
  import XkTypeTabs from '../../module/typeTabs/src/typeTabs'
  import XkSiteInput from '../../module/siteInput/src/siteInput'
  import XkCarType from '../../module/carType/src/carType'
  import {mapGetters} from 'vuex'
  import XkDatePickerMod from '../../module/datePickerMod/src/datePickerMod'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import XkCouponSelect from '../../module/couponSelect/src/couponSelect'
  import XkUnfilledOrderMod from '../../module/unfilledOrderMod/src/unfilledOrderMod'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'
  import XkDetailsBox from '../../module/detailsBox/src/detailsBox.vue'
  import xkMyPicker from '../../module/myPicker/myPicker'
  import datePicker from '../../module/datePicker/datePicker02'
  // import Cookie from 'js-cookie'

  export default {
    components: {
      XkDetailsBox,
      XkHintBox,
      XkUploadMod,
      XkUnfilledOrderMod,
      XkCouponSelect,
      XkInputBox,
      XkDatePickerMod,
      XkCarType,
      XkSiteInput,
      XkTypeTabs,
      XkRollBanner,
      xkMyPicker,
      datePicker
    },
    name: 'ZhuanChe',
    computed: {
      ...mapGetters({
        zhuancheBanner: 'zhuancheBanner',
        ZcQueryCar: 'ZcQueryCar',
        ZcQueryWaiter: 'ZcQueryWaiter',
        addressRecord: 'addressRecord',
        // ZcBudgetCost: 'ZcBudgetCost',
        wxState: 'wxState',
        companyInfo: 'companyInfo',
        ZcOrderId: 'ZcOrderId',
        ZcCoupon: 'ZcCoupon',
        positioning: 'positioning',
        loginData: 'loginData',
        unfilledOrderState: 'unfilledOrderState',
        unfilledOrder: 'unfilledOrder',
        v5ZcBusTypeAndCarType: 'v5ZcBusTypeAndCarType',
        zcCouponsGetter: 'zcCouponsGetter'
      }),
      unfilledOrderFn: function () { // 准备未完成订单数据
        let unfilledOrder = []
        // let zhuancheData = this.unfilledOrder ? this.unfilledOrder.zhuanche : null
        let zhuancheData = this.unfilledOrder ? this.unfilledOrder : null
        if (zhuancheData && zhuancheData.length > 0) {
          let obj = {}
          for (let i = 0; i < zhuancheData.length; i++) {
            obj = {
              startAddress: zhuancheData[i].book_address,
              endAddress: zhuancheData[i].destination,
              serverTime: Toucth.TimestampDate(zhuancheData[i].book_time),
              status: ''
            }
            if (zhuancheData[i].business === 'zhuanche') {
              switch (zhuancheData[i].status) {
                case 1:
                  obj.status = {
                    name: '新单',
                    router: {path: '/zhuanche/await', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 5:
                  obj.status = {
                    name: '已派单',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 10:
                  obj.status = {
                    name: '已接单',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 15:
                  obj.status = {
                    name: '前往预约地',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 20:
                  obj.status = {
                    name: '到达预约地',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 25:
                  obj.status = {
                    name: '前往目的地',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 28:
                  obj.status = {
                    name: '中途等待',
                    router: {path: '/zhuanche/service', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 30:
                  obj.status = {
                    name: '到达目的地',
                    router: {path: '/zhuanche/settlement', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 35:
                  obj.status = {
                    name: '已结算',
                    router: {path: '/zhuanche/affirm', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 40:
                  obj.status = {
                    name: '已评价',
                    router: {path: '/zhuanche/checkOrder', query: {orderId: zhuancheData[i].id}}
                  }
                  break
                case 45:
                  obj.status = {
                    name: '已销单',
                    router: {path: '/zhuanche', query: {orderId: zhuancheData[i].id}}
                  }
                  break
              }
              unfilledOrder.push(obj)
            }
          }
        }
        return unfilledOrder
      },
      ZcBudgetFn: function () {
        let that = this
        let money = 0
        // let num = 0
        let choosedConpon = that.couponMoney
        let budgetCost = that.v5zcBudgetCost
        // 计算费用
        if (choosedConpon && choosedConpon.id && choosedConpon.id !== '') {
          if (choosedConpon.type === 'dikou') {
            money = budgetCost.total - choosedConpon.val
          } else if (choosedConpon.type === 'dazhe') {
            money = (budgetCost.total) * (choosedConpon.val / 10 / 10) // val为百分数，如80%，val为80，折扣为8折
          }
        } else {
          money = budgetCost.total
        }
        // 费用详情
        let budgetDetail = []
        if (that.v5zcBudgetCost.startPrice) {
          budgetDetail.push({
            name: '起步价',
            val: that.v5zcBudgetCost.startPrice
          })
        }
        if (that.v5zcBudgetCost.distancePrice) {
          budgetDetail.push({
            name: '里程费',
            val: that.v5zcBudgetCost.distancePrice
          })
        }
        if (that.v5zcBudgetCost.timePrice) {
          budgetDetail.push({
            name: '时间费',
            val: that.v5zcBudgetCost.timePrice
          })
        }
        if (that.v5zcBudgetCost.total) {
          budgetDetail.push({
            name: '订单总金额',
            val: that.v5zcBudgetCost.total
          })
        }
        if (budgetCost && choosedConpon && choosedConpon.id && choosedConpon.id !== '') {
          if (choosedConpon.type === 'dikou') {
            let dikoufee = (choosedConpon.val).toFixed(1)
            budgetDetail.push({
              name: '优惠券抵扣',
              val: dikoufee
            })
          } else if (choosedConpon.type === 'dazhe') {
            let zhekoufee = ((budgetCost.total) * (1 - choosedConpon.val / 10 / 10)).toFixed(1)
            budgetDetail.push({
              name: '优惠券抵扣',
              val: zhekoufee
            })
          }
        }
        that.BudgetCostDetails = budgetDetail
        // 返回money
        let lastmoney = money ? money.toFixed(1) : 0
        return lastmoney
      },
      addressRecordFn: function () {
        if (this.addressRecord.zhuanche[this.typeAllAc] && this.addressRecord.zhuanche[this.typeAllAc].length > 0) {
          return this.addressRecord.zhuanche[this.typeAllAc]
        }
      }
    },
    data () {
      return {
        startAfterMinute: 0, // 可选择的开始时间大于当前的分钟数
        timeSelectVilable: true, // 是否可预约（可选择时间）
        showCarTypeSelectPicker: true,
        showCarSelecter: false,
        testCarSelectItems: [
          {name: '243', val: 21423, id: 1, key: 214},
          {name: '243', val: 21423, id: 1, key: 214},
          {name: '243', val: 21423, id: 1, key: 214},
          {name: '243', val: 21423, id: 1, key: 214},
          {name: '243', val: 21423, id: 1, key: 214}
        ],
        typeAll: {
          action: 0,
          data: [
            {
              icon: '',
              name: '立即用车',
              anchor: 'tangzu'
            },
            {
              icon: '',
              name: '接机',
              anchor: 'jieji'
            },
            {
              icon: '',
              name: '送机',
              anchor: 'songji'
            },
            {
              icon: '',
              name: '接站',
              anchor: 'jiezhan'
            },
            {
              icon: '',
              name: '送站',
              anchor: 'songzhan'
            },
            {
              icon: '',
              name: '日租',
              anchor: 'rizu'
            },
            {
              icon: '',
              name: '半日租',
              anchor: 'banrizu'
            }
          ]
        },
        typeAllAc: 'tangzu',
        others: { // 他人
          showState: false,
          state: true,
          othersPhone: '',
          othersName: '',
          recordPhone: '',
          recordName: ''
        },
        couponNum: '6.00',
        startAdd: {
          startLng: '',
          startLat: '',
          startAddress: '',
          startAddressDetail: ''
        },
        endAdd: {
          endLng: '',
          endLat: '',
          endAddress: '',
          endAddressDetail: ''
        },
        selectCar: '',
        startTime: {
          val: '',
          name: ''
        },
        flightNo: '',
        trainNo: '',
        couponMoney: {
          type: '',
          id: '',
          num: ''
        },
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        carIdAcFn: [],
        datePickerState: false,
        BudgetCostDetails: [],
        startAddState: false,
        v5zcBudgetCost: 0,
        bussinessType: {
          data: ''
        },
        carTypes: '',
        carTypeAc: 0, // 选择车型active
        choosedCarType: '', // 选中的车型
        nowBussinessType: null, // 当前业务类型
        nearDrivers: { // 附近司机
          drivers: '',
          driversCount: 0
        }
      }
    },
    watch: {
      ZcQueryCar (val) {
        if (val && val.data && val.data.length > 0) {
          this.selectCar = (JSON.stringify([{
            'areaId': val ? val.data[0].serverTypes[0].priceId : '',
            'typeId': val ? val.data[0].id : ''
          }]))
          this.carIdAcFn.push(val.data[0].id)
          // this.queryWaiterFn()
          // this.ZcBudgetCostFn()
        }
      },
      unfilledOrder (val) { // 如果有未结算的专车订单，路由跳转到结算页面
        let that = this
        if (val && val.length > 0) {
          val.forEach(function (row) {
            if (Number(row.status) === 30) { // 专车订单待结算
              if (row.business === 'zhuanche') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '您有未支付的专车订单'
                }
                setTimeout(function () {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/zhuanche/settlement', query: {orderId: row.id}})
                }, 1500)
              }
              if (row.business === 'daijia') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '您有未支付的代驾订单'
                }
                setTimeout(function () {
                  that.hintBox.stateShow = false
                  that.$router.push({path: '/daijia/settlement', query: {order_id: row.id}})
                }, 1500)
              }
            }
          })
        }
      },
      ZcOrderId (val) {
        if (val) {
          let that = this
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '下单成功！'
          }
          setTimeout(() => {
            that.$store.dispatch('addressRecord', {
              type: 'zhuanche',
              sonType: that.typeAllAc,
              address: {
                name: that.endAdd.endAddress,
                detailedAddress: that.endAdd.endAddressDetail,
                lat: that.endAdd.endLat,
                lng: that.endAdd.endLng
              },
              recordTime: new Date().valueOf()
            })
            that.hintBox.stateShow = false
            if (val.indexOf(',') > 0) {
              this.$router.push({path: '/orderRecord/yongche'})
            } else {
              this.$router.push({path: '/zhuanche/await', query: {orderId: val}})
            }
          }, 1300)
        }
      },
      loginData (val) {
        if (val) {
          let that = this
          if (val && val.passenger && that.others.othersPhone === '' && that.others.recordPhone === '') {
            // that.others.recordPhone = val.passenger.phone
            that.unfilledOrderShowState = true
          }
        }
      },
      positioning (val) {
        if (val) {
          let that = this
          if (val && val.name && val.lng && val.lat) {
            that.startAdd = {
              startLng: that.positioning.lng,
              startLat: that.positioning.lat,
              startAddress: that.positioning.name,
              startAddressDetail: that.positioning.detailedAddress
            }
          } else {
            that.startAdd = {
              startLng: '',
              startLat: '',
              startAddress: '获取定位失败',
              startAddressDetail: ''
            }
          }
          // that.ZcBudgetCostFn()
        }
      },
      'typeAllAc': function (val) {
        let that = this
        if (val) {
          let startAddFn = () => {
            that.startAdd = {
              startLng: that.positioning.lng,
              startLat: that.positioning.lat,
              startAddress: that.positioning.name,
              startAddressDetail: that.positioning.detailedAddress
            }
          }

          let endAddFn = () => {
            that.endAdd = {
              endLng: '',
              endLat: '',
              endAddress: '',
              endAddressDetail: ''
            }
          }

          var positioning = that.positioning

          if (positioning.name !== '获取定位失败' && positioning.city !== '选择城市') {
            if (val === 'jieji') {
              that.$store.dispatch('baiduMapSite', {
                query: '机场',
                city: that.positioning.city,
                fn: (data) => {
                  let obj = data[0]
                  that.startAdd.startLng = obj.lng
                  that.startAdd.startLat = obj.lat
                  that.startAdd.startAddress = obj.name
                  that.startAdd.startAddressDetail = obj.detailedAddress
                }
              })
              endAddFn()
            }
            if (val === 'songji') {
              that.$store.dispatch('baiduMapSite', {
                query: '机场',
                city: that.positioning.city,
                fn: (data) => {
                  let obj = data[0]
                  that.endAdd.endLng = obj.lng
                  that.endAdd.endLat = obj.lat
                  that.endAdd.endAddress = obj.name
                  that.endAdd.endAddressDetail = obj.detailedAddress
                  // that.ZcBudgetCostFn()
                }
              })
            }
            if (val === 'jiezhan') {
              that.$store.dispatch('baiduMapSite', {
                query: '火车',
                city: that.positioning.city,
                fn: (data) => {
                  let obj = data[0]
                  that.startAdd.startLng = obj.lng
                  that.startAdd.startLat = obj.lat
                  that.startAdd.startAddress = obj.name
                  that.startAdd.startAddressDetail = obj.detailedAddress
                }
              })
              endAddFn()
            }
            if (val === 'songzhan') {
              that.$store.dispatch('baiduMapSite', {
                query: '火车',
                city: that.positioning.city,
                fn: (data) => {
                  let obj = data[0]
                  that.endAdd.endLng = obj.lng
                  that.endAdd.endLat = obj.lat
                  that.endAdd.endAddress = obj.name
                  that.endAdd.endAddressDetail = obj.detailedAddress
                }
              })
              startAddFn()
            }
          }
          that.startAddState = true
          startAddFn()
          endAddFn()
        }
        // that.ZcBudgetCostFn()
      }
    },
    methods: {
      sureSelectCar (val) { // 选择车型-确认选中
        this.showCarSelecter = false
        this.choosedCarType = val[0]
        this.getV5ZcBudgetCostFn(this.nowBussinessType.id, this.choosedCarType.id)
      },
      selectedStartTime (time) { // 时间选中
        if (time) {
          this.startTime.val = time.value
          this.startTime.name = time.name
          this.getV5ZcBudgetCostFn()
        }
        this.datePickerState = false
      },
      cancleSelectCar (val) { // 选择车型-取消选择
        this.showCarSelecter = val
      },
      chooseCarTypeFn () { // 选择车型
        this.showCarSelecter = true
      },
      typeTabFn: function (obj) { // 业务类型菜单
        let that = this
        let busId = obj.businessId
        this.nowBussinessType = obj.data // 赋值业务
        this.startAfterMinute = this.nowBussinessType.min_book_time
        this.timeSelectVilable = this.nowBussinessType.is_book
        that.findNearDrivers(that.nowBussinessType.id)
        // 重置开始时间
        // let date = new Date()
        // let d = date.getDate()
        // let h = date.getHours()
        // let m = date.getMinutes()
        // m += that.startAfterMinute
        // if (m >= 60) {
        //   h += 1
        //   m = Math.ceil((m - 60) / 10) * 10
        // } else {
        //   m = Math.ceil(m / 10) * 10
        // }
        // let nowTime = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + d + ' ' + h + ':' + m + ':' + date.getSeconds()
        // let nowTimeVal = (new Date(nowTime).getTime()) / 1000
        // that.startTime = {
        //   val: nowTimeVal,
        //   name: nowTime
        // }
        if (that.timeSelectVilable === 1) {
          that.startTime = {
            val: '',
            name: '选择预约时间'
          }
        }
        if (that.timeSelectVilable === 2) {
          that.startTime = {
            val: new Date().getTime(),
            name: '现在出发'
          }
        }
        that.choosedCarType = '' // 取消选中的车型
        if (that.positioning && that.positioning.lng && that.positioning.lat) { // 重置起点地址
          that.startAdd = {
            startLng: that.positioning.lng,
            startLat: that.positioning.lat,
            startAddress: that.positioning.name,
            startAddressDetail: that.positioning.detailedAddress
          }
        }
        that.endAdd = { // 清空终点地址
          endLng: '',
          endLat: '',
          endAddress: '',
          endAddressDetail: ''
        }
        let busTypes = this.bussinessType.data
        busTypes.forEach(function (item) { // 重置carTypes
          if (item.id === busId) {
            that.carTypes = item.models
          }
        })
        let typeId = that.nowBussinessType ? that.nowBussinessType.id : null
        let modelId = that.carTypes && that.carTypes.length > 0 ? that.carTypes[0].id : null
        that.getV5ZcBudgetCostFn(typeId, modelId)
      },
      submitOrderFn: function () { // 立即预约
        let that = this
        this.$store.dispatch('checkLogin', (state) => {
          if (state && that.loginData && that.loginData.passenger) {
            let checkedCar = ''
            if (this.choosedCarType) {
              checkedCar = this.choosedCarType
            } else if (this.carTypes && this.carTypes.length > 0) {
              checkedCar = this.carTypes[0]
            }
            let startTImeVal = null
            // 限制最早下单时间
            let date = new Date()
            let nowH = date.getHours()
            let nowM = date.getMinutes()
            let minStartM = nowM + this.startAfterMinute
            minStartM = Math.ceil(minStartM / 10) * 10
            if (minStartM >= 60) {
              nowH += 1
              minStartM = minStartM - 60
            }
            let newDate = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + nowH + ':' + minStartM + ':' + '00'
            let newTimeStamp = new Date(newDate).getTime()
            if (this.timeSelectVilable === 1) {
              if (!that.startTime.val) {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '请选择预约时间！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
                return false
              }
              if (that.startTime.val && that.startTime.val < newTimeStamp) {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '您选中时间无效，请重新选择预约时间'
                }
                setTimeout(function () {
                  that.hintBox.stateShow = false
                }, 1500)
              }
              if (that.startTime.val && that.startTime.val >= newTimeStamp) {
                startTImeVal = that.startTime && that.startTime.val ? Math.floor(that.startTime.val / 1000) : null
              }
            }
            if (this.timeSelectVilable === 2) {
              startTImeVal = Math.floor((new Date().getTime()) / 1000)
            }
            let obj = {
              'book_time': startTImeVal, // 预约时间
              'book_address': that.startAdd.startAddressDetail, // 预约地址
              'book_address_lat': Number(that.startAdd.startLat),
              'book_address_lng': Number(that.startAdd.startLng),
              'destination': that.endAdd.endAddressDetail, // 目的地
              'destination_lat': Number(that.endAdd.endLat),
              'destination_lng': Number(that.endAdd.endLng),
              'budget_fee': that.ZcBudgetFn ? that.ZcBudgetFn : '0',
              'cid': that.nowBussinessType ? Number(that.nowBussinessType.id) : null, // 订单类型id
              'coupon_id': that.couponMoney && that.couponMoney.id ? that.couponMoney.id : null,
              'channelName': 'wechat',
              'order_person': that.loginData.passenger ? that.loginData.passenger.name : '', // 下单人姓名
              'order_person_id': that.loginData.passenger ? Number(that.loginData.passenger.id) : null,
              'passenger_phone': that.loginData.passenger ? that.loginData.passenger.phone : '',
              'user_name': that.others.recordName ? that.others.recordName : that.loginData.passenger ? that.loginData.passenger.name : '', // 使用人姓名
              'user_phone': that.others.recordPhone ? that.others.recordPhone : that.loginData.passenger ? that.loginData.passenger.phone : '',
              'number': 1, // 下单量
              'car_type': Number(checkedCar.id),
              'pri_user_phone': 1, // 是否优先联系订单发起人
              fn: (data) => {
                that.loadState = false
                if (data.code === 1) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '下单成功'
                  }
                  setTimeout(function () {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche/await', query: {orderId: data.ids[0]}})
                  }, 300)
                } else if (data.code === 30014) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '你已被加入黑名单！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1300)
                } else if (data.code === 20091) {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '你的并行订单量已经达到上限！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1300)
                } else {
                  that.hintBox = {
                    stateShow: true,
                    state: 'no',
                    content: '下单失败！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1300)
                }
              }
            }
            // that.loadState = true
            if (that.startAdd.startAddress === '' || that.startAdd.startAddress === '获取定位失败') {
              if (that.startAdd.startLng !== '' && that.startAdd.startLat !== '') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '请选择起点位置！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
                return false
              }
            }
            if (that.endAdd.endAddress === '' || that.endAdd.endAddress === '获取定位失败') {
              if (that.endAdd.startLng !== '' && that.endAdd.startLat !== '') {
                that.hintBox = {
                  stateShow: true,
                  state: 'no',
                  content: '请选择终点位置！'
                }
                setTimeout(() => {
                  that.hintBox.stateShow = false
                }, 1500)
                return false
              }
            }
            if (!checkedCar || checkedCar === '') {
              that.hintBox = {
                stateShow: true,
                state: 'no',
                content: '请选择车辆！'
              }
              setTimeout(() => {
                that.hintBox.stateShow = false
              }, 1500)
              return false
            }
            that.$store.dispatch('createZcOrder', obj)
          } else {
            that.$store.dispatch('loginAction', {state: false})
          }
        })
      },
      startAddFn: function (obj) { // 开始地址
        this.startAdd.startLng = obj.lng
        this.startAdd.startLat = obj.lat
        this.startAdd.startAddress = obj.name
        this.startAdd.startAddressDetail = obj.detailedAddress
        this.getV5ZcBudgetCostFn()
      },
      endAddFn: function (obj) { // 结束地址
        this.endAdd.endLng = obj.lng
        this.endAdd.endLat = obj.lat
        this.endAdd.endAddress = obj.name
        this.endAdd.endAddressDetail = obj.detailedAddress
        this.getV5ZcBudgetCostFn()
      },
      async getV5ZcBudgetCostFn (typeId, modelId) { // v5专车获取预算金额
        try {
          let that = this
          let disMap = await that.calMapDistance()
          if (!typeId) {
            typeId = this.v5ZcBusTypeAndCarType.types && this.v5ZcBusTypeAndCarType.types[0].id ? this.v5ZcBusTypeAndCarType.types[0].id : that.nowBussinessType ? that.nowBussinessType.id : null
          }
          if (!modelId) {
            modelId = this.v5ZcBusTypeAndCarType.types && this.v5ZcBusTypeAndCarType.types[0].models[0].id ? this.v5ZcBusTypeAndCarType.types[0].models[0].id : that.carTypes ? that.carTypes.id : null
          }
          this.$store.dispatch('getV5ZcBudgetCost', {
            distance: disMap.mileage, // 行驶公里数
            time: disMap.time, // 行驶时间
            order_time: that.startTime.val ? that.startTime.val : new Date().getTime(), // 预约时间
            typeId: typeId, // 业务类型id
            model_id: modelId, // 车型ID
            channel: true,
            fn (data) {
              if (data.code === 1) {
                // 费用
                that.v5zcBudgetCost = data.budgetFee
              }
            }
          })
        } catch (e) {
          alert(e)
        }
      },
      calMapDistance: function () { // 计算公里数
        return new Promise((resolve, reject) => {
          let that = this
          let origins = ''
          let destinations = ''
          let startCity = ''
          let endCity = ''
          let startAddr = {
            startLng: '',
            startLat: '',
            startAddress: '',
            startAddressDetail: ''
          }
          if (that.startAdd.startLat !== '' && that.startAdd.startLng !== '') {
            startAddr = {
              startLng: that.startAdd.startLng,
              startLat: that.startAdd.startLat,
              startAddress: that.startAdd.startAddress,
              startAddressDetail: that.startAdd.startAddressDetail
            }
          } else if (that.positioning && that.positioning.lng && that.positioning.lat) {
            startAddr = {
              startLng: that.positioning.lng,
              startLat: that.positioning.lat,
              startAddress: that.positioning.name,
              startAddressDetail: that.positioning.detailedAddress
            }
          }
          if (startAddr.startLat !== '' && startAddr.startLng !== '') {
            origins = startAddr.startLat + ',' + startAddr.startLng
            startCity = startAddr.startAddress
            if (that.endAdd.endLat && that.endAdd.endLng) {
              destinations = that.endAdd.endLat + ',' + that.endAdd.endLng
              endCity = that.endAdd.endAddress
            } else {
              destinations = startAddr.startLat + ',' + startAddr.startLng
              endCity = startAddr.startAddress
            }
            that.$store.dispatch('countLine', {
              origin: origins,
              destination: destinations,
              origin_region: startCity.replace('市', ''),
              destination_region: endCity.replace('市', ''),
              fn: (data) => {
                if (data.status === 0) {
                  let time = 0
                  let mileage = 0
                  let result = data.result
                  if (result.routes && result.routes.length > 0) {
                    time = (result.routes[0].duration / 60).toFixed(0)
                    mileage = (result.routes[0].distance / 1000).toFixed(1)
                  }
                  resolve({
                    time: time,
                    mileage: mileage
                  })
                } else {
                  reject('无法计算距离')
                }
              }
            })
          } else {
            reject('没有起点地址，无法计算距离')
          }
        })
      },
      getBussinessType () { // v5专车获取业务类型及车型
        let that = this
        this.$store.dispatch('ZcBussinessType', {
          company_id: that.loginData && that.loginData.passenger ? that.loginData.passenger.company_id : null,
          fn (data) {
            if (data.code === 1) {
              that.bussinessType.data = data.types
              if (data.types && data.types.length > 0) {
                // 初始化子业务
                that.nowBussinessType = data.types[0]
                // 初始化车型
                that.carTypes = data.types[0].models
                // 优惠券
                that.v5GetCoupons(20, 1, that.nowBussinessType.id)
                // 查询附近司机
                that.findNearDrivers(that.nowBussinessType.id)
                // 计算预算金额
                that.getV5ZcBudgetCostFn(that.nowBussinessType.id, that.carTypes.id)
                // 开始时间
                // let date = new Date()
                // let d = date.getDate()
                // let h = date.getHours()
                // let m = date.getMinutes()
                // m += that.startAfterMinute
                // if (m >= 60) {
                //   h += 1
                //   m = Math.ceil((m - 60) / 10) * 10
                // } else {
                //   m = Math.ceil(m / 10) * 10
                // }
                // let nowTime = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + d + ' ' + h + ':' + m + ':' + date.getSeconds()
                // let nowTimeVal = (new Date(nowTime).getTime()) / 1000
                // that.startTime = {
                //   val: nowTimeVal,
                //   name: nowTime
                // }
                // 预定延迟时间
                that.startAfterMinute = that.nowBussinessType.min_book_time
                // 是否可预约
                that.timeSelectVilable = that.nowBussinessType.is_book
                if (that.timeSelectVilable === 1) {
                  that.startTime = {
                    val: '',
                    name: '选择预约时间'
                  }
                }
                if (that.timeSelectVilable === 2) {
                  that.startTime = {
                    val: new Date().getTime(),
                    name: '现在出发'
                  }
                }
              }
            }
          }
        })
      },
      v5GetCoupons (limit, page, serviceType) { // 获取v5专车优惠券,limit:限制几条，page，第几页,service_type 业务类型
        let that = this
        that.$store.dispatch('v5getCoupons', {limit: limit, page: page, service_type: serviceType, channel: true})
      },
      couponSlelectFn: function (obj) { // 优惠 state: 4未绑定 1未用(已绑定) 2已用  3失效
        let that = this
        that.couponStatic = true
        if (obj.type === 'nonuse') {
          that.couponMoney = {
            type: '',
            id: '',
            num: ''
          }
          // that.ZcBudgetCostFn()
          that.getV5ZcBudgetCostFn()
          return true
        }
        if (obj.type === 'action' && obj.obj && obj !== '') {
          if (obj.obj.coupon_type === 1 && obj.obj.discount !== '') { // coupon_type 1 打折 2 抵扣
            that.couponMoney = {
              type: 'dazhe',
              val: obj.obj.discount,
              id: obj.obj.id
            }
          } else if (obj.obj.coupon_type === 2 && obj.obj.deductible !== '') {
            that.couponMoney = {
              type: 'dikou',
              val: obj.obj.deductible,
              id: obj.obj.id
            }
          }
          // that.ZcBudgetCostFn()
          return true
        }
        // that.ZcBudgetCostFn()
      },
      othersPhoneFn: function (obj) { // 他人电话
        let that = this
        switch (obj) {
          case 'affirm':
            that.others.showState = false
            if (that.others.othersPhone === '') {
              that.others.state = true
              that.others.recordPhone = ''
              that.others.recordPhone = that.$store.state.mc.phoneNo
              // that.others.recordName = that.$store.state.mc.phoneName
              return false
            }
            if (that.others.othersPhone !== that.$store.state.mc.phoneNo) {
              that.others.state = false
              that.others.recordPhone = that.others.othersPhone
              that.others.recordName = that.others.othersName
            } else {
              that.others.state = true
              that.others.othersPhone = ''
              that.others.recordPhone = that.others.othersPhone
            }
            break
          case 'cancel':
            that.others.showState = false
            if (Toucth.checkPhone(that.others.othersPhone) && that.others.othersPhone === that.$store.state.mc.phoneNo) {
              that.others.state = true
              that.others.othersPhone = ''
            }
            if (Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone) {
              that.others.state = false
              that.others.othersPhone = that.others.recordPhone
            }
            if (!Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone && that.others.recordPhone !== that.$store.state.mc.phoneNo) {
              that.others.state = false
              that.others.othersPhone = ''
            }
            if (!Toucth.checkPhone(that.others.othersPhone) && that.others.recordPhone !== that.others.othersPhone && that.others.recordPhone === that.$store.state.mc.phoneNo) {
              that.others.state = true
              that.others.othersPhone = ''
            }
            break
          case 'show':
            that.others.showState = true
            that.others.othersPhone = that.others.recordPhone
            that.others.othersName = that.others.recordName
            setTimeout(() => {
              if (Toucth.checkPhone(that.others.othersPhone) || this.others.othersPhone === '') {
                that.$refs.othersPhone.affStateFn(true)
              } else {
                that.$refs.othersPhone.affStateFn(false)
              }
            }, 0)
            break
        }
      },
      othersPhoneCheck: function () { // 检查电话
        if (Toucth.checkPhone(this.others.othersPhone) || this.others.othersPhone === '') {
          this.$refs.othersPhone.affStateFn(true)
        } else {
          this.$refs.othersPhone.affStateFn(false)
        }
      },
      DatePickerFn: function (time) { // 日期
        if (time) {
          this.startTime.val = time.val
          this.startTime.name = time.name
          this.getV5ZcBudgetCostFn()
        }
        this.datePickerState = false
      },
      selectTimeFn: function () { // 选择时间
        this.datePickerState = true
      },
      unfilledOrderAcFn: function (val) { // 未完成订单
        switch (val.status) {
          case 0:
            this.$router.push({path: '/zhuanche/await', query: {orderId: val.id}})
            break
          case 1:
            this.$router.push({path: '/zhuanche/await', query: {orderId: val.id}})
            break
          case 2:
            this.$router.push({path: '/zhuanche/service', query: {orderId: val.id}})
            break
          case 3:
            this.$router.push({path: '/zhuanche/service', query: {orderId: val.id}})
            break
          case 4:
            if (val.treatment) {
              this.$router.push({path: '/zhuanche/settlement', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/zhuanche/service', query: {orderId: val.id}})
            }
            break
          case 5:
            if (val.review) {
//              this.$router.push({path: '/zhuanche', query: {orderId: val.id}})
            } else {
              this.$router.push({path: '/zhuanche/affirm', query: {orderId: val.id}})
            }
            break
          case 6:
            break
        }
      },
      unfilledOrdercCloseFn: function () {
        this.$store.dispatch('unfilledOrderState', {type: 'zhuanche', state: false})
      },
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      },
      queryWaiterFn: function () { // v4查询附近司机
        this.$store.dispatch('ZcQueryWaiter', {
          lng: this.positioning.lng,
          lat: this.positioning.lat,
          carTypes: this.carIdAcFn.join(',')
        })
      },
      findNearDrivers (cid) { // v5查询附近司机
        let that = this
        if (that.positioning && that.positioning.lng && that.positioning.lat) {
          this.$store.dispatch('findNearDrivers', {
            business: 'zhuanche',
            cid: cid,
            distance: 5000,
            lng: that.positioning.lng,
            lat: that.positioning.lat,
            fn (data) {
              if (data.code === 1) {
                let emploies = data.emploies
                let freeEmploies = []
                if (emploies && emploies.length > 0) {
                  emploies.forEach(function (row) {
                    if (Number(row.status) === 10) {
                      freeEmploies.push(row)
                    }
                  })
                }
                that.nearDrivers.drivers = freeEmploies
                that.nearDrivers.driversCount = freeEmploies && freeEmploies.length > 0 ? freeEmploies.length : 0
              }
              // nearDrivers
            }
          })
        }
      }
    },
    mounted: function () {
    },
    created: function () {
      let that = this
      that.startTime = {
        val: new Date().getTime(),
        name: '选择预约时间'
      }
      that.$store.dispatch('positioningActions', {
        fn (state) {
          if (state) {
            that.startAdd = {
              startLng: that.positioning.lng,
              startLat: that.positioning.lat,
              startAddress: that.positioning.name,
              startAddressDetail: that.positioning.detailedAddress
            }
            that.getBussinessType()
          }
        }
      })
      that.$store.dispatch('getAddressRecord', {})
      that.$store.dispatch('checkLogin', (data) => {
        if (data && that.loginData && that.loginData.passenger) {
          // that.$store.dispatch('ZcCoupon', {passengerId: that.loginData.passenger.id})
          that.$store.dispatch('unfilledOrder', {
            passengerId: that.loginData.passenger.id,
            page: 1,
            limit: 20
          })
          // that.queryWaiterFn()
          that.unfilledOrderShowState = true
        }
      })
    }
  }
</script>

