              color: #999999;
            }
            .row {
              width: 100%;
              box-sizing: border-box;
              border-bottom: 1px solid #f2f2f2;
              line-height: 3.5rem;
              padding: 0 1rem;
              transition: 0.3s all;
              i {
                float: left;
                margin-right: 0.5rem;
              }
              .icon-yuan {
                font-size: 1rem;
              }
              .val {
                min-width: 90%;
                float: left;
                width: auto;
              }
            }
            .row1, .row5, .row6 {
              margin: 0 1rem;
              width: calc(~'100% - 2rem');
              i {
                color: #3cafb9;
              }
            }
            .row2 {
              padding-left: 2rem;
              i {
                color: #f06553;
              }
            }
            .row3 {
              text-align: center;
              .top {
                width: 100%;
                padding: 0 1rem;
                height: 3.666rem;
                overflow: hidden;
                line-height: 3.666rem;
                .list {
                  box-sizing: border-box;
                  width: 49%;
                  i {
                    font-size: 0.833rem;
                    color: #bbbbbb;
                  }
                  p {
                    float: left;
                    font-size: 1rem;
                    color: #bbbbbb;
                  }
                  .listVal {
                    float: left;
                    width: 80%;
                  }
                }
                .listAc {
                  i, p {
                    color: #333;
                  }
                }
                .bottom {
                  display: inline-block;
                  width: 75%;
                  input {
                    box-sizing: border-box;
                    display: inline-block;
                    line-height: 2rem;
                    width: 100%;
                    padding: 0 0.5rem;
                    background-color: #f3f4f5;
                    border: none;
                    border-radius: 0.333rem;
                  }
                }
              }

            }
            .row4 {
              text-align: center;
              padding: 0 1rem;
              /*color: #999999;*/
              .typeCar {
                text-align: center;
                font-size: 1rem;
                .list {
                  display: inline-block;
                  margin: 0 0.5rem;
                  color: #666;
                  b {
                    color: #f06553;
                    margin: 0 0.3rem;
                  }
                }
              }
            }
            .row5 {
              i {
                font-size: 1.1rem;
                color: #bbbbbb;
              }
              input {
                border: none;
                font-size: 1.1666rem;
              }
            }
            .row6 {
              i {
                font-size: 1rem;
                color: #bbbbbb;
              }
              input {
                border: none;
                font-size: 1.1666rem;
              }
            }
          }
        }
        .totalNum {
          box-sizing: border-box;
          width: 100%;
          background-color: #fff;
          padding: 0 1rem;
          margin-top: 1rem;
          text-align: center;
          position: relative;
          .row {
            width: 50%;
            float: left;
            line-height: 3rem;
            font-size: 0.8333rem;
            .red {
              color: #f06550;
              font-size: 1.5rem;
              margin: 0 0.3rem;
            }
          }
          .line {
            width: 1px;
            height: 50%;
            position: absolute;
            background-color: #eee;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
          }
          i {
            float: none;
            color: #999;
            font-size: 0.8333rem;
          }
        }
      }
      .submitOrder {
        width: 100%;
        box-sizing: border-box;
        margin-top: 1rem;
        span {
          width: 100%;
          color: #fff;
          font-size: 1.25rem;
          background-color: #f06553;
          text-align: center;
          border-radius: 0.333rem;
          height: 3.75rem;
          line-height: 3.75rem;
        }
      }
      .inputBoxContent {
        input::placeholder {
          font-size: 1rem;
        }
      }
    }
    .costBox {
      ul {
        li {
          height: 2rem;
          line-height: 2rem;
          color: #999999;
          .type {
            text-align: center;
            width: 100%;
            height: inherit;
            position: relative;
            .name {
              height: 1.8rem;
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
              padding: 0 0.5rem;
              display: inline-block;
              border-radius: 1rem;
              background: #f06553;
              color: #fff;
              font-size: 1rem;
            }
          }
          .name {
            float: left;
          }
          .time, .val {
            float: right;
          }
          .time {
            margin-right: 0.8rem;
            color: #666;
          }
          .sum {
            color: #f06553;
          }
          .unit {
            margin-left: 0.3rem;
          }
        }
        .carName {
          margin-bottom: 0.8rem;
          margin-top: 0.8rem;
        }
      }
      .BoxContent {
        max-height: 20rem;
        overflow: scroll !important;
      }
      .introduce {
        border-top: 1px solid #eee;
        margin-top: 0.5rem;
        padding-top: 1.2rem;
        line-height: 1.8rem;
        color: #666;
      }
    }
  }
</style>



