/**订单记录Nav**/
<template>
  <div class="orderTabs">
    <div class="orderTabsContent">
      <ul :style="{width:widthUl}" class="clear">
        {{widthLiFn}}
        <li :style="{width:widthLi}" v-for="(item, index) in data.length>0?data:null" :key="index"
            :class="ActionNumFn===item.url?'Action':''">
          <router-link :to="item.url">{{item.name}}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'xk-orderTabs',
    props: ['data'],
    computed: {
      ...mapGetters({
        orderNav: 'orderNav'
      }),
      ActionNumFn: function () {
        return this.$route.path
      },
      widthLiFn: function () {
        let that = this
        if (this.orderNav && this.orderNav.length < 4) {
          that.widthUl = '100%'
          that.widthLi = (100 / this.orderNav.length) + '%'
        } else {
          that.widthUl = (6 * this.orderNav.length) + 'rem'
          that.widthLi = '6rem'
        }
      }
    },
    data () {
      return {
        widthUl: '100%',
        widthLi: '25%',
        actionNum: 0
      }
    },
    methods: {},
    mounted: function () {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .orderTabs {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    line-height: 3rem;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, .1);
    z-index: 150;
    .orderTabsContent {
      width: 100%;
      overflow: auto;
    }
    ul {
      li {
        float: left;
        text-align: center;
        a {
          color: #333;
        }
      }
      .Action {
        a {
          color: #f06553;
        }
      }
    }
  }
</style>



