    function showPosition (position) { // 成功处理
      // alert('浏览器定位成功：' + JSON.stringify(position))
      console.log('浏览器定位成功处理', position)
      let pos = coordtransform.wgs84togcj02(position.coords.longitude, position.coords.latitude)
      url = '/v3/geocode/regeo?'
      params = {
        key: window.configure.amapKey,
        location: pos[0].toFixed(6) + ',' + pos[1].toFixed(6),
        extensions: 'all',
        output: 'JSON'
      }
      Vue.http.get(url, {params: params}).then((data) => {
        let Data = data.body.regeocode
        // lng，lat
        let lng = null
        let lat = null
        let location = null
        if (Data.addressComponent && Data.addressComponent.streetNumber && Data.addressComponent.streetNumber.location && JSON.stringify(Data.addressComponent.streetNumber.location) !== '' && JSON.stringify(Data.addressComponent.streetNumber.location) !== '') {
          lng = Data.addressComponent.streetNumber.location.split(',')[0]
          lat = Data.addressComponent.streetNumber.location.split(',')[1]
        } else {
          if (Data.pois && Data.pois.length > 0) {
            location = Data.pois[0]
            // for (let i = 0; i <= Data.pois.length; i++) {
            //   if (parseFloat(location.distance) > parseFloat(Data.pois[i].distance)) {
            //     location = Data.pois[i]
            //   }
            // }
            lng = location.location.split(',')[0]
            lat = location.location.split(',')[1]
          } else {
            lng = pos[0].toFixed(6)
            lat = pos[1].toFixed(6)
          }
        }
        // city
        let city = null
        if (Data.addressComponent.city && JSON.stringify(Data.addressComponent.city) !== '[]' && JSON.stringify(Data.addressComponent.city) !== '') {
          city = Data.addressComponent.city
        } else if (Data.formatted_address && Data.formatted_address !== '' && Data.formatted_address.indexOf('市') > 0) {
          city = Data.formatted_address.substring(Data.formatted_address.indexOf('省') + 1, Data.formatted_address.indexOf('市') + 1)
        } else if (Data.addressComponent.district) { // 地级市
          city = Data.addressComponent.district
        }
        point.lng = lng
        point.lat = lat
        point.city = city
        point.name = Data.formatted_address
        point.ad_code = Data.addressComponent.adcode ? Data.addressComponent.adcode : null
        point.city_code = Data.addressComponent.citycode ? Data.addressComponent.citycode : null
        point.detailedAddress = Data.formatted_address ? Data.formatted_address : ''
        console.log('initdata.js-getlocation-position', point)
        callback(point)
      })
    }

