/**等待中**/
<template>
  <div class="zhuancheAwait">
    <div class="AwaitContent">
      <div class="page page1">
        <div class="pageCarType">
          <!--<span v-if="ZcQueryOrderInfo&& ZcQueryOrderInfo.ifTaxi==='1'">出租车</span>-->
          <!--<span v-else="">{{ZcQueryOrderInfo ? ZcQueryOrderInfo.carTypeName : ''}}</span>-->
          <span>{{orderData.vehicle_type}}</span>
        </div>
        <div class="upload">
          <div class="img">
            <img src="../../../assets/img/zhuanche/await_img.png"
                 :style="{transform: 'rotate('+rotateVal+'deg)'}"
                 alt="专车">
          </div>
          <span class="text">等待中</span>
        </div>
        <h2>
          请稍等正在为您联络司机
        </h2>
        <span class="cancelBut" @click="cancelOrderFn('show')">取消订单</span>
      </div>
      <xk-Add :time="orderData?orderData.book_time:''"
              :origin="orderData?orderData.book_address:''"
              :endPoint="orderData?orderData.destination:''"></xk-Add>
    </div>
    <xk-input-box v-if="reson.state" title="取消订单理由" @click="cancelOrderFn" :closeState="true">
      <div slot="content" class="ZcCancelOrder">
        <textarea v-model="reson.val" placeholder="请输入取消订单理由">

        </textarea>
      </div>
    </xk-input-box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import Toucth from '../../../assets/js/method'
  import XkAdd from '../../module/add/src/add.vue'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import {mapGetters} from 'vuex'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod,
      XkInputBox,
      XkAdd
    },
    name: 'zhuancheAwait',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        ZcQueryOrderInfo: 'ZcQueryOrderInfo',
        v5FindZcOrderById: 'v5FindZcOrderById'
      })
    },
    data () {
      return {
        rotateVal: 0, // 旋转值
        reson: {
          state: false,
          val: ''
        },
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        },
        orderData: '' // 订单信息
      }
    },
    watch: {
      v5FindZcOrderById (val) {
        let that = this
        if (val.status === 10 || val.status === 15 || val.status === 20 || val.status === 25 || val.status === 28) {
          that.$router.push({path: '/zhuanche/service', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
        if (val.status === 30) { // 到达目的地
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/settlement', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          }, 1500)
        }
        if (val.status === 35) { // 已结算
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已结算！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/affirm'})
            return false
          }, 1500)
        }
        if (val.status === 40) { // 已结算
          that.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已评价！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche/checkOrder'})
            return false
          }, 1500)
        }
        if (val.status === 40) { // 已销单
          that.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被取消！'
          }
          setTimeout(() => {
            that.hintBox = false
            that.$router.push({path: '/zhuanche'})
            return false
          }, 1500)
        }
        setTimeout(function () {
          that.getOrderInforByid()
        }, 1000)
      }
    },
    methods: {
      getOrderInforByid () {
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('findZcOrderById', {
            order_id: that.$route.query.orderId,
            fn (data) {
              if (data.code === 1) {
                resolve(data)
              } else {
                reject(data)
              }
            }
          })
        })
      },
      cancelOrderFn: function (state) {
        let that = this
        if (state === 'show') {
          this.reson.state = true
          return true
        }
        if (state === 'affirm') {
          if (this.reson.val !== '') {
            that.loadState = true
            this.$store.dispatch('v5ZcCancelOrder', {
              order_id: Toucth.AcquireUrlParameter().orderId,
              remark: this.reson.val,
              fn: (state) => {
                if (state) {
                  that.loadState = false
                  this.reson.state = false
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单成功！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanche'})
                  }, 1500)
                } else {
                  this.reson.state = true
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单失败，请稍后重试！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1500)
                }
              }
            })
          } else {
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请输入销单理由！'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        } else {
          this.reson.state = false
        }
      }
    },
    mounted: function () {
      let that = this

      function rotateFn () { // 动画
        setInterval(function () {
          that.rotateVal = that.rotateVal + 8
        }, 39)
      }

      rotateFn()
    },
    created: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.getOrderInforByid().then(function (res) {
            res.order.book_time = Toucth.TimestampDate(res.order.book_time)
            that.orderData = res.order
            // console.log('that.orderData', that.orderData)
          }).catch(function (err) {
            console.log('获取订单信息失败：' + JSON.stringify(err))
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .zhuancheAwait {
    .AwaitContent {
      height: 100vh;
      width: 100vw;
      box-sizing: border-box;
      overflow: hidden;
      overflow-y: auto;
      background-color: #f3f4f5;
      .page {
        margin-bottom: 1rem;
        background-color: #fff;
        box-shadow: 0 0 5px rgba(0, 0, 0, .1);
        text-align: center;
        padding: 2rem 0;
        .pageCarType {
          text-align: center;
          margin-top: 2.125rem;
          span {
            padding: 0 1rem;
            height: 1.8rem;
            line-height: 1.9rem;
            color: #fff;
            background-color: #f06553;
            font-size: 1rem;
            border-radius: 0.9rem;
          }
        }
        .upload {
          width: 100%;
          height: 7rem;
          text-align: center;
          margin-top: 3.125rem;
          position: relative;
          img {
            height: 7rem;
            transform-origin: 50% 50%;
            transform: rotate(0);
          }
          .text {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: #999999;
            font-size: 1rem
          }
        }

        h2 {
          font-size: 1.25rem;
          color: #333;
          margin-top: 2.7rem;
          text-align: center;
        }
        .cancelBut {
          color: #f06553;
          font-size: 1rem;
          margin-top: 1rem;
        }
      }
      .page1 {
        text-align: center;
        padding: 1rem 2rem;
        padding-bottom: 2rem;
      }
    }
    .ZcCancelOrder {
      border: 1px solid #eee;
      height: 10rem;
      textarea {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        border: none;
        resize: none;
        font-size: 1rem;
        padding: 0.5rem;
      }
    }
  }
</style>



