/** 用车**/
<template>
  <div class="recordYongChe">
    <div class="yongcheContent">
      <ul class="contentList" v-if="zcOrderRecord&&zcOrderRecord.length>0">
        <xk-record-order-mod
          v-for="(time,index) in zcOrderRecord"
          :startAddress="time.book_address"
          :endAddress="time.destination"
          :serverTime="time.book_time"
          :status="time.status"
          :orderId="time.id"
          :key="index"
          @orderClick="anOrderClick"
        >
        </xk-record-order-mod>
      </ul>
      <div class="NullOrder" v-else="">
        <div class="img">
          <img src="../../assets/img/yonghu/orderImg.png"/>
          <span class="imgBut"><router-link to="/zhuanche">去下单</router-link></span>
        </div>
      </div>
      <div class="loadDataBut" v-if="ZcQueryAllOrder&&ZcQueryAllOrder.val.length<(ZcQueryAllOrder.total-1)">
        <span @click="loadDataButFn">点击加载更多</span>
      </div>
    </div>
    <!--<xk-upload-mod :state="loadState"></xk-upload-mod>-->
  </div>
</template>

<script>
  import XkRecordOrderMod from '../module/recordOrderMod/src/recordOrderMod_2'
  import {mapGetters} from 'vuex'
  // import Toucth from '../../assets/js/method'
  import XkUploadMod from '../module/uploadMod/src/uploadMod'
  import Vue from 'vue'

  export default {
    components: {
      XkUploadMod,
      XkRecordOrderMod
    },
    name: 'recordDaiJia',
    computed: {
      ...mapGetters({
        ZcQueryAllOrder: 'ZcQueryAllOrder',
        orderNav: 'orderNav'
      })
      // allOrderFn () {
      //   // console.log(this.ZcQueryAllOrder)
      //   let orderData = this.ZcQueryAllOrder.val
      //   let orderList = []
      //   for (let i = 0; i < orderData.length; i++) {
      //     let obj = {
      //       startAddress: orderData[i].startAddress,
      //       endAddress: orderData[i].endAddress,
      //       serverTime: Toucth.TimestampDate(orderData[i].serverTime),
      //       status: ''
      //     }
      //     switch (orderData[i].status) {
      //       case 0:
      //         obj.status = {
      //           name: '新单',
      //           router: {path: '/zhuanche/await', query: {orderId: orderData[i].id}}
      //         }
      //         break
      //       case 1:
      //         obj.status = {
      //           name: '等待中',
      //           router: {path: '/zhuanche/await', query: {orderId: orderData[i].id}}
      //         }
      //         break
      //       case 2:
      //         obj.status = {
      //           name: '已接单',
      //           router: {path: '/zhuanche/service', query: {orderId: orderData[i].id}}
      //         }
      //         break
      //       case 3:
      //         obj.status = {
      //           name: '到达预约地',
      //           router: {path: '/zhuanche/service', query: {orderId: orderData[i].id}}
      //         }
      //         break
      //       case 4:
      //         if (orderData[i].treatment) {
      //           obj.status = {
      //             name: '待结算',
      //             router: {path: '/zhuanche/settlement', query: {orderId: orderData[i].id}}
      //           }
      //           this.$router.push({path: '/zhuanche/settlement', query: {orderId: orderData[i].id}})
      //         } else {
      //           obj.status = {
      //             name: '前往目的地',
      //             router: {path: '/zhuanche/service', query: {orderId: orderData[i].id}}
      //           }
      //         }
      //         break
      //       case 5:
      //         if (orderData[i].review) {
      //           obj.status = {
      //             name: '已完成',
      //             router: {path: '/zhuanche/checkOrder', query: {orderId: orderData[i].id}}
      //           }
      //         } else {
      //           obj.status = {
      //             name: '待评价',
      //             router: {path: '/zhuanche/affirm', query: {orderId: orderData[i].id}}
      //           }
      //         }
      //         break
      //       case 6:
      //         obj.status = {
      //           name: '已销单',
      //           router: {path: '/zhuanche/checkOrder', query: {orderId: orderData[i].id}}
      //         }
      //         break
      //     }
      //     orderList.push(obj)
      //   }
      //   // console.log(orderList)
      //   return orderList
      // }
    },
    data () {
      return {
        page: 1,
        limit: 10,
        rows: 10,
        loadState: false,
        zcOrderRecord: '',
        clickedOrder: ''
      }
    },
    watch: {},
    methods: {
      anOrderClick (orderId) { // 点击单个订单
        console.log('订单id', orderId)
        let that = this
        let order = that.getOrderInforByid(orderId)
        order.then(function (res) {
          that.clickedOrder = res
          that.recordOrderAcFn(that.clickedOrder.status, that.clickedOrder.id)
        })
      },
      getOrderInforByid (orderId) { // 获取单个订单
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('findZcOrderById', {
            order_id: orderId,
            fn (data) {
              if (data.code === 1) {
                resolve(data.order)
              } else {
                reject(data.msg)
              }
            }
          })
        })
      },
      recordOrderAcFn: function (status, orderId) { // 不同的订单状态去到不同的页面
        console.log(status)
        console.log(orderId)
        switch (status) {
          case 1: // 新单
            this.$router.push({path: '/zhuanche/await', query: {orderId: orderId}})
            break
          case 5 :
          case 10 :
          case 15 :
          case 20 :
          case 25 :
          case 28: // 已派单
            this.$router.push({path: '/zhuanche/service', query: {orderId: orderId}})
            break
          case 30: // 到达目的地
            this.$router.push({path: '/zhuanche/settlement', query: {orderId: orderId}})
            break
          case 35: // 已结算
            this.$router.push({path: '/zhuanche/affirm', query: {orderId: orderId}})
            // this.$router.push({path: '/zhuanche/service', query: {orderId: orderId}})
            break
          case 40: // 已评价
            this.$router.push({path: '/zhuanche/checkOrder', query: {orderId: orderId}})
            // this.$router.push({path: '/zhuanche/service', query: {orderId: orderId}})
            break
          case 45: // 已销单
            // this.$router.push({path: '/zhuanche/checkOrder', query: {orderId: orderId}})
            // this.$router.push({path: '/zhuanche/affirm', query: {orderId: orderId}})
            break
        }
      },
      loadDataButFn: function () { //  获取更多订单
        let that = this
        that.page++
        that.loadState = true
        that.$store.dispatch('ZcQueryAllOrder', {
          'serviceType': 'zhuanche',
          'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
          'page': that.page,
          'rows': that.rows,
          fn: (state) => {
            if (state) {
              that.loadState = false
            }
          }
        })
      },
      v5GetZcOrderRecord () { // 获取订单记录
        let that = this
        return new Promise(function (resolve, reject) {
          that.$store.dispatch('v5GetZcOrderRecord', {
            business: 'zhuanche',
            page: that.page,
            limit: that.limit,
            fn (data) {
              if (data.code === 1) {
                resolve(data)
              } else {
                reject(data)
              }
            }
          })
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.loadState = true
          let ZcOrderRecord = that.v5GetZcOrderRecord()
          ZcOrderRecord.then(function (res) {
            that.loadState = false
            let arr = []
            if (res.finishOrders && res.finishOrders.length > 0) {
              res.finishOrders.forEach(function (item) {
                arr.push(item)
              })
            }
            if (res.runOrders && res.runOrders.length > 0) {
              res.runOrders.forEach(function (item) {
                arr.push(item)
              })
            }
            arr.forEach(function (item) {
              item.book_time = Vue.prototype.$utils.TimestampDate(item.book_time)
              item.status = Vue.prototype.$utils.v5ZcOrderStatus(item.status)
            })
            that.zcOrderRecord = arr
          })
          // that.$store.dispatch('ZcQueryAllOrder', {
          //   'serviceType': 'zhuanche',
          //   'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
          //   'page': that.page,
          //   'rows': that.rows,
          //   type: 'upData',
          //   fn: (data) => {
          //     if (data) {
          //       that.loadState = false
          //     }
          //   }
          // })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .recordYongChe {
    .yongcheContent {
      .loadDataBut {
        box-sizing: border-box;
        width: 100%;
        line-height: 3.5rem;
        padding: 0 1rem;
        span {
          width: 100%;
          background-color: #fff;
          border-radius: 0.333rem;
          text-align: center;
        }
      }
    }
  }
</style>



