/**跑腿**/
<template>
  <div class="recordPaoTui">
    <div class="paotuiContent">
      <ul class="contentList" v-if="PtQueryAllOrder.val.length>0">
        <xk-record-order-mod
          v-for="(time,index) in allOrderFn"
          :startAddress="time.startAddress"
          :endAddress="time.endAddress"
          :serverTime="time.serverTime"
          :status="time.status"
          :orderId="time.orderId"
          :key="index"
        >
        </xk-record-order-mod>
      </ul>
      <div class="NullOrder" v-else="">
        <div class="img">
          <img src="../../assets/img/yonghu/orderImg.png"/>
          <span class="imgBut"><router-link to="/paotui">去下单</router-link></span>
        </div>
      </div>
      <div class="loadDataBut" v-if="PtQueryAllOrder&&PtQueryAllOrder.val.length<(PtQueryAllOrder.total-1)">
        <span @click="loadDataButFn">点击加载更多</span>
      </div>
    </div>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
  </div>
</template>

<script>
  import XkRecordOrderMod from '../module/recordOrderMod/src/recordOrderMod'
  import { mapGetters } from 'vuex'
  import Toucth from '../../assets/js/method'
  import XkUploadMod from '../module/uploadMod/src/uploadMod'
  export default {
    components: {
      XkUploadMod,
      XkRecordOrderMod
    },
    name: 'recordPaoTui',
    computed: {
      ...mapGetters({
        PtQueryAllOrder: 'PtQueryAllOrder'
      }),
      allOrderFn () {
        let unfilledOrder = []
        let paotuiData = this.PtQueryAllOrder ? this.PtQueryAllOrder.val : null
        if (paotuiData && paotuiData.length > 0) {
          let obj = {}
          for (let i = 0; i < paotuiData.length; i++) {
            obj = {
              startAddress: paotuiData[i].startAddress,
              endAddress: paotuiData[i].endAddress,
              serverTime: Toucth.TimestampDate(paotuiData[i].serverTime),
              orderId: paotuiData[i].orderId,
              status: ''
            }
            switch (paotuiData[i].status) {
              case 0:
                obj.status = {
                  name: '新单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].orderId}}
                }
                break
              case 1:
                obj.status = {
                  name: '已派单',
                  router: {path: '/paotui/await', query: {orderId: paotuiData[i].orderId}}
                }
                break
              case 2:
                obj.status = {
                  name: '已接单',
                  router: {path: '/paotui/server', query: {orderId: paotuiData[i].orderId}}
                }
                break
              case 3:
                obj.status = {
                  name: '执行中',
                  router: {path: '/paotui/server', query: {orderId: paotuiData[i].orderId}}
                }
                break
              case 4:
                if (paotuiData[i].treatment) {
                  if (paotuiData[i].review) {
                    obj.status = {
                      name: '已完成',
                      router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].orderId}}
                    }
                  } else {
                    obj.status = {
                      name: '待评价',
                      router: {path: '/paotui/affirm', query: {orderId: paotuiData[i].orderId}}
                    }
                  }
                } else {
                  obj.status = {
                    name: '待结算',
                    router: {path: '/paotui/settlement', query: {orderId: paotuiData[i].orderId}}
                  }
                }
                break
              case 5:
                obj.status = {
                  name: '已销单',
                  router: {path: '/paotui/checkOrder', query: {orderId: paotuiData[i].orderId}}
                }
                break
            }
            unfilledOrder.push(obj)
          }
        }
        return unfilledOrder
      }
    },
    data () {
      return {
        loadState: false,
        page: 1,
        rows: 10
      }
    },
    methods: {
      loadDataButFn: function () {
        let that = this
        that.loadState = true
        that.$store.dispatch('PtQueryAllOrder', {
          'serviceType': 'paotui',
          'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
          'page': that.page,
          'rows': that.rows,
          fn: (state) => {
            if (state) {
              that.loadState = false
            }
          }
        })
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.loadState = true
          that.$store.dispatch('PtQueryAllOrder', {
            'serviceType': 'paotui',
            'passengerId': that.$store.state.mc.loginData ? that.$store.state.mc.loginData.passenger.id : '',
            'page': that.page,
            'rows': that.rows,
            'type': 'upData',
            fn: (state) => {
              if (state) {
                that.loadState = false
              }
            }
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .recordPaoTui {
    .loadDataBut {
      box-sizing: border-box;
      width: 100%;
      line-height: 3.5rem;
      padding: 0 1rem;
      span {
        width: 100%;
        background-color: #fff;
        border-radius: 0.333rem;
        text-align: center;
      }
    }
    .NullOrder {
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      .img {
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 50%;
        img {
          width: 100%;
        }
      }
    }
  }
</style>



