/**等待中**/
<template>
  <div class="zhuanxianAwait">
    <div class="AwaitContent">
      <div class="page page1">
        <div class="pageCarType">
          <span>{{ZxFindOrder ? ZxFindOrder.lineName.replace('-', ' | ') : ''}}</span>
        </div>
        <div class="upload">
          <div class="img">
            <img src="../../../assets/img/zhuanche/await_img.png" :style="{transform: 'rotate('+rotateVal+'deg)'}"
                 alt="专车">
          </div>
          <span class="text">等待中</span>
        </div>
        <h2>
          请稍等正在为您联络司机
        </h2>
        <span class="cancelBut" @click="cancelOrderFn('show')">取消订单</span>
      </div>
      <xk-Add :time="ZxFindOrder?serverTimeFn:''"
              :origin="ZxFindOrder?ZxFindOrder.startAddress:''"
              :endPoint="ZxFindOrder?ZxFindOrder.endAddress:''"
      ></xk-Add>
    </div>
    <xk-input-box v-if="reson.state" title="取消订单理由" @click="cancelOrderFn" :closeState="true">
      <div slot="content" class="ZcCancelOrder">
        <textarea v-model="reson.val" placeholder="请输入取消订单理由">
        </textarea>
      </div>
    </xk-input-box>
    <xk-upload-mod :state="loadState"></xk-upload-mod>
    <xk-hint-box v-if="hintBox.stateShow" :state="hintBox.state" :content="hintBox.content"></xk-hint-box>
  </div>
</template>

<script>
  import Toucth from '../../../assets/js/method'
  import XkAdd from '../../module/add/src/add.vue'
  import XkInputBox from '../../module/inputBox/src/inputBox'
  import { mapGetters } from 'vuex'
  import XkUploadMod from '../../module/uploadMod/src/uploadMod'
  import XkHintBox from '../../module/hintBox/src/hintBox'

  export default {
    components: {
      XkHintBox,
      XkUploadMod,
      XkInputBox,
      XkAdd
    },
    name: 'zhuancheAwait',
    computed: {
      ...mapGetters({
        loginData: 'loginData',
        ZxFindOrder: 'ZxFindOrder'
      }),
      serverTimeFn: function () {
        return Toucth.TimestampDate(this.ZxFindOrder.serverTime)
      }
    },
    data () {
      return {
        rotateVal: 0, // 旋转值
        reson: {
          state: false,
          val: ''
        },
        loadState: false,
        hintBox: {
          stateShow: false,
          state: 'yes',
          content: ''
        }
      }
    },
    watch: {
      ZxFindOrder (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/zhuanxian')
          return
        }
        if (val.status === 0 || val.status === 1) {
          setTimeout(function () {
            if (that.$route.fullPath.split('?')[0] === '/zhuanxian/await') {
              that.$store.dispatch('ZxFindOrder', {orderId: Toucth.AcquireUrlParameter().orderId})
            }
          }, 3000)
        }
        if (val.status === 2 || val.status === 3 || val.status === 4) {
          that.$router.push({path: '/zhuanxian/service', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
        }
        if (val.status === 5) {
          this.hintBox = {
            stateShow: true,
            state: 'yes',
            content: '您的订单已完成！'
          }
          setTimeout(() => {
            this.hintBox = false
            that.$router.push({path: '/zhuanxian/affirm', query: {orderId: Toucth.AcquireUrlParameter().orderId}})
            return false
          }, 1500)
        }
        if (val.status === 6) {
          this.hintBox = {
            stateShow: true,
            state: 'no',
            content: '您的订单已被销单！'
          }
          setTimeout(() => {
            this.hintBox = false
            that.$router.push('/zhuanxian')
            return false
          }, 1500)
        }
      }
    },
    methods: {
      cancelOrderFn: function (state) {
        let that = this
        if (state === 'show') {
          this.reson.state = true
          return true
        }
        if (state === 'affirm') {
          if (this.reson.val !== '') {
            that.loadState = true
            this.$store.dispatch('ZxCancelOrder', {
              orderId: Toucth.AcquireUrlParameter().orderId,
              reson: this.reson.val,
              fn: (state) => {
                if (state) {
                  that.loadState = false
                  this.reson.state = false
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单成功！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                    that.$router.push({path: '/zhuanxian'})
                    return false
                  }, 1500)
                } else {
                  this.reson.state = true
                  that.hintBox = {
                    stateShow: true,
                    state: 'yes',
                    content: '销单失败，请稍后重试！'
                  }
                  setTimeout(() => {
                    that.hintBox.stateShow = false
                  }, 1500)
                }
              }
            })
          } else {
            that.hintBox = {
              stateShow: true,
              state: 'no',
              content: '请输入销单理由！'
            }
            setTimeout(() => {
              that.hintBox.stateShow = false
            }, 1500)
          }
        } else {
          this.reson.state = false
        }
      }
    },
    mounted: function () {
      let that = this

      function rotateFn () { // 动画
        setTimeout(function () {
          that.rotateVal = that.rotateVal + 8
          rotateFn()
        }, 39)
      }

      rotateFn()
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('ZxFindOrder', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .zhuanxianAwait {
    .AwaitContent {
      height: 100vh;
      width: 100vw;
      box-sizing: border-box;
      overflow: hidden;
      overflow-y: auto;
      background-color: #f3f4f5;
      .page {
        margin-bottom: 1rem;
        background-color: #fff;
        box-shadow: 0 0 5px rgba(0, 0, 0, .1);
        text-align: center;
        padding: 2rem 0;
        .pageCarType {
          text-align: center;
          margin-top: 2.125rem;
          span {
            padding: 0 1rem;
            height: 1.8rem;
            line-height: 1.9rem;
            color: #fff;
            background-color: #f06553;
            font-size: 1rem;
            border-radius: 0.9rem;
          }
        }
        .upload {
          width: 100%;
          height: 7rem;
          text-align: center;
          margin-top: 3.125rem;
          position: relative;
          img {
            height: 7rem;
            transform-origin: 50% 50%;
            transform: rotate(0);
          }
          .text {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: #999999;
            font-size: 1rem
          }

        }

        h2 {
          font-size: 1.25rem;
          color: #333;
          margin-top: 2.7rem;
          text-align: center;
        }
        .cancelBut {
          color: #f06553;
          font-size: 1rem;
          margin-top: 1rem;
        }
      }
      .page1 {
        text-align: center;
        padding: 1rem 2rem;
        padding-bottom: 2rem;

      }
    }
    .ZcCancelOrder {
      border: 1px solid #eee;
      height: 10rem;
      textarea {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        border: none;
        resize: none;
        font-size: 1rem;
        padding: 0.5rem;
      }
    }
  }
</style>



