import Vue from 'vue'
import Router from 'vue-router'
import Project from '@/components/project/project' // 产品
import BannerContent from '@/components/project/bannerContent' // 产品
import DaiJia from '@/components/project/daijia/daijia' // 代驾
import DaiJiaAwait from '@/components/project/daijia/await' // 代驾 等待中
import DaiJiaService from '@/components/project/daijia/service' // 代驾 服务中
import DaiJiaSettlement from '@/components/project/daijia/settlement' // 代驾 结算
import DaiJiaAffirm from '@/components/project/daijia/affirm' // 代驾 评价
import DaiJiaCheckOrder from '@/components/project/daijia/checkOrder' // 代驾 订单查看
import PaoTui from '@/components/project/paotui/paotui' // 跑腿
import PaoDownOrder from '@/components/project/paotui/downOrder' // 跑腿 下单
import PaoAwait from '@/components/project/paotui/await' // 跑腿 等待中
import PaoServer from '@/components/project/paotui/server' // 跑腿 服务中
import PaoSettlement from '@/components/project/paotui/settlement' // 跑腿 结算订单
import PaoAffirm from '@/components/project/paotui/affirm' // 跑腿 结算订单
import PaoCheckOrder from '@/components/project/paotui/checkOrder' // 跑腿 订单查看
import HuoYun from '@/components/project/huoyun/huoyun' // 货运
import HuoYunOrder from '@/components/project/huoyun/orderInfo' // 货运下单
import HuoYunAwait from '@/components/project/huoyun/await' // 货运等待
import HuoYunService from '@/components/project/huoyun/service' // 货运 服务中
import HuoYunSettlement from '@/components/project/huoyun/settlement' // 货运 结算订单
import HuoYunAffirm from '@/components/project/huoyun/affirm' // 货运 评价
import HuoYunCheckOrder from '@/components/project/huoyun/checkOrder' // 货运 完成查看
import ZhuanChe from '@/components/project/zhuanche/zhuanche' // 专车
import ZhuanCheAwait from '@/components/project/zhuanche/await' // 专车 等待中
import ZhuanCheService from '@/components/project/zhuanche/service' // 专车 服务中
import ZhuanCheSettlement from '@/components/project/zhuanche/settlement' // 专车 结算订单
import ZhuanCheAffirm from '@/components/project/zhuanche/affirm' // 专车 确认订单 评价
import ZhuanCheCheckOrder from '@/components/project/zhuanche/checkOrder' // 专车 订单查看
import ZhuanXian from '@/components/project/zhuanxian/zhuanxian' // 城际专线
import ZhuanXianAffirm from '@/components/project/zhuanxian/affirm' // 城际评价
import ZhuanXianAwait from '@/components/project/zhuanxian/await' // 城际等待
import ZhuanXianService from '@/components/project/zhuanxian/service' // 城际服务中
import ZhuanXianSettlement from '@/components/project/zhuanxian/settlement' // 城际结算
import ZhuanXianCheckOrder from '@/components/project/zhuanxian/checkOrder' // 城际查看
import Inform from '@/components/inform/inform' // 通知
import qrCode from '@/components/qrCode/qrCode' // 分享
import friend from '@/components/friend/friend' // 立即
import Settings from '@/components/settings/settings' // 设置
import serviceAgreement from '@/components/settings/serviceAgreement' // 服务协议
import servicePrice from '@/components/settings/servicePrice' // 服务价格
import CompileNom from '@/components/compileNom/compileNom' // 修改个人信息
import MeWallet from '@/components/mewallet/mewallet' // 钱包
import Invoice from '@/components/mewallet/invoice/invoice' // 发票
import invoiceApply from '@/components/mewallet/invoice/invoiceApply' // 发票申请
import InvoiceRecord from '@/components/mewallet/invoice/invoiceRecord' // 发票申请
import invoiceOrderRecord from '@/components/mewallet/invoice/invoiceOrderRecord' // 发票选择订单
import CapitalRecord from '@/components/mewallet/capitalRecord/capitalRecord' // 资金流水
import Recharge from '@/components/recharge/recharge' // 充值
import Coupon from '@/components/mewallet/coupon/coupon' // 优惠券
import CouponStale from '@/components/mewallet/coupon/stale' // 优惠券 已过期
import CouponUnused from '@/components/mewallet/coupon/unused' // 优惠券 未使用
import CouponUsed from '@/components/mewallet/coupon/used' // 优惠券 已使用
import OrderRecord from '@/components/orderRecord/orderRecord' // 订单记录
import RecordDaiJia from '@/components/orderRecord/daijia' // 订单记录 代驾
import RecordYongChe from '@/components/orderRecord/yongche' // 订单记录 用车
import RecordPaoTui from '@/components/orderRecord/paotui' // 订单记录 跑腿
import RecordZhuanXian from '@/components/orderRecord/zhuanxian' // 订单记录 城际专线
import RecordHuoyun from '@/components/orderRecord/huoyun' // 订单记录 货运

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Project',
      component: Project,
      children: [
        {
          path: 'daijia',
          name: 'DaiJia',
          component: DaiJia
        },
        {
          path: 'paotui',
          name: 'PaoTui',
          component: PaoTui
        },
        {
          path: 'zhuanche',
          name: 'ZhuanChe',
          component: ZhuanChe
        },
        {
          path: 'zhuanxian',
          name: 'ZhuanXian',
          component: ZhuanXian
        },
        {
          path: 'huoyun',
          name: 'huoyun',
          component: HuoYun
        }
      ]
    },
    {
      path: '/inform',
      name: 'Inform',
      component: Inform
    },
    {
      path: '/qrCode',
      name: 'qrCode',
      component: qrCode
    },
    {
      path: '/friend',
      name: 'friend',
      component: friend
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings
    },
    {
      path: '/serviceAgreement',
      name: 'serviceAgreement',
      component: serviceAgreement
    },
    {
      path: '/servicePrice',
      name: 'servicePrice',
      component: servicePrice
    },
    {
      path: '/mewallet',
      name: 'MeWallet',
      component: MeWallet
    },
    {
      path: '/invoice',
      name: 'Invoice',
      component: Invoice
    },
    {
      path: '/invoiceRecord',
      name: 'InvoiceRecord',
      component: InvoiceRecord
    },
    {
      path: '/invoiceApply',
      name: 'invoiceApply',
      component: invoiceApply
    },
    {
      path: '/invoiceOrderRecord',
      name: 'invoiceOrderRecord',
      component: invoiceOrderRecord
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: Coupon,
      children: [
        {
          path: 'stale',
          name: 'CouponStale',
          component: CouponStale
        },
        {
          path: 'used',
          name: 'CouponUsed',
          component: CouponUsed
        },
        {
          path: 'nused',
          name: 'CouponUnused',
          component: CouponUnused
        }
      ]
    },
    {
      path: '/compileNom',
      name: 'CompileNom',
      component: CompileNom
    },
    {
      path: '/capitalRecord',
      name: 'CapitalRecord',
      component: CapitalRecord
    },
    {
      path: '/recharge',
      name: 'Recharge',
      component: Recharge
    },
    {
      path: '/daijia/await',
      name: 'DaiJiaAwait',
      component: DaiJiaAwait
    },
    {
      path: '/daijia/service',
      name: 'DaiJiaService',
      component: DaiJiaService
    },
    {
      path: '/daijia/settlement',
      name: 'DaiJiaSettlement',
      component: DaiJiaSettlement
    },
    {
      path: '/daijia/affirm',
      name: 'DaiJiaAffirm',
      component: DaiJiaAffirm
    },
    {
      path: '/daijia/checkOrder',
      name: 'DaiJiaCheckOrder',
      component: DaiJiaCheckOrder
    },
    {
      path: '/huoyun/orderInfo',
      name: 'HuoYunOrder',
      component: HuoYunOrder
    },
    {
      path: '/huoyun/await',
      name: 'HuoYunAwait',
      component: HuoYunAwait
    },
    {
      path: '/huoyun/service',
      name: 'HuoYunService',
      component: HuoYunService
    },
    {
      path: '/huoyun/settlement',
      name: 'HuoYunSettlement',
      component: HuoYunSettlement
    },
    {
      path: '/huoyun/affirm',
      name: 'HuoYunAffirm',
      component: HuoYunAffirm
    },
    {
      path: '/huoyun/checkOrder',
      name: 'HuoYunCheckOrder',
      component: HuoYunCheckOrder
    },
    {
      path: '/zhuanche/await',
      name: 'ZhuanCheAwait',
      component: ZhuanCheAwait
    },
    {
      path: '/zhuanche/affirm',
      name: 'ZhuanCheAffirm',
      component: ZhuanCheAffirm
    },
    {
      path: '/zhuanche/service',
      name: 'ZhuanCheService',
      component: ZhuanCheService
    },
    {
      path: '/zhuanche/settlement',
      name: 'ZhuanCheSettlement',
      component: ZhuanCheSettlement
    },
    {
      path: '/zhuanche/checkOrder',
      name: 'ZhuanCheCheckOrder',
      component: ZhuanCheCheckOrder
    },
    {
      path: '/orderRecord',
      name: 'OrderRecord',
      component: OrderRecord,
      children: [
        {
          path: '/orderRecord/daijia',
          name: 'RecordDaiJia',
          component: RecordDaiJia
        },
        {
          path: '/orderRecord/yongche',
          name: 'RecordYongChe',
          component: RecordYongChe
        },
        {
          path: '/orderRecord/paotui',
          name: 'RecordPaoTui',
          component: RecordPaoTui
        },
        {
          path: '/orderRecord/zhuanxian',
          name: 'RecordZhuanXian',
          component: RecordZhuanXian
        },
        {
          path: '/orderRecord/huoyun',
          name: 'RecordHuoyun',
          component: RecordHuoyun
        }
      ]
    },
    {
      path: '/paotui/downOrder',
      name: 'PaoDownOrder',
      component: PaoDownOrder
    },
    {
      path: '/paotui/await',
      name: 'PaoAwait',
      component: PaoAwait
    },
    {
      path: '/paotui/server',
      name: 'PaoServer',
      component: PaoServer
    },
    {
      path: '/paotui/settlement',
      name: 'PaoSettlement',
      component: PaoSettlement
    },
    {
      path: '/paotui/affirm',
      name: 'PaoAffirm',
      component: PaoAffirm
    },
    {
      path: '/paotui/checkOrder',
      name: 'PaoCheckOrder',
      component: PaoCheckOrder
    },
    {
      path: '/bannerContent',
      name: 'BannerContent',
      component: BannerContent
    },
    {
      path: '/zhuanxian/affirm',
      name: 'ZhuanXianAffirm',
      component: ZhuanXianAffirm
    },
    {
      path: '/zhuanxian/await',
      name: 'ZhuanXianAwait',
      component: ZhuanXianAwait
    },
    {
      path: '/zhuanxian/service',
      name: 'ZhuanXianService',
      component: ZhuanXianService
    },
    {
      path: '/zhuanxian/settlement',
      name: 'ZhuanXianSettlement',
      component: ZhuanXianSettlement
    },
    {
      path: '/zhuanxian/checkOrder',
      name: 'ZhuanXianCheckOrder',
      component: ZhuanXianCheckOrder
    },
    {path: '*', redirect: '/'}
  ]
})



