import paotui from '../../../api/paotui/paotui'
import initdata from '../../../api/initdata'

const state = {
  budgetCost: null,
  couponList: [],
  queryOrder: null,
  payData: null,
  AllOrder: {
    state: true,
    total: true,
    val: []
  },
  queryReview: null
}

const mutations = {
  PtBudgetCost (state, data) {
    state.budgetCost = data
  },
  PtCoupon (state, data) {
    state.couponList = []
    for (let i = 0; i < data.length; i++) {
      state.couponList.push({
        couponRule: {
          money: data[i].money,
          title: data[i].title,
          discount: data[i].discount,
          couponTypeName: data[i].couponTypeName,
          end: data[i].endTime
        },
        id: data[i].id
      })
    }
  },
  PtQueryOrder (state, data) {
    // console.log(data)
    state.queryOrder = data
  },
  PtPayData (state, data) {
    state.payData = data
  },
  PtQueryAllOrder (state, obj) {
    // console.log(data)
    let type = obj.type
    let data = obj.data
    if (data) {
      state.AllOrder.total = data.total
      state.AllOrder.state = false
      if (type === 'upData') {
        state.AllOrder.val = []
      }
      for (let i = 0; i < data.rows.length; i++) {
        state.AllOrder.val.push(data.rows[i])
      }
    }
  },
  PtQueryReview (state, data) {
    if (data && data.content !== '') {
      let tabs = data.content.substring(0, data.content.lastIndexOf('&*&'))
      tabs = tabs !== '' ? tabs.split('&|&') : []
      state.queryReview = {
        reviewContent: data.content.substring(data.content.lastIndexOf('&*&')).replace('&*&', ''),
        tabs: tabs.length > 0 ? tabs : null,
        score: data.score
      }
    }
  }
}

const getters = {
  PtBudgetCost: state => {
    return state.budgetCost
  },
  PtCoupon: state => {
    return state.couponList
  },
  PtQueryOrder: state => {
    return state.queryOrder
  },
  PtPayData: state => {
    return state.payData
  },
  PtQueryAllOrder: state => {
    return state.AllOrder
  },
  PtQueryReview: state => {
    return state.queryReview
  }
}

const actions = {
  PtBudgetCost ({commit}, obj) {
    paotui.budgetCost(obj, (data) => {
      commit('PtBudgetCost', data.data)
    })
  },
  PtCoupon ({commit}, obj) {
    paotui.coupon(obj, (data) => {
      // console.log(data)
      commit('PtCoupon', data.data)
    })
  },
  PtPlaceAnOrder ({commit}, obj) {
    paotui.placeAnOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  PtQueryOrder ({commit}, obj) {
    paotui.queryOrder(obj, (data) => {
      commit('PtQueryOrder', data.data)
    })
  },
  PtPayData ({commit}, obj) {
    paotui.payData(obj, (data) => {
      commit('PtPayData', data.data)
    })
  },
  PtToPay ({commit}, obj) {
    paotui.toPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  PtReview ({commit}, obj) {
    paotui.review(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  PtCancelOrder ({commit}, obj) {
    paotui.cancelOrder(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  },
  PtQueryAllOrder ({commit}, obj) {
    initdata.allOrder(obj, (data) => {
      if (data.code === 0 && obj.fn) {
        obj.fn(true)
      }
      commit('PtQueryAllOrder', {data: data.data, type: obj.type})
    })
  },
  PtQueryReview ({commit}, obj) {
    paotui.queryReview(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
      commit('PtQueryReview', data.data)
    })
  },
  PtWXPay ({commit}, obj) {
    paotui.weixinPay(obj, (data) => {
      if (data && obj.fn) {
        obj.fn(data)
      }
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



