/**查看订单**/
<template>
  <div class="checkOrder">
    <xk-waiter :src="'../../../../assets/img/yonghu/touxiang.jpg'"
               :name="HyOrderInfo?HyOrderInfo.employName:''"
               :headImg="HyOrderInfo?HyOrderInfo.employPhoto:''"
               :carNo="HyOrderInfo?HyOrderInfo.truckNumber:''"
               :carName="HyOrderInfo?HyOrderInfo.truckName:''"
               :carType="HyOrderInfo ? HyOrderInfo.truckTypeName :''"
    >
    </xk-waiter>
    <div class="money">
      <h2>
        <span class="max">{{HyPayInfo ? HyPayInfo.payMoney.toFixed(1).split('.')[0] : '0'}}</span>
        <span>.{{HyPayInfo && HyPayInfo.payMoney ? HyPayInfo.payMoney.toFixed(1).split('.')[1] : '0'}}元</span>
      </h2>
      <div class="detail">
        <span @click="detailsBoxFn"><i class="iconfont icon-iconfontyiwen"></i>查看明细</span>
      </div>
      <xk-star :defScore="HyQueryReview?HyQueryReview.score:5" :state="true"></xk-star>
      <div class="label">
        <xk-tag-item :name="item" :AcState="false" v-for="(item, index) in HyQueryReview?HyQueryReview.tabs:null"
                     :key="index"></xk-tag-item>
      </div>
      <div class="reviewContent">
        {{HyQueryReview && HyQueryReview.reviewContent && HyQueryReview.reviewContent !== '' ? HyQueryReview.reviewContent : '暂无评价'}}
      </div>
    </div>
    <Xk-detailsBox :title="'费用详情'" class="costBox" ref="detailsBox">
      <div slot="content">
        <ul>
          <li v-for="itme in payDataFn" v-if="itme.val !== 0" class="clear">
            <span class="name">{{itme.name}}</span>
            <span class="val"><span class="sum">{{itme.val}}</span><span class="unit">元</span></span>
            <span class="time" v-if="itme.time">{{itme.time}}<span class="unit">分钟</span></span>
          </li>
        </ul>
      </div>
    </Xk-detailsBox>
  </div>
</template>

<script>
  import XkWaiter from '../../module/waiter/src/waiter'
  import { mapGetters } from 'vuex'
  import Toucth from '../../../assets/js/method'
  import XkStar from '../../module/star/src/star'
  import XkTagItem from '../../module/tagitem/src/tagitem'

  export default {
    components: {
      XkTagItem,
      XkStar,
      XkWaiter
    },
    name: '',
    computed: {
      ...mapGetters({
        HyQueryReview: 'HyQueryReview',
        loginData: 'loginData',
        HyPayInfo: 'HyPayInfo',
        HyOrderInfo: 'HyOrderInfo'
      }),
      payDataFn: function () {
        let that = this
        let obj = []
        if (that.HyPayInfo) {
          if (that.HyPayInfo.payMoney) {
            obj.push({
              name: '服务费',
              val: (that.HyPayInfo.realCash - that.HyPayInfo.otherMoney - that.HyPayInfo.yuanchengMoney).toFixed(1)
            })
          }
          if (that.HyPayInfo.goodsCash) {
            obj.push({
              name: '商品价格',
              val: that.HyPayInfo.goodsCash.toFixed(1)
            })
          }
          if (that.HyPayInfo.guoluMoney) {
            obj.push({
              name: '过路费',
              val: that.HyPayInfo.guoluMoney.toFixed(1)
            })
          }
          if (that.HyPayInfo.otherMoney) {
            obj.push({
              name: '其他费用',
              val: that.HyPayInfo.otherMoney.toFixed(1)
            })
          }
          if (that.HyPayInfo.thanksCash) {
            obj.push({
              name: '感谢费',
              val: that.HyPayInfo.thanksCash.toFixed(1)
            })
          }
          if (that.HyPayInfo.yuanchengMoney) {
            obj.push({
              name: '远程费',
              val: that.HyPayInfo.yuanchengMoney.toFixed(1)
            })
          }
          if (that.HyPayInfo.couponMoney) {
            obj.push({
              name: '优惠券',
              red: true,
              val: (-that.HyPayInfo.couponMoney) + '.00'
            })
          }
        }
        return obj
      },
      carTypeFn: function () {
        let HyOrderInfo = this.HyOrderInfo
        if (this.HyOrderInfo) {
          if (HyOrderInfo.ifTaxi && HyOrderInfo.ifTaxi === '1') {
            return '出租车'
          }
          if (HyOrderInfo.carTypeName) {
            return HyOrderInfo.carTypeName
          }
        }
      }
    },
    data () {
      return {}
    },
    watch: {
      HyOrderInfo (val) {
        let that = this
        if (that.loginData.passenger && val && that.loginData.passenger.id !== val.passengerId) {
          that.$router.push('/huoyun')
          return
        }
      }
    },
    methods: {
      detailsBoxFn: function () { // 详情事件
        this.$refs.detailsBox.switchFn()
      }
    },
    mounted: function () {
      let that = this
      that.$store.dispatch('checkLogin', (state) => {
        if (state) {
          that.$store.dispatch('HyOrderInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
          that.$store.dispatch('HyQueryReview', {orderId: Toucth.AcquireUrlParameter().orderId})
          that.$store.dispatch('HyPayInfo', {orderId: Toucth.AcquireUrlParameter().orderId})
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/less" lang="less">
  .checkOrder {
    .money {
      padding-top: 3rem;
      text-align: center;
      h2 {
        font-size: 1.7rem;
        text-align: center;
        color: #f06553;
        .max {
          font-size: 3.2rem;
        }
      }
      .detail {
        margin-top: 0rem;
        line-height: 1.2rem;
        text-align: center;
        color: #878787;
      }
      .score {
        margin-top: 3rem;
        margin-bottom: 3rem;
      }
      .label {
        margin: 0 auto;
        margin-top: 1rem;
        width: 80%;
        text-align: center;
        .TagItem {
          box-sizing: border-box;
          width: calc(~'50% - 1rem');
          border-radius: 0;
          background: rgba(0, 0, 0, 0.04);
          font-size: 1rem;
          color: #999;
          border: none;
          line-height: 2.5rem;
          height: 2.5rem;
          margin: 0.3rem 0.5rem;
        }
      }
      .reviewContent {
        margin: 0 auto;
        box-sizing: border-box;
        width: 80%;
        margin-top: 1rem;
        line-height: 1.5rem;
        padding: 0.5rem;
        min-height: 5rem;
        border-radius: 0.333rem;
        text-align: center;
        color: #999;
      }
    }
    .costBox {
      ul {
        li {
          height: 2rem;
          line-height: 2rem;
          color: #999999;
          .name {
            float: left;
          }
          .time, .val {
            float: right;
          }
          .time {
            margin-right: 0.8rem;
            color: #333333;
          }
          .sum {
            color: #f06553;
          }
          .unit {
            margin-left: 0.3rem;
          }
        }
      }
    }
  }
</style>



